import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";

const Loading = ({ className }) => {
  return (
    <div className={className}>
      <div className="spinner" />
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string
};

const LoadingStyles = styled(Loading)`
  text-align: ${props => props.position};
  .spinner {
    display: inline-block;
    width: 64px;
    height: 64px;
  }
  .spinner:after {
    content: " ";
    display: block;
    width: 46px;
    height: 46px;
    margin: 1px;
    border-radius: 50%;
    border: 5px solid ${props => props.color};
    border-color: ${props => props.color} transparent ${props => props.color}
      transparent;
    animation: spinner 1.2s linear infinite;
  }
  @keyframes spinner {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(360deg);
    }
  }
`;

const defaults = {
  color: "#fff",
  position: "center"
};

const types = {
  color: PropTypes.string,
  position: PropTypes.oneOf(["center", "left", "right"])
};

LoadingStyles.propTypes = types;
LoadingStyles.defaultProps = defaults;

export default LoadingStyles;

//Only used for the Storybook Props Table
export const WithProps = props => <LoadingStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
