import React from "react";
import PropTypes from "prop-types";
//Styles
import styled from "styled-components";

const Section = styled.section`
  //highContrast only border
  border-top: ${props =>
    props.hasHighContrastBorderTop && props.theme.highContrast.border};

  min-height: ${props => props.height};
  padding-top: ${props =>
    props.paddingTop || props.theme.components.section.paddingTop};
  padding-bottom: ${props =>
    props.hasBottomSpacing
      ? props.paddingBottom || props.theme.components.section.paddingBottom
      : 0};

  background-color: ${props =>
    props.backgroundColor || props.theme.components.section.backgroundColor};
  position: ${props => props.position};
  width: ${props => props.width};
  bottom: ${props => props.bottom};
  border-top: ${props => props.borderTop};

  ${props =>
    props.verticalAlign ? "display: flex; align-items: center;" : null};

  // Large devices (desktops, less than 1200px)
  @media (max-width: 1199px) {
    border-top: ${props => props.lgBorderTop};
    padding-top: ${props => props.lgPaddingTop};
    padding-bottom: ${props => props.lgPaddingBottom};
  }

  // Medium devices (tablets, less than 992px)
  @media (max-width: 991px) {
    border-top: ${props => props.mdBorderTop};
    padding-bottom: ${props => props.mdPaddingBottom};
    padding-top: ${props => props.mdPaddingTop};
    margin-top: ${props => props.mdMarginTop};

    min-height: ${props => props.mdHeight};
  }
  // Small devices (landscape phones, less than 768px)
  @media (max-width: 767px) {
    padding-top: ${props => props.smPaddingTop};
    padding-bottom: ${props => props.smPaddingBottom};
  }
`;

const types = {
  children: PropTypes.node,
  hasHighContrastBorderTop: PropTypes.bool,
  height: PropTypes.string,
  paddingTop: PropTypes.string,
  hasBottomSpacing: PropTypes.bool,
  paddingBottom: PropTypes.string,
  backgroundColor: PropTypes.string,
  position: PropTypes.string,
  width: PropTypes.string,
  bottom: PropTypes.string,
  borderTop: PropTypes.string,
  verticalAlign: PropTypes.bool,
  lgBorderTop: PropTypes.string,
  lgPaddingTop: PropTypes.string,
  lgPaddingBottom: PropTypes.string,
  mdBorderTop: PropTypes.string,
  mdPaddingBottom: PropTypes.string,
  mdPaddingTop: PropTypes.string,
  mdMarginTop: PropTypes.string,
  mdHeight: PropTypes.string,
  smPaddingTop: PropTypes.string,
  smPaddingBottom: PropTypes.string
};

const defaults = {
  hasHighContrastBorderTop: true,
  hasBottomSpacing: true,
  verticalAlign: false
};

Section.propTypes = types;
Section.defaultProps = defaults;

export default Section;

//Only used for the Storybook Props Table
export const WithProps = ({ children, ...props }) => (
  <Section {...props}>{children}</Section>
);

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
