import React from "react";
import PropTypes from "prop-types";
import { lighten, darken } from "polished";
//Styles
import styled, { withTheme } from "styled-components";
import { Icon } from "../../atoms/index";

const CustomArrow = ({
  className,
  onClick,
  left,
  darken,
  ariaLabel,
  tabIndex,
  theme,
  arrowSize,
  ...props
}) => {
  return (
    <button
      className={className}
      onClick={onClick}
      aria-label={ariaLabel}
      tabIndex={tabIndex}
    >
      <Icon
        name={`${left ? theme.icons.chevron_left : theme.icons.chevron_right}`}
        darken={darken}
        color={props.color || theme.components.customArrow.customArrowColor}
        {...props}
        size={arrowSize || "xLarge"}
      />
    </button>
  );
};

CustomArrow.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  name: PropTypes.string,
  onClick: PropTypes.func,
  style: PropTypes.shape({}),
  darken: PropTypes.bool,
  left: PropTypes.bool,
  tabIndex: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  ariaLabel: PropTypes.string,
  theme: PropTypes.object,
  color: PropTypes.string,
  arrowSize: PropTypes.string
};

CustomArrow.defaultProps = {
  left: false,
  darken: false,
  tabIndex: "-1"
};

const CustomArrowWithStyles = styled(CustomArrow)`
  z-index: ${props => props.zIndex || "2"};
  border: none;
  background-color: transparent;
  cursor: pointer;
  &:focus {
    ${props => props.theme.mixins.focus()};
  }
  &.slick-disabled {
    opacity: 0.4;
    outline: none;
    i {
      cursor: default;
      &:hover {
        ${props =>
          props.darken
            ? `color: ${darken(
                0,
                props.color ||
                  props.theme.components.customArrow.customArrowColor
              )}`
            : `color: ${lighten(
                0,
                props.color ||
                  props.theme.components.customArrow.customArrowColor
              )}`};
      }
    }
  }
`;

export default withTheme(CustomArrowWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <CustomArrowWithStyles {...props} />;

WithProps.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  name: PropTypes.string,
  onClick: PropTypes.func,
  style: PropTypes.shape({}),
  darken: PropTypes.bool,
  left: PropTypes.bool,
  tabIndex: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  ariaLabel: PropTypes.string,
  theme: PropTypes.object,
  color: PropTypes.string,
  arrowSize: PropTypes.string,
  zIndex: PropTypes.string
};

WithProps.defaultProps = {
  left: false,
  darken: false,
  tabIndex: "-1",
  zIndex: "2",
  arrowSize: "xLarge"
};
