import React from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../../atoms";
import { withTheme } from "styled-components";

const Mask = props => {
  return (
    <ContentContainer
      backgroundColor={props.theme.loadings.journalDetails.maskBgColor}
      position="absolute"
      {...props}
    />
  );
};

const Loading = ({ className, ...props }) => {
  return (
    <div className={className}>
      <div className="row no-gutters">
        <div className="col-xl-7 offset-xl-3 col-12 offset-0">
          <AnimatedBackground height="361px">
            <Mask height="18px" right="0" width="85%" {...props} />
            <Mask height="12px" top="18px" width="100%" {...props} />
            <Mask height="30px" top="30px" right="0" width="10%" {...props} />
            <Mask height="10px" top="60px" width="100%" {...props} />
            <Mask height="30px" top="70px" right="0" width="80%" {...props} />
            <Mask height="28px" top="100px" width="100%" {...props} />
            <Mask height="18px" top="128px" right="0" width="85%" {...props} />
            {/*Icons*/}
            <ContentContainer
              position="absolute"
              zIndex="2"
              backgroundColor={
                props.theme.loadings.journalDetails.socialMediaIconsColor
              }
              height="18px"
              top="128px"
              right="2px"
              width="18px"
              borderRadius="50%"
            />
            <ContentContainer
              position="absolute"
              zIndex="2"
              backgroundColor={
                props.theme.loadings.journalDetails.socialMediaIconsColor
              }
              height="18px"
              top="128px"
              right="27px"
              width="18px"
              borderRadius="50%"
            />
            <ContentContainer
              position="absolute"
              zIndex="2"
              backgroundColor={
                props.theme.loadings.journalDetails.socialMediaIconsColor
              }
              height="18px"
              top="128px"
              right="52px"
              width="18px"
              borderRadius="50%"
            />
            <Mask height="45px" top="146px" width="100%" {...props} />
            <Mask height="20px" top="191px" right="0" width="5%" {...props} />
            <Mask height="5px" top="211px" width="100%" {...props} />
            <Mask height="20px" top="216px" right="0" width="10%" {...props} />
            <Mask height="5px" top="236px" width="100%" {...props} />
            <Mask height="20px" top="241px" right="0" width="15%" {...props} />
            <Mask height="5px" top="261px" width="100%" {...props} />
            <Mask height="20px" top="266px" right="0" width="10%" {...props} />
            <Mask height="5px" top="286px" width="100%" {...props} />
            <Mask height="20px" top="291px" right="0" width="20%" {...props} />
            <Mask height="5px" top="311px" width="100%" {...props} />
            <Mask height="20px" top="316px" right="0" width="50%" {...props} />
            <Mask height="5px" top="336px" width="100%" {...props} />
            <Mask height="20px" top="341px" right="0" width="80%" {...props} />
          </AnimatedBackground>
        </div>
      </div>
      <div className="row no-gutters">
        <div className="col-xl-7 offset-xl-3 col-12 offset-0">
          <ContentContainer
            backgroundColor={props.theme.loadings.iconContainerBgColor}
            width="100%"
            height="432px"
            marginTop="38px"
          />
        </div>
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string
};

export default withTheme(Loading);
