import React from "react";
import PropTypes from "prop-types";
//Styles
import styled from "styled-components";

const VerticalList = styled.ul`
  margin-top: ${props => props.marginTop};
  margin-bottom: ${props => props.marginBottom};
  li {
    line-height: ${props => props.lineHeight};
    list-style-type: ${props => props.listStyleType};

    padding-top: ${props => props.listPaddingTop};
    padding-bottom: ${props => props.listPaddingBottom};

    &:last-child {
      margin-bottom: ${props => props.marginBottom};
    }
  }
`;

const defaults = {
  marginTop: "0",
  marginBottom: "0",
  lineHeight: "normal",
  listStyleType: "none"
};

const types = {
  marginTop: PropTypes.string,
  marginBottom: PropTypes.string,
  lineHeight: PropTypes.string,
  listPaddingTop: PropTypes.string,
  listPaddingBottom: PropTypes.string,
  children: PropTypes.node
};

VerticalList.defaultProps = defaults;
VerticalList.propTypes = types;

export default VerticalList;

//Only used for the Storybook Props Table
export const WithProps = ({ children, ...props }) => (
  <VerticalList {...props}>{children}</VerticalList>
);

WithProps.defaultProps = defaults;
WithProps.propTypes = types;
