import React from "react";
import styled from "styled-components";
import PropTypes from "prop-types";
import { NewsCardLoading, PersonCardLoading } from "../../../molecules";

const Loading = ({ className }) => {
  return (
    <div className={className}>
      <div className="row">
        <div className="col-xl col-lg col-md-6 col-sm-12">
          <NewsCardLoading />
        </div>
        <div className="col-xl col-lg col-md-6 margin">
          <PersonCardLoading />
        </div>
      </div>
      <div className="row hide-sm">
        <div className="col-xl col-lg col-md-6 col-sm-12">
          <PersonCardLoading />
        </div>
        <div className="col-xl col-lg col-md-6">
          <NewsCardLoading />
        </div>
        <div className="col-xl col-lg col-md-6 hide-md">
          <NewsCardLoading />
        </div>
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string
};

const LoadingWithStyles = styled(Loading)`
  .row {
    :nth-child(2) {
      margin-top: 30px;
    }
  }

  // Large devices (desktops, 992px and up)
  @media (max-width: 992px) {
    .hide-md {
      display: none;
    }
  }

  // Medium devices (tablets, 768px and up)
  @media (max-width: 768px) {
    .hide-sm {
      display: none;
    }
    .loading-card,
    .loading-mask {
      background-color: ${props =>
        props.theme.loadings.journalListing.mediumDevices.loadingMaskBgColor};
    }
    .margin {
      margin-top: 20px;
    }
  }

  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    .margin {
      margin-top: 15px;
    }
  }
`;

export default LoadingWithStyles;
