import React from "react";
import PropTypes from "prop-types";
import { ContentContainer } from "../../atoms";
import { AgendaCardLoading, AgendaFeaturedLoading } from "../../molecules";
import { withTheme } from "styled-components";

const Loading = ({ className, ...props }) => {
  return (
    <ContentContainer marginTop="65px" className={className}>
      <div className="row">
        <div className="col-xl-6 col-lg-6 col-md-12 col-12">
          <AgendaCardLoading {...props} />
          <AgendaCardLoading {...props} />
          <AgendaCardLoading {...props} />
          <AgendaCardLoading
            {...props}
            borderBottom={`1px solid ${props.theme.loadings.cardBorderColor}`}
          />
        </div>
        <div className="col-xl-6 col-lg-6 col-md-12 col-12">
          <ContentContainer mdMargintop="50px">
            <AgendaFeaturedLoading {...props} />
          </ContentContainer>
        </div>
      </div>
    </ContentContainer>
  );
};

Loading.propTypes = {
  className: PropTypes.string
};

export default withTheme(Loading);
