import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { withTheme } from "styled-components";
//components
import { Text, Section, ContentContainer } from "../../atoms";
import { IconCard } from "../../molecules";

const Services = ({
  className,
  title,
  theme,
  data,
  setFavorite,
  removeFavorite,
  demoFavorites
}) => {
  return (
    <Section className={className}>
      <div className="container">
        <div className="row no-gutters">
          <div className="col-xl-7 col-12 offset-xl-3">
            <Text size="xLarge" fontWeight="500">
              {title}
            </Text>

            {data.map((service, index) => {
              return (
                <Fragment key={index}>
                  <Text
                    size="large"
                    color={theme.components.services.groupTitleColor}
                    fontWeight="400"
                    marginTop="50px"
                    marginBottom="20px"
                    mobileXsMarginBottom="10px"
                  >
                    {service.title}
                  </Text>
                  <ContentContainer className="d-flex flex-wrap">
                    {service.list.map((item, i) => {
                      return (
                        <IconCard
                          key={i}
                          margin="10px 60px 10px 0"
                          mobileXsMargin="10px 20px 10px 0"
                          linkText={item.text}
                          iconName={item.icon}
                          linkProps={{
                            href: item.href
                          }}
                          favorite={{
                            show: demoFavorites,
                            check: item.favorite,
                            set: () => setFavorite(item.id),
                            remove: () => removeFavorite(item.id)
                          }}
                        />
                      );
                    })}
                  </ContentContainer>
                </Fragment>
              );
            })}
          </div>
        </div>
      </div>
    </Section>
  );
};

const defaults = {
  title: "Título",
  setFavorite: () => null,
  removeFavorite: () => null,
  demoFavorites: false
};

const types = {
  className: PropTypes.string,
  title: PropTypes.string,
  theme: PropTypes.object,
  data: PropTypes.array.isRequired,
  setFavorite: PropTypes.func,
  removeFavorite: PropTypes.func,
  demoFavorites: PropTypes.bool
};

Services.propTypes = types;
Services.defaultProps = defaults;

export default withTheme(Services);

//Only used for the Storybook Props Table
export const WithProps = props => <Services {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
