import React, { Fragment } from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//components
import { HorizontalNewsCard, Error, NewsCard } from "./../../../molecules";
import moment from "moment";
import Loading from "./Loading";
import { withSize } from "react-sizeme";
import { compose } from "../../../../utils/utilities";

const Highlights = ({
  className,
  data,
  loading,
  hasError,
  size,
  theme,
  ...props
}) => {
  return (
    <section className={className}>
      <div className="container">
        {loading ? (
          <Loading />
        ) : hasError ? (
          <Error />
        ) : (
          <Fragment>
            {data.list
              .filter((_, index) => index === 0)
              .map(item => {
                return (
                  <div key={item.id}>
                    {size.width <= 575 ? (
                      <NewsCard
                        className="highlight-card"
                        data={{
                          imageCover: { ...item.image },
                          alt: item.image.alt !== null ? item.image.alt : "",
                          ...item
                        }}
                        cardBgColor={
                          theme.components.journalHighlights
                            .highlightCardBgColor
                        }
                        plusButtonBgColor={
                          theme.components.journalHighlights
                            .highlightCardBgColor
                        }
                        imageBgColor="#fff"
                        hasShareIcon={true}
                        size={size}
                        share={item.share}
                        history={props.history}
                        changeImageVerticalOrder={true}
                        centerPlusButton={true}
                        hasColAuto={false}
                        {...props}
                      />
                    ) : (
                      <HorizontalNewsCard
                        className="change-order-card"
                        src={
                          item.image !== null && item.image.url !== null
                            ? item.image.url
                            : ""
                        }
                        alt={
                          item.image !== null && item.image.alt !== null
                            ? item.image.alt
                            : ""
                        }
                        href={item.url}
                        category={item.category}
                        title={item.title}
                        date={moment(item.startDate).format("DD MMMM YYYY")}
                        categoryUrl={item.categoryUrl}
                        share={item.share}
                        contentContainerBgColor={
                          theme.components.journalHighlights
                            .highlightCardBgColor
                        }
                        triangleBgColor={
                          theme.components.journalHighlights
                            .highlightCardBgColor
                        }
                        size={size}
                        changeMdOrder={true}
                        {...props}
                      />
                    )}
                  </div>
                );
              })}

            {/*Cards*/}
            <div className="row no-gutters">
              {data.list
                .filter((_, index) => index >= 1 && index <= 3)
                .map((item, index) => {
                  return (
                    <div key={item.id} className="col-xl col-lg col-md-12">
                      <HorizontalNewsCard
                        className={index === 1 && "change-order-card"}
                        src={
                          item.image !== null && item.image.url !== null
                            ? item.image.url
                            : ""
                        }
                        href={item.url}
                        alt={item.image.alt}
                        category={item.category}
                        title={item.title}
                        date={moment(item.startDate).format("DD MMMM YYYY")}
                        changeMdOrder={index === 1}
                        categoryUrl={item.categoryUrl}
                        share={item.share}
                        size={size}
                        {...props}
                      />
                    </div>
                  );
                })}
            </div>
          </Fragment>
        )}
      </div>
    </section>
  );
};

const types = {
  className: PropTypes.string,
  data: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),
  loading: PropTypes.bool,
  hasError: PropTypes.bool,
  hasHash: PropTypes.string,
  route: PropTypes.string,
  lang: PropTypes.string,
  categoryId: PropTypes.number,
  history: PropTypes.object.isRequired,
  size: PropTypes.object,
  theme: PropTypes.object
};

Highlights.propTypes = types;

const HighlightsWithStyles = styled(Highlights)`
  background-color: ${props =>
    props.theme.components.journalHighlights.backgroundColor};

  // Medium devices (tablets, less than 992px)
  @media (max-width: 991px) {
    //Change triangle position on card with different order
    .change-order-card {
      .triangle {
        left: -33px;
        right: unset;
        transform: rotate(-90deg);
      }
    }
  }

  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    .highlight-card {
      .image-container {
        .news-plus-button {
          top: unset;
          bottom: 170px;
        }
      }
    }
  }
`;

export default compose(withSize(), withTheme)(HighlightsWithStyles);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => <HighlightsWithStyles {...props} />;

WithProps.propTypes = types;
