import React from "react";
import styled, { withTheme } from "styled-components";
import PropTypes from "prop-types";
import { ShowMoreCardLoading } from "../../../molecules";

const Loading = ({ className, CardBgColor, theme }) => {
  return (
    <div className={className}>
      <div className="container">
        <div className="row justify-content-between">
          <div className="col-md-3 col-4">
            <ShowMoreCardLoading
              CardBgColor={
                CardBgColor || theme.components.subWebCourses.backgroundColor
              }
            />
          </div>
          <div className="col-md-3 col-4">
            <ShowMoreCardLoading
              CardBgColor={
                CardBgColor || theme.components.subWebCourses.backgroundColor
              }
            />
          </div>
          <div className="col-md-3 col-4">
            <ShowMoreCardLoading
              CardBgColor={
                CardBgColor || theme.components.subWebCourses.backgroundColor
              }
            />
          </div>
        </div>
        <div className="row justify-content-between">
          <div className="col-md-3 col-4">
            <ShowMoreCardLoading
              CardBgColor={
                CardBgColor || theme.components.subWebCourses.backgroundColor
              }
            />
          </div>
          <div className="col-md-3 col-4">
            <ShowMoreCardLoading
              CardBgColor={
                CardBgColor || theme.components.subWebCourses.backgroundColor
              }
            />
          </div>
          <div className="col-md-3 col-4">
            <ShowMoreCardLoading
              CardBgColor={
                CardBgColor || theme.components.subWebCourses.backgroundColor
              }
            />
          </div>
        </div>
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  theme: PropTypes.object,
  CardBgColor: PropTypes.string
};

const LoadingWithStyles = styled(Loading)`
  .row:first-child {
    margin-bottom: 60px;
  }
`;

export default withTheme(LoadingWithStyles);
