import React from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../atoms";
import { withTheme } from "styled-components";

const Mask = ({ ...props }) => {
  return (
    <ContentContainer
      backgroundColor={props.theme.loadings.bgColor}
      {...props}
    />
  );
};

const Loading = ({ CardBgColor, offsetClasses, ...props }) => {
  return (
    <ContentContainer
      padding="18px 0"
      borderBottom={`1px solid ${props.theme.loadings.lightBorderColor}`}
    >
      <div className="container">
        <ContentContainer
          width="300px"
          mobileXsWidth="90%"
          className={`${offsetClasses}`}
        >
          <AnimatedBackground height="15px">
            <div className="row no-gutters">
              <div className="col d-flex justify-content-end">
                <Mask height="15px" width="50px" {...props} />
              </div>
              <div className="col d-flex justify-content-end">
                <Mask height="15px" width="50px" {...props} />
              </div>
              <div className="col d-flex justify-content-end">
                <Mask height="15px" width="50px" {...props} />
              </div>
            </div>
          </AnimatedBackground>
        </ContentContainer>
      </div>
    </ContentContainer>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  CardBgColor: PropTypes.string
};

Loading.defaultProps = {
  offsetClasses: ""
};

export default withTheme(Loading);
