import React from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
import { lighten } from "polished";
//Components
import { Picture, Text, Triangle, Link } from "../../../atoms/index";

const BackgroundImageNewsCard = ({
  className,
  src,
  href,
  title,
  theme,
  alt
}) => {
  return (
    <Link className={className} href={href} hasHover={false}>
      <Picture src={src} alt={alt} height="100%" />
      <div className="content-container">
        <Triangle
          className="triangle"
          withContent={true}
          contentColor={theme.components.backgroundImageNewsCard.textColor}
          color={
            theme.components.backgroundImageNewsCard
              .contentContainerBackgroundColor
          }
          size="22px"
          top="-22px"
          left="20px"
          contentSize="24px"
        />
        <Text
          as="h3"
          size="medium"
          fontDecoration="none"
          fontWeight="400"
          lineHeight="1.5"
          text={title}
        />
      </div>
    </Link>
  );
};

const types = {
  className: PropTypes.string,
  src: PropTypes.string,
  href: PropTypes.string,
  title: PropTypes.string,
  theme: PropTypes.object,
  alt: PropTypes.string
};

const defaults = {
  href: "/",
  title: "Título",
  alt: "Imagem sem alt"
};

BackgroundImageNewsCard.propTypes = types;
BackgroundImageNewsCard.defaultProps = defaults;

const BackgroundImageNewsCardWithStyles = styled(BackgroundImageNewsCard)`
  height: 360px;
  text-decoration: none;
  display: block;
  position: relative;
  overflow: hidden;
  button {
    background-color: transparent;
    cursor: pointer;
    border: none;
    &:hover {
      i {
        color: ${props =>
          lighten(
            0.5,
            props.theme.components.backgroundImageNewsCard.textColor
          )};
      }
    }
  }
  &:focus {
    ${props => props.theme.mixins.focus};
  }
  .content-container {
    position: absolute;
    bottom: 20px;
    left: 20px;
    right: 20px;
    height: auto;
    background-color: ${props =>
      props.theme.components.backgroundImageNewsCard
        .contentContainerBackgroundColor};
    padding: 20px;
  }

  //Image Animation
  img {
    -webkit-transition: all 0.4s;
    -moz-transition: all 0.4s;
    -o-transition: all 0.4s;
    transition: all 0.4s;
  }

  &:hover {
    img {
      -ms-transform: scale(1.1);
      -moz-transform: scale(1.1);
      -webkit-transform: scale(1.1);
      -o-transform: scale(1.1);
      transform: scale(1.1);
    }
  }
`;

export default withTheme(BackgroundImageNewsCardWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => (
  <BackgroundImageNewsCardWithStyles {...props} />
);

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
