import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";

const Empty = ({ className, text }) => {
  return <div className={className}>{text}</div>;
};

Empty.propTypes = {
  text: PropTypes.string,
  className: PropTypes.string
};

Empty.defaultProps = {
  text: "Sem conteudo"
};

const EmptyStyles = styled(Empty)`
  padding: ${props => props.padding};
`;

const defaults = {
  padding: "0"
};

const types = {
  padding: PropTypes.string
};

EmptyStyles.propTypes = types;
EmptyStyles.defaultProps = defaults;

export default EmptyStyles;

//Only used for the Storybook Props Table
export const WithProps = props => <EmptyStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
