import React from "react";
//Helpers
import styled from "styled-components";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../atoms";

const Mask = props => {
  return (
    <ContentContainer backgroundColor="#fff" position="absolute" {...props} />
  );
};

const LoadingCard = () => {
  return (
    <ContentContainer
      height="155px"
      width="100%"
      mobileXlWidth="80%"
      mobileMdWidth="90%"
      mobileXsWidth="100%"
    >
      <AnimatedBackground height="155px">
        <Mask height="20px" right="0" width="70%" />
        <Mask height="15px" top="20px" width="100%" />
        <Mask height="5px" top="35px" right="0" width="90%" />
        <Mask height="25px" top="40px" width="100%" />
        <Mask height="15px" top="65px" right="0" width="20%" />
        <Mask height="10px" top="80px" width="100%" />
        <Mask height="15px" top="90px" right="0" width="20%" />
        <Mask height="10px" top="105px" width="100%" />
        <Mask height="15px" top="115px" right="0" width="20%" />
        <Mask height="10px" top="130px" width="100%" />
        <Mask height="15px" top="140px" right="0" width="60%" />
      </AnimatedBackground>
    </ContentContainer>
  );
};

const Loading = ({ className }) => {
  return (
    <div className={className}>
      <div className="row no-gutters">
        <div className="col-xl-6 offset-xl-3 col-lg-8 offset-lg-4 col-12 offset-0">
          <LoadingCard />
        </div>
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string
};

const LoadingWithStyles = styled(Loading)`
  min-height: 400px;
`;

export default LoadingWithStyles;
