import React, { Fragment } from "react";
import PropTypes from "prop-types";
//Components
import Loading from "./Loading";
import {
  Text,
  Icon,
  ListItem,
  HorizontalList,
  Section,
  ContentContainer
} from "../../atoms";
import { Select, Table, Error, BorderedCard } from "../../molecules";
import { withSize } from "react-sizeme";
import { withTheme } from "styled-components";
import { compose } from "../../../utils/utilities";
//Context
import { useConfig } from "../../../context/ConfigContext";

const Edicts = ({
  title,
  className,
  lang,
  hasHash,
  isLoading,
  hasError,
  tables,
  filters,
  onChangeFaseFilter,
  onChangeDegreeFilter,
  faseFilterValue,
  degreeFilterValue,
  size,
  theme
}) => {
  const {
    link: Link,
    formattedMessage: FormattedMessage,
    useIntl
  } = useConfig();
  const intl = useIntl();

  return (
    <Section
      className={className}
      mdPaddingTop="122px"
      backgroundColor={theme.components.edicts.backgroundColor}
      hasHighContrastBorderTop={false}
    >
      <div className="container">
        <div className="row no-gutters align-items-center">
          <div className="col-xl-8 offset-xl-3 col-lg-8 offset-lg-4 col-12 offset-0">
            {isLoading ? (
              <Loading />
            ) : hasError ? (
              <Error />
            ) : (
              <Fragment>
                <Text
                  as="h2"
                  fontWeight="500"
                  size="xLarge"
                  marginBottom="10px"
                  firstLetter="capitalize"
                >
                  <FormattedMessage
                    id="app.course.edicts"
                    defaultMessage={title}
                  />
                </Text>
                <ContentContainer marginTop="40px" marginBottom="20px">
                  <div className="row">
                    {filters
                      .filter(res => res.jsonField === "degree")
                      .map((item, i) => {
                        return (
                          <div key={i} className="col-sm col-12">
                            <ContentContainer mobileXsMarginBottom="20px">
                              <Select
                                id={item.jsonField}
                                variant="black"
                                options={item.options}
                                placeholder={item.title}
                                label={item.title}
                                onChange={option =>
                                  onChangeDegreeFilter(option, item.jsonField)
                                }
                                isClearable
                              />
                            </ContentContainer>
                          </div>
                        );
                      })}
                    {filters
                      .filter(res => res.jsonField === "fase")
                      .map((item, i) => {
                        return (
                          <div key={i} className="col-sm col-12">
                            <Select
                              id={item.jsonField}
                              variant="black"
                              options={item.options}
                              placeholder={item.title}
                              label={item.title}
                              onChange={option =>
                                onChangeFaseFilter(option, item.jsonField)
                              }
                              isClearable
                            />
                          </div>
                        );
                      })}
                  </div>
                </ContentContainer>

                {size.width <= 575 ? (
                  tables
                    .filter((_, index) => index !== 0)
                    .map((res, i) => {
                      return (
                        <BorderedCard key={i} cardIsLink={false}>
                          <Text size="small" marginBottom="10px">
                            {res.columns[0].text}
                          </Text>
                          <Text size="small" marginBottom="10px">
                            {res.columns[1].text}
                          </Text>
                          <ContentContainer>
                            <HorizontalList display="inline-block">
                              <ListItem
                                paddingRight="5px"
                                borderRight="1px solid #000"
                                lineHeight="1"
                              >
                                <Link
                                  as={res.columns[3].href ? "a" : "span"}
                                  fontWeight="300"
                                  display="inline-block"
                                  textDecoration="underline"
                                  target={res.columns[3].href ? "_blank" : null}
                                  href={
                                    res.columns[3].href
                                      ? res.columns[3].href
                                      : null
                                  }
                                  color={
                                    res.columns[3].href
                                      ? theme.components.edicts.iconColor
                                      : theme.components.edicts.emptyIconColor
                                  }
                                  cursor={
                                    res.columns[3].href ? "pointer" : "default"
                                  }
                                  hasHover={res.columns[3].href && true}
                                  aria-label={
                                    res.columns[3].href
                                      ? res.columns[3].href
                                      : intl({
                                          id:
                                            "app.course.edicts.icon.empty.documents",
                                          defaultMessage:
                                            "Ausência de documentos"
                                        })
                                  }
                                  native
                                >
                                  ver ata
                                </Link>
                              </ListItem>
                              <ListItem paddingLeft="5px" lineHeight="1">
                                <Link
                                  as={res.columns[2].href ? "a" : "span"}
                                  fontWeight="300"
                                  display="inline-block"
                                  textDecoration="underline"
                                  target={res.columns[2].href ? "_blank" : null}
                                  href={
                                    res.columns[2].href
                                      ? res.columns[2].href
                                      : null
                                  }
                                  color={
                                    res.columns[2].href
                                      ? theme.components.edicts.iconColor
                                      : theme.components.edicts.emptyIconColor
                                  }
                                  cursor={
                                    res.columns[2].href ? "pointer" : "default"
                                  }
                                  hasHover={res.columns[2].href && true}
                                  aria-label={
                                    res.columns[2].href
                                      ? intl({
                                          id:
                                            "app.course.edicts.icon.documents",
                                          defaultMessage:
                                            "Aceder aos documentos"
                                        })
                                      : intl({
                                          id:
                                            "app.course.edicts.icon.empty.documents",
                                          defaultMessage:
                                            "Ausência de documentos"
                                        })
                                  }
                                  native
                                >
                                  ver edital
                                </Link>
                              </ListItem>
                            </HorizontalList>
                          </ContentContainer>
                        </BorderedCard>
                      );
                    })
                ) : (
                  <Table
                    marginTop="0"
                    col2Size="230px"
                    col3Size="65px"
                    col4Size="30px"
                    col2SizeMobile="145px"
                    col3SizeMobile="65px"
                    col4SizeMobile="30px"
                    tableRowSpace="10px"
                    borders
                  >
                    {Object.keys(tables).length <= 1 ? (
                      <Error>
                        Não existe informação para esta fase{" "}
                        <b>
                          {faseFilterValue.label || degreeFilterValue.label}
                        </b>
                      </Error>
                    ) : (
                      <Fragment>
                        {tables
                          .filter(item => item.isHeader)
                          .map((item, index) => {
                            return (
                              <thead key={`table-${item.fase}-${index}`}>
                                <tr>
                                  {item.columns.map((cols, index) => {
                                    return (
                                      <th key={index}>
                                        <Text fontSize="14px" fontWeight="400">
                                          {cols.text}
                                        </Text>
                                      </th>
                                    );
                                  })}
                                </tr>
                              </thead>
                            );
                          })}

                        <tbody>
                          {tables
                            .filter(item => !item.isHeader)
                            .map((item, index) => {
                              return (
                                <tr key={index}>
                                  {item.columns.map(cols => {
                                    return cols.type === "link" ? (
                                      <td>
                                        <Link
                                          color={
                                            theme.components.edicts.linkColor
                                          }
                                          fontSize="14px"
                                          fontWeight="300"
                                          href={`${hasHash}/${lang}/curso/${cols.id}`}
                                          aria-label={cols.text}
                                        >
                                          {cols.text}
                                        </Link>
                                      </td>
                                    ) : cols.type === "icon" ? (
                                      <td>
                                        <Link
                                          as={cols.href ? "a" : "span"}
                                          href={cols.href ? cols.href : null}
                                          aria-label={
                                            cols.href
                                              ? intl({
                                                  id:
                                                    "app.course.edicts.icon.documents",
                                                  defaultMessage:
                                                    "Aceder aos documentos"
                                                })
                                              : intl({
                                                  id:
                                                    "app.course.edicts.icon.empty.documents",
                                                  defaultMessage:
                                                    "Ausência de documentos"
                                                })
                                          }
                                          target={cols.href && "_blank"}
                                          hasHover={cols.href}
                                          cursor={
                                            cols.href ? "pointer" : "default"
                                          }
                                          native
                                        >
                                          <Icon
                                            name="fal fa-file"
                                            color={
                                              cols.href === ""
                                                ? theme.components.edicts
                                                    .emptyIconColor
                                                : theme.components.edicts
                                                    .iconColor
                                            }
                                            fontSize="14px"
                                          />
                                        </Link>
                                      </td>
                                    ) : (
                                      <td>
                                        <Text fontSize="14px" fontWeight="300">
                                          {cols.text}
                                        </Text>
                                      </td>
                                    );
                                  })}
                                </tr>
                              );
                            })}
                        </tbody>
                      </Fragment>
                    )}
                  </Table>
                )}
              </Fragment>
            )}
          </div>
        </div>
      </div>
    </Section>
  );
};

const defaults = {
  title: "Title",
  lang: "pt",
  hasHash: "",
  tables: [],
  filters: [],
  onChangeFaseFilter: () => null,
  onChangeDegreeFilter: () => null
};

const types = {
  className: PropTypes.string,
  title: PropTypes.string,
  lang: PropTypes.string,
  hasHash: PropTypes.string,
  isLoading: PropTypes.bool,
  hasError: PropTypes.bool,
  tables: PropTypes.array.isRequired,
  filters: PropTypes.array.isRequired,
  onChangeFaseFilter: PropTypes.func.isRequired,
  onChangeDegreeFilter: PropTypes.func.isRequired,
  faseFilterValue: PropTypes.string.isRequired,
  degreeFilterValue: PropTypes.string.isRequired,
  size: PropTypes.object,
  theme: PropTypes.object
};

Edicts.propTypes = types;
Edicts.defaultProps = defaults;

export default compose(withSize(), withTheme)(Edicts);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => <Edicts {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
