import React from "react";
import { AnimatedBackground, ContentContainer } from "../../../atoms";
import styled, { withTheme } from "styled-components";

const Mask = props => {
  return (
    <ContentContainer
      backgroundColor={props.theme.loadings.bgColor}
      position="absolute"
      {...props}
    />
  );
};

const Loading = ({ className, ...props }) => {
  return (
    <ContentContainer
      height="109px"
      backgroundColor={props.theme.loadings.bgColor}
      padding="11px 0"
      borderTop={`1px solid ${props.theme.loadings.cardBorderColor}`}
      className={className}
    >
      <AnimatedBackground height="87px">
        <Mask height="16px" right="0" width="80%" {...props} />
        <Mask height="3px" top="16px" width="100%" {...props} />
        <Mask height="18px" top="19px" right="0" width="20%" {...props} />
        <Mask height="3px" top="37px" width="100%" {...props} />
        <Mask height="18px" top="40px" right="0" width="50%" {...props} />
        <Mask height="11px" top="58px" width="100%" {...props} />
        <Mask height="18px" top="69px" right="0" width="70%" {...props} />
      </AnimatedBackground>
    </ContentContainer>
  );
};

const LoadingWithStyles = styled(Loading)`
  :last-child {
    border-bottom: 1px solid ${props => props.theme.loadings.cardBorderColor};
  }
`;

export default withTheme(LoadingWithStyles);
