import React from "react";
//Styles
import styled from "styled-components";
//Components
import {
  AnimatedBackground,
  AspectRatio,
  ContentContainer
} from "../../atoms/index";

const LoadingContainer = styled.div`
  .row {
    margin-right: -10px;
    margin-left: -10px;
  }
  [class^="col-"] {
    margin-bottom: 20px;
    padding-right: 10px;
    padding-left: 10px;
  }
`;

const Card = () => {
  return (
    <div className="col-md-3 col-6">
      <AspectRatio ratio="treeByTwo" />
      <ContentContainer marginTop="20px" width="80%">
        <AnimatedBackground height="20px" />
      </ContentContainer>
    </div>
  );
};

const Loading = ({ className }) => {
  return (
    <LoadingContainer className={className}>
      <div className="container">
        <div className="row">
          <Card />
          <Card />
          <Card />
          <Card />
          <Card />
          <Card />
        </div>
      </div>
    </LoadingContainer>
  );
};

export default Loading;
