import React from "react";
import styled, { withTheme } from "styled-components";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../atoms";

const Mask = props => {
  return (
    <ContentContainer
      backgroundColor={props.theme.highContrast.backgroundColor || "#F3F3F4"}
      position="absolute"
      {...props}
    />
  );
};

const LoadingCard = ({ ...theme }) => {
  return (
    <ContentContainer height="55px">
      <AnimatedBackground height="55px">
        <Mask height="20px" right="0" width="50%" {...theme} />
        <Mask height="5px" top="20px" width="100%" {...theme} />
        <Mask height="15px" right="0" top="25px" width="30%" {...theme} />
        <Mask height="15px" top="40px" width="100%" {...theme} />
      </AnimatedBackground>
    </ContentContainer>
  );
};

const Loading = ({ className, ...theme }) => {
  return (
    <div className={className}>
      <div className="container">
        <div className="row">
          <div className="col-xl col-lg col-6">
            <LoadingCard {...theme} />
          </div>
          <div className="col-xl col-lg col-6">
            <LoadingCard {...theme} />
          </div>
          <div className="col-xl col-lg col-6">
            <ContentContainer mdPadding="70px 0 0 0">
              <LoadingCard {...theme} />
            </ContentContainer>
          </div>
          <div className="col-xl col-lg col-6">
            <ContentContainer mdPadding="70px 0 0 0">
              <LoadingCard {...theme} />
            </ContentContainer>
          </div>
        </div>
        <ContentContainer marginTop="70px">
          <div className="row">
            <div className="col-xl col-lg col-6">
              <LoadingCard {...theme} />
            </div>
            <div className="col-xl col-lg col-6">
              <LoadingCard {...theme} />
            </div>
            <div className="col-xl col-lg col-6">
              <ContentContainer mdPadding="70px 0 0 0">
                <LoadingCard {...theme} />
              </ContentContainer>
            </div>
            <div className="col-xl col-lg col-6">
              <ContentContainer mdPadding="70px 0 0 0">
                <LoadingCard {...theme} />
              </ContentContainer>
            </div>
          </div>
        </ContentContainer>
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string
};

const LoadingWithStyles = styled(Loading)`
  padding-top: 70px;
`;

export default withTheme(LoadingWithStyles);
