import React from "react";
import styled from "styled-components";
import PropTypes from "prop-types";
import { NewsCardLoading } from "../../../molecules";

const Loading = ({ className }) => {
  return (
    <div className={className}>
      <div className="row">
        <div className="col-xl col-lg col-md-6 col-sm-12">
          <NewsCardLoading />
        </div>
        <div className="col-xl col-lg col-md-6 col-sm-12 margin-sm">
          <NewsCardLoading />
        </div>
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string
};

const LoadingWithStyles = styled(Loading)`
  // Medium devices (tablets, 768px and up)
  @media (max-width: 768px) {
    .margin-sm {
      margin-top: 15px;
    }
  }
`;

export default LoadingWithStyles;
