import React from "react";
import PropTypes from "prop-types";
import { compose } from "../../../../utils/utilities";
import styled, { withTheme } from "styled-components";
//Components
import { Text, Picture, LineTruncate, Triangle, Link } from "../../../atoms";

const MiniHorizontalNewsCard = ({
  className,
  title,
  date,
  src,
  alt,
  backgroundColor,
  theme,
  href,
  linkProps
}) => {
  return (
    <div className={className}>
      <Link
        tabIndex={0}
        href={href}
        className="horizontal-news-card"
        {...linkProps}
      >
        <div className="row no-gutters">
          <div className="col">
            <div className="content-container d-flex flex-column justify-content-between">
              {src !== null && (
                <Triangle
                  zIndex="2"
                  transform="rotate(90deg)"
                  withContent={true}
                  contentColor={
                    theme.components.miniHorizontalNewsCard.triangleContentColor
                  }
                  color={
                    backgroundColor ||
                    theme.components.miniHorizontalNewsCard.triangleBgColor
                  }
                  size="16px"
                  top="calc(50% - 8px)"
                  right="-24px"
                  contentSize="18px"
                />
              )}

              <div>
                <div className="title-container">
                  <Text
                    as="h3"
                    display="block"
                    size="small"
                    fontDecoration="none"
                    fontWeight="400"
                    text={title}
                  />
                </div>
              </div>
              <div className="row">
                <div className="col">
                  <LineTruncate>
                    <Text
                      text={date}
                      size="xSmall"
                      color={theme.components.miniHorizontalNewsCard.dateColor}
                    />
                  </LineTruncate>
                </div>
              </div>
            </div>
          </div>
          {src !== null && (
            <div className="col-auto">
              <div className="image-container">
                <Picture
                  src={src}
                  alt={alt}
                  height={theme.components.miniHorizontalNewsCard.height}
                />
              </div>
            </div>
          )}
        </div>
      </Link>
    </div>
  );
};

const types = {
  className: PropTypes.string,
  category: PropTypes.string,
  title: PropTypes.string,
  date: PropTypes.string,
  src: PropTypes.string,
  backgroundColor: PropTypes.string,
  theme: PropTypes.func,
  onClick: PropTypes.func,
  onKeyPress: PropTypes.func,
  href: PropTypes.string,
  alt: PropTypes.string,
  history: PropTypes.object.isRequired,
  linkProps: PropTypes.object
};

const defaults = {
  category: "Categoria",
  title: "Título",
  date: "data",
  src: "",
  href: "/"
};

MiniHorizontalNewsCard.propTypes = types;
MiniHorizontalNewsCard.defaultProps = defaults;

const MiniHorizontalNewsCardWithStyles = styled(MiniHorizontalNewsCard)`
  border-bottom: 1px solid
    ${props => props.theme.components.miniHorizontalNewsCard.borderColor};
  padding-bottom: 10px;
  padding-top: 10px;

  &:first-child {
    border-top: 1px solid
      ${props => props.theme.components.miniHorizontalNewsCard.borderColor};
  }

  .horizontal-news-card {
    display: block;
    background-color: ${props =>
      props.backgroundColor ||
      props.theme.components.miniHorizontalNewsCard.backgroundColor};
    height: ${props => props.theme.components.miniHorizontalNewsCard.height};
    .image-container {
      height: ${props => props.theme.components.miniHorizontalNewsCard.height};
      width: 115px;
      overflow: hidden;
    }
    .content-container {
      position: relative;
      height: ${props => props.theme.components.miniHorizontalNewsCard.height};
      padding-right: 20px;
      .title-container {
        margin-bottom: 10px;
        max-height: 42px;
        overflow: hidden;
      }
    }
    //Image Animation
    img {
      -webkit-transition: all 0.4s;
      -moz-transition: all 0.4s;
      -o-transition: all 0.4s;
      transition: all 0.4s;
    }

    cursor: pointer;
    &:hover {
      text-decoration: none;
      img {
        -ms-transform: scale(1.1);
        -moz-transform: scale(1.1);
        -webkit-transform: scale(1.1);
        -o-transform: scale(1.1);
        transform: scale(1.1);
      }
    }
    &:focus {
      ${props => props.theme.mixins.focus};
    }
  }
`;

export default compose(withTheme)(MiniHorizontalNewsCardWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => (
  <MiniHorizontalNewsCardWithStyles {...props} />
);

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
