import React from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//Components
import { Section } from "../../../atoms";
import {
  SectionHeader,
  BackgroundImageNewsCard,
  Error
} from "../../../molecules";
import moment from "moment";
import Loading from "./Loading";
import { useConfig } from "../../../../context/ConfigContext";

const Dossiers = ({
  className,
  backgroundColor,
  titleProps,
  sectionTitle,
  data,
  loading,
  hasError,
  theme
}) => {
  const { formattedMessage: FormattedMessage } = useConfig();
  return (
    <Section
      className={className}
      backgroundColor={
        theme.highContrast.backgroundColor ||
        backgroundColor ||
        theme.components.journalDossiers.backgroundColor
      }
      paddingTop="0"
      mdPaddingTop="20x"
      hasHighContrastBorderTop={false}
    >
      <SectionHeader
        textColor={theme.components.journalDossiers.titleColor}
        {...titleProps}
        title={sectionTitle}
        hasLink={false}
        hasLine={false}
      />
      <div className="container">
        {loading ? (
          <Loading />
        ) : hasError ? (
          <Error />
        ) : data.list.length <= 0 ? (
          <div>
            <FormattedMessage
              id="app.content.empty"
              defaultMessage="No content to display"
            />
          </div>
        ) : (
          data.list.chunk(2).map((res, index) => {
            return (
              <div key={index} className="row">
                {res.map(item => {
                  return (
                    <div key={item.id} className="col-md-6 col-12">
                      <BackgroundImageNewsCard
                        className="background-image-card"
                        title={item.title}
                        date={moment(data.startDate).format("DD MMMM YYYY")}
                        href={item.url}
                        src={
                          item.image !== null && item.image.url !== null
                            ? item.image.url
                            : ""
                        }
                        alt={
                          item.image !== null && item.image.alt !== null
                            ? item.image.alt
                            : ""
                        }
                      />
                    </div>
                  );
                })}
              </div>
            );
          })
        )}

        {/* <div className="button-container d-flex justify-content-center">
          <Button variant="default">Carregar mais</Button>
        </div>*/}
      </div>
    </Section>
  );
};

const types = {
  className: PropTypes.string,
  backgroundColor: PropTypes.string,
  sectionTitle: PropTypes.string,
  cardBackgroundColor: PropTypes.string,
  titleProps: PropTypes.object,
  data: PropTypes.array.isRequired,
  loading: PropTypes.bool,
  hasError: PropTypes.bool,
  theme: PropTypes.object
};

Dossiers.propTypes = types;

const DossiersWithStyles = styled(Dossiers)`
  .row {
    margin-bottom: 30px;
  }

  .button-container {
    margin-top: 70px;
  }

  // Small devices (landscape phones, less than 768px)
  @media (max-width: 767px) {
    .row {
      margin-bottom: 0;
      .background-image-card {
        margin-bottom: 20px;
      }
    }
    .button-container {
      margin-top: 50px;
    }
  }
  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    .row {
      .background-image-card {
        margin-bottom: 15px;
      }
    }
    .button-container {
      margin-top: 55px;
    }
  }
`;

export default withTheme(DossiersWithStyles);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => <DossiersWithStyles {...props} />;

WithProps.propTypes = types;
