import React from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../atoms";
import { withTheme } from "styled-components";

const Mask = props => {
  return (
    <ContentContainer
      backgroundColor={props.theme.loadings.bgColor}
      position="absolute"
      {...props}
    />
  );
};

const LoadingCard = props => {
  return (
    <ContentContainer height="394px">
      <AnimatedBackground height="394px">
        <Mask {...props} height="25px" right="0" width="70%" />
        <Mask {...props} height="10px" top="25px" width="100%" />
        <Mask {...props} height="5px" right="0" top="35px" width="90%" />
        <Mask {...props} height="40px" top="40px" width="100%" />
        <Mask
          {...props}
          backgroundColor={props.theme.loadings.bgColor}
          height="40px"
          top="80px"
          width="100%"
          border={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />
        <Mask {...props} height="40px" top="120px" width="100%" />
        <Mask
          {...props}
          height="12px"
          top="160px"
          width="100%"
          borderTop={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />
        <Mask {...props} height="15px" top="172px" right="0" width="60%" />
        <Mask
          {...props}
          height="12px"
          top="187px"
          width="100%"
          borderBottom={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />
        <Mask {...props} height="12px" top="199px" width="100%" />
        <Mask {...props} height="15px" top="211px" right="0" width="65%" />
        <Mask
          {...props}
          height="12px"
          top="226px"
          width="100%"
          borderBottom={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />
        <Mask {...props} height="12px" top="238px" width="100%" />
        <Mask {...props} height="15px" top="250px" right="0" width="65%" />
        <Mask
          {...props}
          height="12px"
          top="265px"
          width="100%"
          borderBottom={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />
        <Mask {...props} height="12px" top="277px" width="100%" />
        <Mask {...props} height="15px" top="289px" right="0" width="70%" />
        <Mask
          {...props}
          height="12px"
          top="304px"
          width="100%"
          borderBottom={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />
        <Mask {...props} height="12px" top="316px" width="100%" />
        <Mask {...props} height="15px" top="328px" right="0" width="60%" />
        <Mask
          {...props}
          height="12px"
          top="343px"
          width="100%"
          borderBottom={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />
        <Mask {...props} height="12px" top="355px" width="100%" />
        <Mask {...props} height="15px" top="367px" right="0" width="65%" />
        <Mask
          {...props}
          height="12px"
          top="382px"
          width="100%"
          borderBottom={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />
      </AnimatedBackground>
    </ContentContainer>
  );
};

const Loading = ({ className, ...props }) => {
  return (
    <div className={className}>
      <div className="container">
        <div className="row no-gutters">
          <div className="col-xl-7 offset-xl-3 col-lg-8 offset-lg-4 col-12 offset-0">
            <LoadingCard {...props} />
          </div>
        </div>
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  backgroundColor: PropTypes.string
};

export default withTheme(Loading);
