import React, { Fragment } from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//Components
import { Text, ScreenReaderText, Section } from "../../../atoms";
import {
  SectionHeader,
  NewsCard,
  BorderedCard,
  Error
} from "../../../molecules";
import { withSize } from "react-sizeme";
import moment from "moment";
//Loading
import LoadingPopular from "./LoadingPopular";
import LoadingRecent from "./LoadingRecent";
//Locales
import { compose } from "../../../../utils/utilities";
//Context
import { useConfig } from "../../../../context/ConfigContext";

const Card = ({ category, title, date, url, linkColor, ...props }) => {
  const { link: Link } = useConfig();
  return (
    <BorderedCard href={url} history={props.history}>
      <Link
        as="button"
        onClick={e => {
          e.preventDefault();
          props.history.push(props.categoryUrl);
        }}
        fontSize="14px"
        fontWeight={300}
        style={{ border: 0, backgroundColor: "transparent" }}
        color={linkColor}
      >
        {category}
      </Link>

      <Text size="small" fontWeight="400">
        {title}
      </Text>
      <Text size="xSmall" marginTop="10px" textTransform="lowercase">
        {date}
      </Text>
    </BorderedCard>
  );
};

Card.propTypes = {
  title: PropTypes.string,
  category: PropTypes.string,
  url: PropTypes.string,
  date: PropTypes.string,
  categoryUrl: PropTypes.string,
  history: PropTypes.object.isRequired,
  linkColor: PropTypes.string
};

const More = ({
  className,
  backgroundColor,
  titleProps,
  sectionTitle,
  rightSectionTitle,
  size,
  data,
  hasError,
  loading,
  onEnter,
  theme,
  ...props
}) => {
  const { formattedMessage: FormattedMessage } = useConfig();
  return (
    <Section
      className={className}
      backgroundColor={
        theme.highContrast.backgroundColor ||
        backgroundColor ||
        theme.components.journalMoreNews.backgroundColor
      }
      paddingTop="0"
      paddingBottom="0"
      mdPaddingTop="0"
    >
      <ScreenReaderText>
        <Text as="h2" text="Notícias" />
      </ScreenReaderText>
      <div className="container">
        <div className="row">
          <div className="col-xl-8 col-lg-8 col-12">
            <div className="container-left">
              <SectionHeader
                hasContainer={false}
                hasLink={false}
                hasLine={false}
                textColor={theme.components.journalMoreNews.titleColor}
                {...titleProps}
              >
                {props.resource ? (
                  <FormattedMessage
                    id={props.resource}
                    defaultMessage={sectionTitle}
                  />
                ) : (
                  sectionTitle
                )}
              </SectionHeader>
              {loading ? (
                <LoadingPopular />
              ) : hasError ? (
                <Error />
              ) : data.popular.listNews.length <= 0 ? (
                <div>
                  <FormattedMessage
                    id="app.content.empty"
                    defaultMessage="No content to display"
                  />
                </div>
              ) : (
                <Fragment>
                  <div className="row">
                    {data.popular.listNews.map(item => {
                      return (
                        <div
                          key={item.id}
                          className="col-xl col-lg col-md col-12 more-news-card"
                        >
                          <NewsCard
                            data={{
                              title: item.title,
                              url: item.url,
                              category: item.category,
                              summary: item.summary,
                              startDate: item.startDate,
                              categoryUrl: item.categoryUrl,
                              share: item.share,
                              imageCover: {
                                url:
                                  item.imageCover !== null &&
                                  item.imageCover.url
                              },
                              alt:
                                item.imageCover !== null
                                  ? item.imageCover.alt
                                  : ""
                            }}
                            onKeyPress={onEnter}
                            cardBgColor={
                              theme.components.journalMoreNews.newsCardBgColor
                            }
                            hasColAuto={false}
                            hasShareIcon={true}
                            size={size}
                            history={props.history}
                          />
                        </div>
                      );
                    })}
                  </div>
                </Fragment>
              )}
            </div>
          </div>
          {size.width >= 992 && (
            <div className="col-xl col-lg col-12">
              <div className="container-right">
                <SectionHeader
                  hasContainer={false}
                  hasLink={false}
                  hasLine={false}
                  textColor={theme.components.journalMoreNews.titleColor}
                  {...titleProps}
                >
                  {props.rightResource ? (
                    <FormattedMessage
                      id={props.rightResource}
                      defaultMessage={rightSectionTitle}
                    />
                  ) : (
                    rightSectionTitle
                  )}
                </SectionHeader>
                {loading ? (
                  <LoadingRecent />
                ) : hasError ? (
                  <Error />
                ) : data.recent.listNews.length <= 0 ? (
                  <div>
                    <FormattedMessage
                      id="app.content.empty"
                      defaultMessage="No content to display"
                    />
                  </div>
                ) : (
                  <Fragment>
                    {data.recent.listNews.map(item => {
                      return (
                        <Card
                          key={item.id}
                          date={moment(item.startDate).format("DD MMMM YYYY")}
                          {...item}
                          history={props.history}
                          linkColor={
                            theme.components.journalMoreNews
                              .borderedCardLinkColor
                          }
                        />
                      );
                    })}
                  </Fragment>
                )}
              </div>
            </div>
          )}
        </div>
      </div>
      {size.width <= 991 && (
        <div className="container-right">
          <div className="container">
            <SectionHeader
              hasContainer={false}
              hasLink={false}
              hasLine={false}
              textColor={theme.components.journalMoreNews.titleColor}
              {...titleProps}
              title={rightSectionTitle}
            />
            {loading ? (
              <LoadingRecent />
            ) : hasError ? (
              <Error />
            ) : data.recent.listNews.length <= 0 ? (
              <div>
                <FormattedMessage
                  id="app.content.empty"
                  defaultMessage="No content to display"
                />
              </div>
            ) : (
              <Fragment>
                {data.recent.listNews.map(item => {
                  return (
                    <Card
                      key={item.id}
                      date={moment(item.startDate).format("DD MMMM YYYY")}
                      history={props.history}
                      linkColor={
                        theme.components.journalMoreNews.borderedCardLinkColor
                      }
                      {...item}
                    />
                  );
                })}
              </Fragment>
            )}
          </div>
        </div>
      )}
    </Section>
  );
};

const types = {
  title: PropTypes.string,
  sectionTitle: PropTypes.string,
  rightSectionTitle: PropTypes.string,
  className: PropTypes.string,
  backgroundColor: PropTypes.string,
  titleProps: PropTypes.object,
  size: PropTypes.object,
  data: PropTypes.object.isRequired,
  loading: PropTypes.bool,
  hasError: PropTypes.bool,
  onEnter: PropTypes.func,
  history: PropTypes.object.isRequired,
  resource: PropTypes.string,
  rightResource: PropTypes.string,
  theme: PropTypes.object
};

More.propTypes = types;

const MoreWithStyles = styled(More)`
  &:before {
    content: "";
    position: absolute;
    top: 0;
    right: 0;
    height: 100%;
    width: 50%;
    background-color: ${props =>
      props.theme.components.journalMoreNews.containerRightBgColor};
  }

  .container-left {
    .Section-Header {
      position: relative;
      z-index: 1;
    }
    &:before {
      content: "";
      position: absolute;
      top: 0;
      left: 15px;
      right: 0;
      height: 100%;
      width: 100%;
      z-index: 0;
      background-color: ${props =>
        props.theme.highContrast.backgroundColor ||
        props.backgroundColor ||
        props.theme.components.journalMoreNews.backgroundColor};
    }
  }

  .container-left,
  .container-right {
    padding: 70px 0;
  }
  .container-right {
    background-color: ${props =>
      props.theme.components.journalMoreNews.containerRightBgColor};
    padding-left: 30px;
    height: 100%;
  }

  // Medium devices (tablets, less than 992px)
  @media (max-width: 991px) {
    &:before {
      content: none;
    }
    .container-left {
      &:before {
        content: none;
      }
    }
    .container-right {
      padding-left: 0;
    }
  }

  // Small devices (landscape phones, less than 768px)
  @media (max-width: 767px) {
    .container-left {
      .more-news-card {
        margin-bottom: 20px;
        &:last-child {
          margin-bottom: 0;
        }
      }
    }
  }
  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    .container-left {
      .more-news-card {
        margin-bottom: 15px;
      }
    }
  }
`;

export default compose(withTheme, withSize())(MoreWithStyles);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => <MoreWithStyles {...props} />;

WithProps.propTypes = types;
