import React from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
//Components
import { Text } from "../../../atoms";
import PlusButton from "../../PlusButton";

const AgendaFeatured = ({
  className,
  dateContainerTextColor,
  dateContainerDay,
  dateContainerMonth,
  buttonBgColor,
  buttonTextColor,
  text,
  textColor,
  title,
  titleColor,
  date,
  dateColor,
  href,
  target,
  theme,
  dateContainerMonthAriaLabel
}) => {
  return (
    <a className={className} tabIndex={0} href={href} target={target}>
      <div className="row no-gutters">
        <div className="col-auto">
          <div className="date-container">
            <div className="date-content-container">
              <Text
                fontSize="56px"
                text={dateContainerDay}
                color={
                  dateContainerTextColor ||
                  theme.components.agendaFeatured.dateContainerTextColor
                }
                fontWeight="900"
                lineHeight="1"
              />
              <Text
                fontSize="30px"
                text={dateContainerMonth}
                aria-label={dateContainerMonthAriaLabel}
                color={
                  dateContainerTextColor ||
                  theme.components.agendaFeatured.dateContainerTextColor
                }
                fontWeight="300"
                textTransform="uppercase"
                lineHeight="1"
              />
              <PlusButton
                backgroundColor={
                  buttonBgColor || theme.components.agendaFeatured.buttonBgColor
                }
                size="30px"
                right="-16px"
                top="calc(50% - 15px)"
                zIndex="2"
                aria-hidden="true"
                tabIndex={-1}
                as="div"
              >
                <Text
                  fontSize="24px"
                  fontWeight="100"
                  color={
                    buttonTextColor ||
                    theme.components.agendaFeatured.buttonTextColor
                  }
                  lineHeight="1"
                  text={theme.icons.plus}
                />
              </PlusButton>
            </div>
          </div>
        </div>
        <div className="col">
          <div className="content-container">
            <Text
              color={textColor || theme.components.agendaFeatured.textColor}
              fontWeight="300"
              text={text}
              size="article"
            />
            <div className="title">
              <Text
                as="h3"
                size="large"
                text={title}
                color={titleColor || theme.components.agendaFeatured.titleColor}
                fontWeight="500"
                lineHeight="1.3"
              />
            </div>
            <Text
              textTransform="lowercase"
              color={dateColor || theme.components.agendaFeatured.dateColor}
              text={date}
              fontSize="13px"
            />
          </div>
        </div>
      </div>
    </a>
  );
};

AgendaFeatured.propTypes = {
  className: PropTypes.string,
  dateContainerTextColor: PropTypes.string,
  dateContainerDay: PropTypes.string,
  dateContainerMonth: PropTypes.string,
  buttonBgColor: PropTypes.string,
  buttonTextColor: PropTypes.string,
  text: PropTypes.string,
  textColor: PropTypes.string,
  title: PropTypes.string,
  titleColor: PropTypes.string,
  date: PropTypes.string,
  dateColor: PropTypes.string,
  href: PropTypes.string,
  target: PropTypes.string,
  theme: PropTypes.object,
  dateContainerMonthAriaLabel: PropTypes.string
};

AgendaFeatured.defaultProps = {
  dateContainerDay: "00",
  dateContainerMonth: "MMM",
  text: "Texto",
  title: "Título",
  date: "data",
  target: "_blank"
};

const AgendaFeaturedWithStyles = styled(AgendaFeatured)`
  display: block;
  text-decoration: none;
  margin-top: ${props => props.marginTop};
  &:focus {
    ${props => props.theme.mixins.focus};
  }
  .date-container {
    height: 120px;
    width: 120px;
    text-align: center;
    background-color: ${props =>
      props.dateContainerBgColor ||
      props.theme.components.agendaFeatured.dateContainerBgColor};
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
    border: 1px solid
      ${props =>
        props.dateContainerBgColor ||
        props.theme.components.agendaFeatured.dateContainerBgColor};
    border-right: none;

    .date-content-container {
      z-index: 1;
    }

    &:before {
      content: "";
      -webkit-transition: all 0.2s;
      -moz-transition: all 0.2s;
      -o-transition: all 0.2s;
      transition: all 0.2s;
      position: absolute;
      left: 0;
      width: 0;
      height: 100%;
      background-color: ${props =>
        props.animationBgColor ||
        props.theme.components.agendaFeatured.animationBgColor};
      z-index: 0;
    }
  }

  &:hover {
    .date-container {
      p {
        color: ${props =>
          props.textColorHover ||
          props.theme.components.agendaFeatured.textColorHover};
      }
      :before {
        width: 100%;
      }
      a {
        background-color: ${props =>
          props.animationBgColor ||
          props.theme.components.agendaFeatured.animationBgColor};
      }
      .plus-button-triangle {
        background-color: ${props =>
          props.plusButtonHoverBgColor ||
          props.theme.components.agendaFeatured.plusButtonHoverBgColor};
      }
    }
  }

  .content-container {
    border: 1px solid
      ${props =>
        props.borderColor || props.theme.components.agendaFeatured.borderColor};
    padding: 30px;
    .title {
      margin: 10px 0;
    }
  }
`;

export default withTheme(AgendaFeaturedWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <AgendaFeaturedWithStyles {...props} />;

WithProps.propTypes = {
  className: PropTypes.string,
  dateContainerTextColor: PropTypes.string,
  dateContainerDay: PropTypes.string,
  dateContainerMonth: PropTypes.string,
  buttonBgColor: PropTypes.string,
  buttonTextColor: PropTypes.string,
  text: PropTypes.string,
  textColor: PropTypes.string,
  title: PropTypes.string,
  titleColor: PropTypes.string,
  date: PropTypes.string,
  dateColor: PropTypes.string,
  href: PropTypes.string,
  target: PropTypes.string,
  theme: PropTypes.object,
  dateContainerMonthAriaLabel: PropTypes.string,
  marginTop: PropTypes.string,
  dateContainerBgColor: PropTypes.string,
  animationBgColor: PropTypes.string,
  textColorHover: PropTypes.string,
  plusButtonHoverBgColor: PropTypes.string,
  borderColor: PropTypes.string
};

WithProps.defaultProps = {
  dateContainerDay: "00",
  dateContainerMonth: "MMM",
  text: "Texto",
  title: "Título",
  date: "data",
  target: "_blank"
};
