import React from "react";
import PropTypes from "prop-types";
//Styles
import styled from "styled-components";

const Input = styled.input`
  height: ${props => props.height || props.theme.components.input.height};
  width: ${props => props.width || "100%"};
  border: ${props => props.border || "none"};
  font-size: ${props =>
    props.fontSize || props.theme.components.input.fontSize};
  padding: 0 8px 0 8px;
  background-color: transparent;
  color: ${props => props.theme.components.input.color};

  &:focus {
    outline: 2px solid ${props => props.theme.components.input.focus};
  }

  &::placeholder {
    color: ${props => props.color || props.theme.components.input.color};
  }
`;

const types = {
  height: PropTypes.string,
  width: PropTypes.string,
  border: PropTypes.string,
  fontSize: PropTypes.string,
  color: PropTypes.string,
  children: PropTypes.node
};

Input.propTypes = types;

export default Input;

//Only used for the Storybook Props Table
export const WithProps = ({ children, ...props }) => (
  <Input {...props}>{children}</Input>
);

WithProps.propTypes = types;
