import React from "react";
import PropTypes from "prop-types";
import {
  AnimatedBackground,
  AspectRatio,
  ContentContainer
} from "../../../atoms";
import { withTheme } from "styled-components";

const Mask = ({ ...props }) => {
  return (
    <ContentContainer
      backgroundColor={props.theme.loadings.bgColor}
      position="absolute"
      {...props}
    />
  );
};

const Loading = ({ ...props }) => {
  return (
    <ContentContainer backgroundColor={props.theme.loadings.bgColor}>
      <AspectRatio ratio="oneByOne">
        <ContentContainer
          width="100%"
          height="100%"
          backgroundColor={props.theme.loadings.iconContainerBgColor}
          marginRight="20px"
        />
      </AspectRatio>

      <ContentContainer marginTop="10px">
        <AnimatedBackground height="94px">
          <Mask height="42px" right="0" width="10%" {...props} />
          <Mask height="16px" top="42px" width="100%" {...props} />
          <Mask height="36px" top="58px" right="0" width="50%" {...props} />
        </AnimatedBackground>
      </ContentContainer>
    </ContentContainer>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  CardBgColor: PropTypes.string
};

export default withTheme(Loading);
