import React from "react";
import PropTypes from "prop-types";
//Styles
import styled from "styled-components";
//theme
import { withExtendedClass } from "../../../utils/utilities.style";

const sizing = {
  xxSmall: `
    font-size: 12px;
  `,
  xSmall: `
    font-size: 14px;
  `,
  small: `
    font-size: 18px;
  `,
  medium: `
    font-size: 24px;
  `,
  large: `
    font-size: 32px;
  `,
  xLarge: `
    font-size: 36px;
  `,
  xxLarge: `
    font-size: 42px;
  `,
  xxxLarge: `
    font-size: 48px;
  `
};

const Icon = styled.i.attrs(({ className, name }) =>
  withExtendedClass({
    className,
    extend: `${name}`
  })
)`
  /*
  -- Size
  */
  ${props =>
    props.fontSize
      ? `font-size: ${props.fontSize || "inherit"}`
      : sizing[props.size]};
  /*
  -- Other props
  */
  cursor: ${props => props.cursor};
  color: ${props => props.color || props.theme.components.icon.color};
  margin-top: ${props => props.marginTop};
  margin-right: ${props => props.marginRight};
  margin-left: ${props => props.marginLeft};
  min-width: ${props => props.minWidth};
  opacity: ${props => props.opacity};

  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    /*
  -- Size XS
  */
    ${props =>
      props.fontSize ? `font-size: ${props.xsFontSize}` : sizing[props.xsSize]};
  }
`;

const types = {
  className: PropTypes.string,
  name: PropTypes.string.isRequired,
  cursor: PropTypes.string,
  fontSize: PropTypes.string,
  color: PropTypes.string,
  marginRight: PropTypes.string,
  children: PropTypes.node,
  xsSize: PropTypes.string,
  xsFontSize: PropTypes.string,
  marginTop: PropTypes.string,
  opacity: PropTypes.string
};

const defaults = {
  name: "fa-info"
};

Icon.propTypes = types;

Icon.defaultProps = defaults;

export default Icon;

//Only used for the Storybook Props Table
export const WithProps = ({ children, ...props }) => (
  <Icon {...props}>{children}</Icon>
);

WithProps.propTypes = types;

WithProps.defaultProps = defaults;
