import React from "react";
import PropTypes from "prop-types";
//Styles
import styled from "styled-components";
import theme from "../../../styles/theme";
import { rem } from "polished";
const sizing = {
  xSmall: `
    font-size: ${theme.sizes.xs}; //12px
  `,
  small: `
    font-size: ${theme.sizes.s}; //14px
  `,
  article: `
    font-size: ${theme.sizes.sm}; //15px
  `,
  mediumSmall: `
    font-size: ${theme.sizes.m}; //16px
  `,
  medium: `
    font-size: ${theme.sizes.l}; //18px
    `,
  mediumLarge: `
    font-size: ${theme.sizes.xl}; //20px
    `,
  large: `
    font-size: ${theme.sizes.xl2}; //22px
  `,
  xLarge: `
    font-size: ${theme.sizes.xl3m}; //28px
  `,
  xxLarge: `
    font-size: ${theme.sizes.xl4}; //32px
  `,
  xxxLarge: `
    font-size: ${theme.sizes.xl6}; //48px
  `
};

const Text = styled.p.attrs(({ text, children, isThemeReverse, isSerif }) => ({
  children: text || children,
  isThemeReverse,
  isSerif
}))`
  /*
  -- Size
  */
  ${props =>
    props.fontSize ? `font-size: ${rem(props.fontSize)}` : sizing[props.size]};
  /*
  -- Other props
  */
  color: ${props =>
    props.color ||
    (props.isThemeReverse
      ? props.theme.components.text.colorReverse
      : props.theme.components.text.color)};
  line-height: ${props => props.lineHeight || "1.5"};
  font-weight: ${props => props.fontWeight || props.theme.weight.light};
  margin-right: ${props => props.marginRight};
  margin-top: ${props => props.marginTop};
  margin-bottom: ${props => props.marginBottom};
  text-transform: ${props => props.textTransform};
  margin-left: ${props => props.marginLeft};
  word-break: ${props => props.wordBreak || "break-word"};
  display: ${props => props.display};
  transform: ${props => props.transform};
  padding-top: ${props => props.paddingTop};
  padding-bottom: ${props => props.paddingBottom};
  font-family: ${props =>
    props.fontFamily ||
    (props.isSerif ? props.theme.type.serif : props.theme.type.base)};
  visibility: ${props => props.visibility};
  cursor: ${props => props.cursor};
  opacity: ${props => props.opacity};
  white-space: ${props => props.whiteSpace};

  &:first-letter {
    text-transform: ${props => props.firstLetter};
  }

  &:focus {
    outline: 2px solid inherit;
  }

  ${props =>
    props.hasHover &&
    `&:hover{
    color: ${props.hoverColor};
  }`};

  /*
  -- Responsive
  */
  // Medium devices (tablets, less than 992px)
  @media (max-width: 991px) {
    color: ${props => props.mobileMdColor};
  }
  // Small devices (landscape phones, less than 768px)
  @media (max-width: 767px) {
    /*
    -- Size
    */
    ${props =>
      props.fontSize
        ? `font-size: ${props.mobileSmFontSize}`
        : sizing[props.sizeSmSize]}
  }

  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    /*
    -- Size
    */
    ${props =>
      props.fontSize
        ? `font-size: ${props.mobileXsFontSize}`
        : sizing[props.sizeXsSize]}
    margin-top: ${props => props.mobileXsMarginTop};
    margin-bottom: ${props => props.mobileXsMarginBottom};
  }
`;

const types = {
  as: PropTypes.string,
  fontSize: PropTypes.string,
  color: PropTypes.string,
  lineHeight: PropTypes.string,
  fontWeight: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  marginBottom: PropTypes.string,
  marginTop: PropTypes.string,
  display: PropTypes.string,
  marginRight: PropTypes.string,
  marginLeft: PropTypes.string,
  textTransform: PropTypes.string,
  mobileSmFontSize: PropTypes.string,
  mobileXsFontSize: PropTypes.string,
  sizeSmSize: PropTypes.string,
  sizeXsSize: PropTypes.string,
  visibility: PropTypes.string,
  isThemeReverse: PropTypes.bool,
  isSerif: PropTypes.bool,
  firstLetter: PropTypes.string,
  paddingBottom: PropTypes.string,
  children: PropTypes.node,
  opacity: PropTypes.string
};

const defaults = {
  isThemeReverse: false,
  isSerif: false,
  size: "small"
};

Text.propTypes = types;
Text.defaultProps = defaults;

export default Text;

//Only used for the Storybook Props Table
export const WithProps = ({ children, ...props }) => (
  <Text {...props}>{children}</Text>
);

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
