import React from "react";
import PropTypes from "prop-types";
//components
import Loading from "./Loading";
import { Title, Text, Section, ContentContainer } from "../../atoms";
import { Error } from "../../molecules";
import { withTheme } from "styled-components";

function Curricular({
  backgroundColor,
  styles,
  loading,
  hasError,
  content,
  theme
}) {
  return (
    <Section
      backgroundColor={
        theme.highContrast.backgroundColor ||
        backgroundColor ||
        theme.components.curricular.backgroundColor
      }
      paddingTop="0"
    >
      <div className="container">
        {loading ? (
          <Loading />
        ) : hasError ? (
          <Error text={"No content to display"} />
        ) : (
          content.data.list.map((res, index) => {
            return (
              <ContentContainer key={index} paddingTop="70px">
                <div className="row">
                  {res.content.map((item, index) => {
                    return (
                      <div key={index} className="col-xl col-lg col-6">
                        <ContentContainer
                          mdMargintop={index > 1 ? "70px" : "0"}
                        >
                          <Text
                            text={item.value}
                            {...styles.text}
                            color={theme.highContrast.text}
                          />
                          <Title
                            tag="h3"
                            text={item.label}
                            {...styles.title}
                            color={theme.highContrast.text}
                          />
                        </ContentContainer>
                      </div>
                    );
                  })}
                </div>
              </ContentContainer>
            );
          })
        )}
      </div>
    </Section>
  );
}

const defaults = {
  content: {}
};

const types = {
  loading: PropTypes.bool,
  hasError: PropTypes.bool,
  content: PropTypes.object.isRequired,
  backgroundColor: PropTypes.string,
  styles: PropTypes.object.isRequired,
  theme: PropTypes.object
};

Curricular.propTypes = types;
Curricular.defaultProps = defaults;

export default withTheme(Curricular);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => <Curricular {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
