import React from "react";
import { withTheme } from "styled-components";
import PropTypes from "prop-types";
//Components
import { ContentContainer } from "./../../../atoms";
import { EventCardLoading } from "./../../../molecules";

const Loading = ({ className, theme }) => {
  return (
    <ContentContainer
      className={className}
      marginTop="50px"
      backgroundColor={theme.loadings.mainBgColor}
    >
      {/*Row of Cards*/}
      <div className="row no-gutters">
        <div className="col-lg col-12">
          <EventCardLoading
            cardBgColor={theme.components.subWebEvents.card.bgColor.lightGray}
          />
        </div>
        <div className="col-lg col-12">
          <EventCardLoading
            orderClasses={[
              "order-lg-0 order-md-1 order-sm-1 order-1",
              "order-lg-1 order-md-0 order-sm-0 order-0"
            ]}
          />
        </div>
      </div>
      {/*Row of Cards*/}
      <div className="row no-gutters">
        <div className="col-lg col-12">
          <EventCardLoading
            orderClasses={[
              "order-lg-1 order-md-0 order-sm-0 order-0",
              "order-lg-0 order-md-1 order-sm-1 order-1"
            ]}
          />
        </div>
        <div className="col-lg col-12">
          <EventCardLoading orderClasses={["order-1", "order-0"]} />
        </div>
      </div>
      {/*Row of One Card*/}
      <div className="row no-gutters justify-content-end">
        <div className="col-lg-6 col-12">
          <EventCardLoading
            cardBgColor={theme.components.subWebEvents.card.bgColor.darkGray}
            orderClasses={[
              "order-lg-1 order-md-0 order-sm-0 order-0",
              "order-lg-0 order-md-1 order-sm-1 order-1"
            ]}
          />
        </div>
      </div>
    </ContentContainer>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  theme: PropTypes.object
};

export default withTheme(Loading);
