import React from "react";
import { Text, Section } from "../../atoms";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";

const OneFact = ({ data, backgroundColor, isThemeReverse, theme }) => {
  return (
    <Section
      backgroundColor={
        theme.highContrast.backgroundColor ||
        backgroundColor ||
        theme.components.oneFact.backgroundColor
      }
    >
      <div className="container">
        <div className="row align-items-center">
          {data.map((columnContent, index) => (
            <div className="col-xl col-lg col-md-12" key={index}>
              {index === 0 ? (
                <Number
                  isThemeReverse={isThemeReverse}
                  numberMobileSmFontSize="80px"
                >
                  {columnContent}
                </Number>
              ) : (
                <Text
                  fontSize="20px"
                  mobileSmFontSize="16px"
                  lineHeight="1.8"
                  isThemeReverse={isThemeReverse}
                  isSerif
                >
                  {columnContent}
                </Text>
              )}
            </div>
          ))}
        </div>
      </div>
    </Section>
  );
};

const Number = styled.div`
  font-family: ${props => props.theme.type.serif};
  font-size: ${props => props.theme.sizes.xll};
  color: ${props =>
    props.theme.highContrast.text || props.isThemeReverse
      ? props.theme.components.text.colorReverse
      : props.textColor || props.theme.components.text.color};

  // Small devices (landscape phones, less than 768px)
  @media (max-width: 767px) {
    font-size: ${props => props.numberMobileSmFontSize};
  }
`;

OneFact.defaultProps = {
  hasLine: false,
  data: [],
  isThemeReverse: false
};

OneFact.propTypes = {
  backgroundColor: PropTypes.string,
  hasLine: PropTypes.bool,
  data: PropTypes.arrayOf(PropTypes.string).isRequired,
  isThemeReverse: PropTypes.bool,
  numberMobileSmFontSize: PropTypes.string,
  theme: PropTypes.object
};

export default withTheme(OneFact);
