import React from "react";
import styled, { withTheme } from "styled-components";
import PropTypes from "prop-types";
import { HorizontalNewsCardLoading } from "../../../molecules";

const Loading = ({ className, theme, ...props }) => {
  return (
    <div className={className}>
      <div className="horizontal-cards">
        <div className="row">
          <div className="col-12">
            <HorizontalNewsCardLoading
              {...props}
              contentContainerBgColor={
                theme.loadings.journalHighlights.horizontalNewsCard.bgColor
              }
              maskBgColor={
                theme.loadings.journalHighlights.horizontalNewsCard.maskColor
              }
              columnFullSize={true}
              changeMdOrder={true}
            />
          </div>
        </div>
        <div className="row no-gutters">
          <div className="col-xl-6 col-lg-6 col-12">
            <HorizontalNewsCardLoading {...props} />
          </div>
          <div className="col-xl-6 col-lg-6 col-12">
            <HorizontalNewsCardLoading {...props} changeMdOrder={true} />
          </div>
        </div>
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string
};

const LoadingWithStyles = styled(Loading)`
  background-color: ${props =>
    props.theme.loadings.journalHighlights.backgroundColor};

  // Small devices (landscape phones, less than 768px)
  @media (max-width: 767px) {
    .loading-card-animated-background {
      height: 122px;
    }
  }

  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
  .loading-card-animated-background {
    height: 100px;
  }
`;

export default withTheme(LoadingWithStyles);
