import React from "react";
import PropTypes from "prop-types";
import styled, { withTheme, css } from "styled-components";
import { Text, ContentContainer } from "../../../atoms";

const titleStyles = css`
  .card-title {
    color: ${props =>
      props.theme.highContrast.text ||
      props.titleHoverColor ||
      props.theme.components.showMoreCard.hoverTitleColor};
  }
`;

const ShowMoreCardContainer = styled.a`
  text-decoration: none;
  position: relative;
  height: 100%;
  padding: 10px;
  min-height: 80px;
  border-top: 1px solid
    ${props => props.theme.components.showMoreCard.borderColor};

  &:after {
    content: "";
    position: absolute;
    height: 0;
    top: -1px;
    left: 0;
    right: 0;
    background-color: ${props =>
      props.theme.components.showMoreCard.hoverBgColor};
    z-index: 0;
    transition: height 0.3s ease-in-out;
  }

  @media (min-width: 768px) {
    &:hover {
      ${titleStyles};
      &:after {
        height: 100%;
      }
    }
  }

  @media (max-width: 767.98px) {
    ${titleStyles};
    &:after {
      height: 100%;
    }
  }
`;

const ShowMoreCard = ({ className, title, theme, href, ...props }) => {
  return (
    <ShowMoreCardContainer
      className={`${className} d-flex`}
      href={href}
      {...props}
    >
      <ContentContainer zIndex="1">
        <Text
          size="large"
          sizeSmSize="medium"
          color={theme.components.showMoreCard.titleColor}
          className="card-title"
          fontWeight="400"
        >
          {title}
        </Text>
      </ContentContainer>
    </ShowMoreCardContainer>
  );
};

const types = {
  className: PropTypes.string,
  theme: PropTypes.object,
  title: PropTypes.string,
  href: PropTypes.string,
  titleHoverColor: PropTypes.string
};

const defaults = {
  title: "Título"
};

ShowMoreCard.propTypes = types;
ShowMoreCard.defaultProps = defaults;

export default withTheme(ShowMoreCard);

//Only used for the Storybook Props Table
export const WithProps = props => <ShowMoreCard {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
