import React from "react";
import styled from "styled-components";
import PropTypes from "prop-types";

const FadeIn = styled.div`
  height: ${props => props.height};
  ${props =>
    props.fadeIn &&
    `
    -webkit-animation: FadeIn ${props.animationTime}s
      cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    -moz-animation: FadeIn ${props.animationTime}s
      cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    -o-animation: FadeIn ${props.animationTime}s
      cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
    animation: FadeIn ${props.animationTime}s
      cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
  `};

  @-webkit-keyframes FadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @-moz-keyframes FadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @-o-keyframes FadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

  @keyframes FadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }
`;

const types = {
  fadeIn: PropTypes.bool,
  animationTime: PropTypes.number,
  height: PropTypes.string,
  children: PropTypes.node
};

const defaults = {
  fadeIn: true,
  animationTime: 1
};

FadeIn.propTypes = types;
FadeIn.defaultProps = defaults;

export default FadeIn;

//Only used for the Storybook Props Table
export const WithProps = ({ children, ...props }) => (
  <FadeIn {...props}>{children}</FadeIn>
);

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
