import React from "react";
import styled from "styled-components";

export const TableStyles = styled.div`
  overflow-x: auto;

  table {
    font-family: ${props => props.theme.type.base};
    font-size: ${props => props.theme.sizes.s};
    line-height: 1.6;
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    color: ${props => props.theme.components.table.textColor};
    font-weight: 300;
  }

  caption,
  th,
  td {
    text-align: left;
  }

  th,
  strong,
  b,
  td:first-child {
    font-weight: 400;
  }

  caption {
    font-size: ${props => props.theme.sizes.xl3m};
    font-weight: 500;
    line-height: 1.4;
    margin-bottom: 25px;
  }

  th {
    padding: 0 5px 0 5px;
    &:first-child {
      padding: 0 5px 0 0;
    }
    &:last-child {
      padding: 0 0 0 5px;
    }
  }

  td,
  th {
    padding: 10px 5px;
    min-width: 75px;
    &:first-child {
      padding: 10px 5px 10px 0;
    }
    &:last-child {
      padding: 10px 0 10px 5px;
    }
  }

  td {
    border-top: ${props =>
      `1px solid ${props.theme.components.table.borderColor}`};
    border-bottom: ${props =>
      `1px solid ${props.theme.components.table.borderColor}`};
  }

  a {
    color: ${props => props.theme.components.tables.linkColor};
    &:hover {
      color: ${props => props.activeColor};
    }
  }
`;
