import React, { Fragment } from "react";
import PropTypes from "prop-types";
//Components
import { Title, Text, Line } from "../../atoms";
import { Error } from "../../molecules";
import Loading from "./Loading";
import {
  ContentContainer,
  ListItem,
  Section,
  VerticalList
} from "../../atoms/index";
import { Select } from "../../molecules/index";
//import { StoreContext } from "../../../context/StoreContext";
import { withTheme } from "styled-components";
//Context
import { useConfig } from "../../../context/ConfigContext";
function Courses({
  titleColor,
  textColor,
  textSize,
  textLineHeight,
  className,
  content,
  loading,
  onFilter,
  data,
  filters,
  hasLine,
  lang,
  hasHash,
  theme,
  activeColor
}) {
  const { link: Link } = useConfig();
  /*
  const GlobalStore = useContext(StoreContext);

  const {
    state: { activeColor }
  } = GlobalStore;
  */

  return (
    <Section
      className={className}
      backgroundColor={theme.components.courses.backgroundColor}
      hasHighContrastBorderTop={false}
    >
      {loading ? (
        <Loading />
      ) : (
        <Fragment>
          <div className="container">
            <div className="row no-gutters">
              <div className="offset-xl-3 col-xl-7 offset-lg-4 col-lg col-12">
                <Title
                  tag="h1"
                  text={content.title}
                  color={theme.highContrast.text || titleColor}
                  marginBottom="10px"
                  firstLetter="capitalize"
                />
                {hasLine && (
                  <Line
                    color={theme.components.courses.lineColor}
                    marginBottom="25px"
                  />
                )}
                <ContentContainer mobileMdDisplay="none">
                  <Text
                    text={content.description}
                    color={theme.highContrast.text || textColor}
                    fontWeight="300"
                    fontSize={textSize}
                    lineHeight={textLineHeight}
                  />
                </ContentContainer>

                <ContentContainer marginTop="40px" marginBottom="40px">
                  <div className="row">
                    {filters.map((item, index) => {
                      return (
                        <div
                          className="col-xl col-lg col-md col-sm col-12"
                          key={index}
                        >
                          <ContentContainer
                            mobileXsMarginTop={index === 1 ? "20px" : "0"}
                          >
                            <Select
                              variant="black"
                              options={item.options}
                              placeholder={item.title}
                              aria-label={item.title}
                              inputProps={{ "aria-label": item.title }}
                              label={item.title}
                              onChange={option =>
                                onFilter(option, item.jsonField)
                              }
                              isClearable
                            />
                          </ContentContainer>
                        </div>
                      );
                    })}
                  </div>
                </ContentContainer>

                <ContentContainer
                  marginTop="40px"
                  marginRight="0"
                  mobileXsMarginRight="0"
                >
                  {/*Lista de Cursos*/}
                  <VerticalList>
                    {Object.keys(data).length <= 0 ? (
                      <Error>Sem conteúdos para o filtro.</Error>
                    ) : (
                      data.map((item, index) => {
                        return (
                          <ListItem
                            key={item.id}
                            paddingTop="10px"
                            paddingBottom="10px"
                            borderBottom={`1px solid ${theme.components.courses.borderColor}`}
                            borderTop={
                              index === 0
                                ? `1px solid ${theme.components.courses.borderColor}`
                                : "none"
                            }
                          >
                            {item.type === "link" ? (
                              <Link
                                href={`${
                                  lang === "pt"
                                    ? `${hasHash}/${lang}/curso`
                                    : `${hasHash}/${lang}/course`
                                }${item.url}`}
                                fontSize="14px"
                                fontWeight="300"
                                aria-label={item.name + " " + item.note}
                                default={true}
                                color={theme.components.courses.listLinkColor}
                                textDecorationColor={
                                  theme.highContrast.linkHover || activeColor
                                }
                              >
                                {item.name}{" "}
                                {item.note && (
                                  <Text
                                    as="span"
                                    size="small"
                                    color={theme.components.courses.noteColor}
                                  >
                                    {item.note}
                                  </Text>
                                )}
                              </Link>
                            ) : (
                              <Text
                                color={theme.components.courses.textColor}
                                fontSize="14px"
                                fontWeight="300"
                                aria-label={item.name}
                              >
                                {item.name}
                              </Text>
                            )}
                          </ListItem>
                        );
                      })
                    )}
                  </VerticalList>
                </ContentContainer>
              </div>
            </div>
          </div>
        </Fragment>
      )}
    </Section>
  );
}

const defaults = {
  content: {},
  data: [],
  onFilter: () => null,
  filters: [],
  lang: "pt",
  hasHash: false,
  activeColor: "#0EB4BD"
};

const types = {
  className: PropTypes.string,
  titleColor: PropTypes.string,
  textColor: PropTypes.string,
  textSize: PropTypes.string,
  textLineHeight: PropTypes.string,
  content: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
  data: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
  hasLine: PropTypes.bool,
  loading: PropTypes.bool,
  onFilter: PropTypes.func.isRequired,
  filters: PropTypes.array.isRequired,
  lang: PropTypes.string,
  hasHash: PropTypes.bool,
  theme: PropTypes.object,
  activeColor: PropTypes.string
};

Courses.propTypes = types;
Courses.defaultProps = defaults;

export default withTheme(Courses);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => <Courses {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
