import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../../atoms";
import { withTheme } from "styled-components";

const Mask = ({ maskBgColor, theme, ...props }) => {
  return (
    <ContentContainer
      backgroundColor={maskBgColor || theme.loadings.maskBgColor}
      position="absolute"
      {...props}
    />
  );
};

Mask.propTypes = {
  maskBgColor: PropTypes.string
};

const CardTextAnimation = ({ ...props }) => {
  return (
    <AnimatedBackground
      height="146px"
      className="loading-card-animated-background"
    >
      <Mask height="18px" right="0" width="80%" {...props} />
      <Mask height="18px" top="18px" width="100%" {...props} />
      <Mask
        height="30px"
        smHeight="22px"
        mobileXsHeight="18px"
        top="36px"
        right="0"
        width="10%"
        {...props}
      />
      <Mask
        height="10px"
        mobileXsHeight="5px"
        top="66px"
        smTop="58px"
        xsTop="54px"
        width="100%"
        {...props}
      />
      <Mask
        height="30px"
        smHeight="22px"
        mobileXsHeight="18px"
        top="76px"
        smTop="68px"
        xsTop="59px"
        right="0"
        width="20%"
        {...props}
      />
      <Mask
        height="10px"
        mobileXsHeight="5px"
        top="106px"
        smTop="90px"
        xsTop="77px"
        width="100%"
        {...props}
      />
      <Mask
        height="30px"
        smHeight="22px"
        mobileXsHeight="18px"
        top="116px"
        smTop="100px"
        xsTop="82px"
        right="0"
        width="30%"
        {...props}
      />
    </AnimatedBackground>
  );
};

const Loading = ({
  changeMdOrder,
  contentContainerBgColor,
  columnFullSize,
  ...props
}) => {
  return (
    <Fragment>
      <div className="row no-gutters">
        <div
          className={`${columnFullSize ? "col-sm col-12" : "col"} order-lg-0 ${
            changeMdOrder ? "order-1" : ""
          }`}
        >
          <ContentContainer
            className="d-flex flex-column justify-content-between"
            padding="20px"
            height="294px"
            mobileXsHeight={columnFullSize ? "213px" : "250px"}
            backgroundColor={
              contentContainerBgColor || props.theme.loadings.cardBgColor
            }
          >
            {/*Text Animation*/}
            <CardTextAnimation {...props} />
            <ContentContainer>
              <AnimatedBackground height="18px">
                <Mask height="18px" right="0" width="75%" {...props} />
              </AnimatedBackground>
            </ContentContainer>
          </ContentContainer>
        </div>
        <div
          className={`${columnFullSize ? "col-sm col-12" : "col"} order-lg-1 ${
            changeMdOrder ? "order-0" : ""
          }`}
        >
          <ContentContainer
            height="294px"
            mobileXsHeight={columnFullSize ? "185px" : "250px"}
            backgroundColor={props.theme.loadings.iconContainerBgColor}
          />
        </div>
      </div>
    </Fragment>
  );
};

Loading.propTypes = {
  changeMdOrder: PropTypes.bool,
  contentContainerBgColor: PropTypes.string,
  columnFullSize: PropTypes.bool
};

Loading.defaultProps = {
  columnFullSize: false
};

export default withTheme(Loading);
