import React from "react";
import PropTypes from "prop-types";
//components
import { Text, Image, ContentContainer } from "../../atoms";
import { withTheme } from "styled-components";
//Context
import { useConfig } from "../../../context/ConfigContext";

const NestedRow = ({ data, theme }) => {
  const { link: Link } = useConfig();
  return (
    <div className="row">
      {data.contents.map((res, index) => {
        return (
          <div key={index} className="col-xl col-lg col-md-12">
            <Text
              as="h2"
              size="xLarge"
              fontWeight="500"
              text={res.title}
              marginBottom="10px"
            />
            {res.list.map((item, index) => {
              return (
                <ContentContainer
                  key={index}
                  mdMarginBottom={index === 0 && "50px"}
                >
                  <Text
                    as="h3"
                    size="small"
                    fontWeight="500"
                    text={item.title}
                    marginTop="10px"
                    marginBottom="10px"
                  />
                  {item.contents.map((item, index) => {
                    return (
                      <ContentContainer key={index}>
                        {item.type === "link" ? (
                          <Link
                            lineHeight="1.6"
                            fontSize="15px"
                            fontWeight="300"
                            text={item.info.value}
                            href={item.extra}
                            aria-label={item.info.value}
                            target="_blank"
                            textDecoration="underline"
                            color={
                              theme.components.contents.nestedRow.linkColor
                            }
                            hoverColor={
                              theme.components.contents.nestedRow.linkHoverColor
                            }
                            display="block"
                            default
                            native
                          />
                        ) : item.type === "image" ? (
                          <Link href={item.info.value} default>
                            <Image
                              src={item.extra}
                              title={item.info.label}
                              alt={item.info.label}
                            />
                          </Link>
                        ) : (
                          <Text
                            lineHeight="1.6"
                            size="article"
                            fontWeight="300"
                            text={item.info.value}
                          />
                        )}
                      </ContentContainer>
                    );
                  })}
                </ContentContainer>
              );
            })}
          </div>
        );
      })}
    </div>
  );
};

NestedRow.propTypes = {
  data: PropTypes.object.isRequired,
  theme: PropTypes.object
};

export default withTheme(NestedRow);
