import React from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
import { lighten } from "polished";
//Components
import { Text, Link } from "../../../atoms";
import PlusButton from "../../PlusButton";
import SocialMediaActions from "../../SocialMediaActions";
import ImageWrapper from "../../../organisms/ImageWrapper";
import moment from "moment";
import { compose } from "../../../../utils/utilities";

const NewsCard = ({
  className,
  data,
  onKeyPress,
  imageBgColor,
  linkColor,
  onKeyDown,
  hasColAuto,
  hasShareIcon,
  centerPlusButton,
  onMouseOver,
  theme,
  hasCategoryTitle,
  size,
  history,
  imageSize,
  plusButtonSize,
  plusButtonFontSize,
  titleIsSerif,
  plusButtonBgColor,
  changeImageVerticalOrder,
  linkProps
}) => {
  return (
    <div className={className} onKeyDown={onKeyDown} onMouseOver={onMouseOver}>
      <div
        className={`d-flex flex-column flex-grow-1 ${hasColAuto && "col-auto"}`}
      >
        <Link
          tabIndex={0}
          onKeyPress={e => onKeyPress(e, data.url)}
          href={data.url}
          className="news-card d-flex flex-column"
          hasHover={false}
          {...linkProps}
        >
          {data.imageCover !== null && data.imageCover !== undefined && (
            <div
              className={`image-container ${
                changeImageVerticalOrder ? "order-last" : ""
              }`}
            >
              <ImageWrapper
                size="xs"
                className="news-image"
                height={imageSize + "px" || "185px"}
                width="100%"
                backgroundColor={imageBgColor}
                data={{
                  url:
                    data.imageCover !== null && data.imageCover.url !== null
                      ? data.imageCover.url
                      : "https://via.placeholder.com/350"
                }}
                alt={
                  data.imageCover !== null && data.imageCover.alt !== null
                    ? data.imageCover.alt
                    : `Imagem da Notícia ${data.title}`
                }
                hasEndpoint={false}
                endpoint={() => null}
              />
            </div>
          )}
          <div
            className={`content-container d-flex flex-column justify-content-between flex-grow-1 ${
              changeImageVerticalOrder ? "order-first" : ""
            }`}
          >
            {data.imageCover !== null && (
              <PlusButton
                left={centerPlusButton ? "0" : "25px"}
                right={centerPlusButton ? "0" : null}
                justifyContent={centerPlusButton && "center"}
                size={plusButtonSize + "px"}
                top={
                  changeImageVerticalOrder ? null : -plusButtonSize / 2 + "px"
                }
                bottom={
                  changeImageVerticalOrder ? -plusButtonSize / 2 + "px" : null
                }
                aria-hidden="true"
                tabIndex={-1}
                target="_blank"
                alt={data.title}
                href={data.url}
                className="news-plus-button"
                backgroundColor={
                  plusButtonBgColor ||
                  theme.components.newsCard.plusButtonBgColor
                }
                native
              >
                <Text
                  fontSize={plusButtonFontSize || "24px"}
                  fontWeight="100"
                  margin="auto"
                  fontDecoration="none"
                  color={
                    theme.highContrast.text ||
                    linkColor ||
                    theme.components.newsCard.plusButtonTextColor
                  }
                  text={theme.icons.plus}
                  lineHeight="0"
                />
              </PlusButton>
            )}
            <div>
              {hasCategoryTitle && (
                <Link
                  as="button"
                  onClick={e => {
                    e.preventDefault();
                    history.push(data.categoryUrl);
                  }}
                  text={data.category}
                  color={theme.components.newsCard.categoryColor}
                  fontWeight="300"
                  lineHeight="1.4"
                  //Accessibility problems with id="tag" remove later on if all ok
                  //id="tag"
                  marginBottom="10px"
                  //this className in other components to access this element
                  className="news-card-category"
                />
              )}

              <div className="title-container">
                <Text
                  as="h3"
                  size="medium"
                  fontDecoration="none"
                  fontWeight="400"
                  lineHeight="1.5"
                  text={data.title}
                  marginBottom={data.imageCover === null ? "20px" : ""}
                  isSerif={titleIsSerif}
                  //this className in other components to access this element
                  className="news-card-title"
                />
              </div>
              {data.imageCover === null && (
                <div className="cover-is-null">
                  <Text
                    size="article"
                    lineHeight="1.6"
                    text={data.summary}
                    //this className in other components to access this element
                    className="news-card-text"
                  />
                </div>
              )}
            </div>
            <div className="content-container-footer">
              <div className="row align-items-center">
                <div className="col">
                  <Text
                    text={moment(data.startDate).format("DD MMMM YYYY")}
                    lineHeight="1.6"
                    size="xSmall"
                    fontWeight="300"
                    textTransform="lowercase"
                    color={theme.components.newsCard.dateColor}
                    //this className in other components to access this element
                    className="news-card-date"
                  />
                </div>
                {hasShareIcon && (
                  <div className="col-auto">
                    <SocialMediaActions
                      size={size}
                      iconColor={theme.components.newsCard.shareIconColor}
                      share={data.share}
                    />
                  </div>
                )}
              </div>
            </div>
          </div>
        </Link>
      </div>
    </div>
  );
};

const types = {
  className: PropTypes.string,
  data: PropTypes.object,
  onClick: PropTypes.func,
  onKeyDown: PropTypes.func,
  cardBgColor: PropTypes.string,
  onKeyPress: PropTypes.func,
  onMouseOver: PropTypes.func,
  imageBgColor: PropTypes.string,
  linkColor: PropTypes.string,
  href: PropTypes.string,
  hasColAuto: PropTypes.bool,
  hasShareIcon: PropTypes.bool,
  centerPlusButton: PropTypes.bool,
  theme: PropTypes.object,
  hasCategoryTitle: PropTypes.bool,
  size: PropTypes.object,
  history: PropTypes.object,
  imageSize: PropTypes.number,
  plusButtonSize: PropTypes.number,
  plusButtonFontSize: PropTypes.string,
  plusButtonBgColor: PropTypes.string,
  titleIsSerif: PropTypes.bool,
  changeImageVerticalOrder: PropTypes.bool,
  linkProps: PropTypes.object,
  mobileCardBgColor: PropTypes.string
};

const defaults = {
  hasColAuto: true,
  hasShareIcon: true,
  centerPlusButton: false,
  href: "/",
  hasCategoryTitle: true,
  imageSize: 185,
  plusButtonSize: 30,
  titleIsSerif: false,
  changeImageVerticalOrder: false
};

NewsCard.propTypes = types;
NewsCard.defaultProps = defaults;

const NewsCardWithStyles = styled(NewsCard)`
  height: 100%;
  > div {
    height: inherit;
  }
  .news-card {
    display: block;
    text-decoration: none;
    flex-grow: 1;
    button {
      background-color: transparent;
      cursor: pointer;
      border: none;
      &:hover {
        i {
          color: ${props =>
            lighten(0.5, props.theme.components.newsCard.shareIconColor)};
        }
      }
    }
    &:focus {
      ${props => props.theme.mixins.focus};
    }
    background-color: ${props =>
      props.cardBgColor || props.theme.components.newsCard.cardBackgroundColor};
    min-height: 398px;
    height: 100%;

    //Image Animation
    img {
      -webkit-transition: all 0.4s;
      -moz-transition: all 0.4s;
      -o-transition: all 0.4s;
      transition: all 0.4s;
    }

    cursor: pointer;
    &:hover {
      img {
        -ms-transform: scale(1.1);
        -moz-transform: scale(1.1);
        -webkit-transform: scale(1.1);
        -o-transform: scale(1.1);
        transform: scale(1.1);
      }
    }

    .image-container {
      height: ${props => props.imageSize + "px" || "200px"};
      overflow: hidden;
    }

    .content-container {
      position: relative;
      padding: 20px;
      min-height: ${props =>
        props.data.imageCover === null
          ? "100%"
          : props.contentContainerHeight || "198px"};

      .title-container {
        max-height: 80px;
        overflow: hidden;
        z-index: 1;
        position: relative;
      }

      .cover-is-null {
        margin-top: 10px;
        height: 190px;
        overflow: hidden;
      }

      .content-container-footer {
        margin-top: 20px;
      }
    }
  }
  // Small devices (landscape phones, less than 768px)
  @media (max-width: 767px) {
    .news-card {
      background-color: ${props => props.mobileCardBgColor};
    }
  }
`;

NewsCardWithStyles.propTypes = {
  contentContainerHeight: PropTypes.string,
  mobileCardBgColor: PropTypes.string
};

NewsCardWithStyles.defaultProps = {
  imageSize: 185
};

export default compose(withTheme)(NewsCardWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <NewsCardWithStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
