import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";

const variantModifier = {
  /* 16:9 Aspect Ratio */
  sixteenByNine: `
    padding-top: 56.25%;
  `,
  /* 4:3 Aspect Ratio */
  fourByTree: `
    padding-top: 75%;
  `,
  /* 3:2 Aspect Ratio */
  treeByTwo: `
    padding-top: 66.66%;
  `,
  /* 8:5 Aspect Ratio */
  eightByFive: `
    padding-top: 62.5%;
  `,
  /* 1:1 Aspect Ratio  */
  oneByOne: `
    padding-top: 100%;
  `
};

const AspectRatio = ({ children, className }) => {
  return (
    <div className={className}>
      <div className="aspRTO">{children}</div>
    </div>
  );
};

AspectRatio.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string
};

const AspectRatioWithStyles = styled(AspectRatio)`
  position: relative;
  background-color: ${props =>
    props.backgroundColor ||
    props.theme.components.aspectRatio.backgroundColor};
  height: 0;

  //Default Aspect Ratio 1:1
  ${props => variantModifier[props.ratio]};

  .aspRTO {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
  }
`;

const types = {
  children: PropTypes.node,
  className: PropTypes.string,
  backgroundColor: PropTypes.string,
  ratio: PropTypes.string.isRequired
};

const defaults = {
  ratio: "oneByOne"
};

AspectRatioWithStyles.propTypes = types;
AspectRatioWithStyles.defaultProps = defaults;

export default AspectRatioWithStyles;

//Only used for the Storybook Props Table
export const WithProps = ({ children, ...props }) => (
  <AspectRatioWithStyles {...props}>{children}</AspectRatioWithStyles>
);

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
