import React from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
import { Icon } from "../../atoms";

const ArrowButton = ({ className, arrowRight, onClick, theme }) => {
  return (
    <button
      onClick={onClick}
      className={`${className} ${arrowRight ? "arrow right" : "arrow left"}`}
    >
      <Icon
        name={arrowRight ? theme.icons.chevron_right : theme.icons.chevron_left}
        size="small"
        color={theme.components.scrollableTabs.button.iconColor}
      />
    </button>
  );
};

ArrowButton.defaultProps = {
  arrowRight: false
};

ArrowButton.propTypes = {
  className: PropTypes.string,
  arrowRight: PropTypes.bool
};

const ArrowButtonWithStyles = styled(ArrowButton)`
  background-color: ${props =>
    props.theme.components.scrollableTabs.button.bgColor};
  display: ${props => props.display || "none"};
  position: absolute;
  border: none;
  height: 100%;
  width: 40px;
  cursor: pointer;
  top: 0;
  z-index: 1;
  &.right {
    right: -15px;
  }
  &.left {
    left: -15px;
  }
`;

export default withTheme(ArrowButtonWithStyles);
