import React, { Fragment, memo } from "react";
import PropTypes from "prop-types";
//components
import { Text, Line, Section, ContentContainer } from "../../atoms";
import { InjectHtml } from "../../molecules";
import { withSize } from "react-sizeme";
import { compose } from "../../../utils/utilities";
import { withTheme } from "styled-components";
//Context
import { useConfig } from "../../../context/ConfigContext";

const CurricularUnit = memo(function CurricularUnit({
  hasLine,
  loading,
  hasError,
  data,
  lang,
  hasHash,
  size,
  theme
}) {
  const { link: Link } = useConfig();
  const LeftColumn = () => {
    return (
      <div className="col-xl-6 offset-xl-3 col-lg-8 offset-lg-4 col-12 offset-0 order-xl-first order-last">
        {hasLine && <Line color={theme.components.curricularUnit.lineColor} />}

        {Object.keys(data.description).length > 0 &&
          data.description.map((item, index) => {
            return (
              <ContentContainer key={index} marginTop="40px">
                <Text
                  as="h2"
                  size="large"
                  text={item.label}
                  color={theme.components.curricularUnit.titleColor}
                  fontWeight="400"
                  marginBottom="5px"
                />
                <InjectHtml content={item.value} variant="presentation" />
              </ContentContainer>
            );
          })}

        {Object.keys(data.contents).length > 0 && (
          <ContentContainer marginTop="40px">
            <Text
              as="h2"
              size="large"
              text={lang === "pt" ? "Conteúdos" : "Contents"}
              color={theme.components.curricularUnit.titleColor}
              fontWeight="400"
              marginBottom="5px"
            />

            {data.contents.map((item, index) => {
              return (
                <ContentContainer key={index} marginTop="20px">
                  <Text
                    as="h2"
                    size="large"
                    text={item.label}
                    color="#000"
                    fontWeight="500"
                    marginBottom="5px"
                  />
                  <InjectHtml content={item.value} variant="presentation" />
                </ContentContainer>
              );
            })}
          </ContentContainer>
        )}

        {Object.keys(data.links).length > 0 &&
          data.links.map((item, index) => {
            return (
              <ContentContainer key={index} marginTop="40px">
                <Text
                  as="h2"
                  size="large"
                  text={item.label}
                  color={theme.components.curricularUnit.titleColor}
                  fontWeight="400"
                  marginBottom="5px"
                />
                <InjectHtml content={item.value} variant="presentation" />
              </ContentContainer>
            );
          })}
      </div>
    );
  };

  const RightColumn = () => {
    return (
      <Fragment>
        {Object.keys(data.sideInfo).length > 0 && (
          <div className="col-xl offset-xl-0 col-lg-4 offset-lg-4 col-md-6 order-xl-last order-first">
            <ContentContainer
              marginTop="50px"
              marginLeft="30px"
              mobileLgMarginLeft="0"
              lgMargintop="40px"
            >
              <ContentContainer
                padding="5px 0"
                borderTop={`3px solid ${theme.components.curricularUnit.containerBorderColor}`}
                borderBottom={`3px solid ${theme.components.curricularUnit.containerBorderColor}`}
              >
                {data.sideInfo.map((item, index) => {
                  return (
                    <ContentContainer
                      key={index}
                      padding="5px 0"
                      borderBottom={`1px solid ${theme.components.curricularUnit.infoContainerBorderColor}`}
                      lastChildBorderBottom="none"
                    >
                      <div className="row no-gutters">
                        <div className="col">
                          <Text
                            as="h3"
                            size="small"
                            text={`${item.info.label}:`}
                            fontWeight="500"
                          />
                          {item.type === "link" ? (
                            <Link
                              text={item.info.value}
                              title={item.info.value}
                              color={theme.components.curricularUnit.linkColor}
                              fontWeight="300"
                              fontSize="14px"
                              textDecoration="underline"
                              hoverColor={
                                theme.components.curricularUnit.linkHoverColor
                              }
                              href={`${hasHash}/${lang}/p/${item.extra}`}
                            />
                          ) : (
                            <InjectHtml
                              content={item.info.value}
                              variant="presentation"
                            />
                          )}
                        </div>
                      </div>
                    </ContentContainer>
                  );
                })}
              </ContentContainer>
            </ContentContainer>
          </div>
        )}
      </Fragment>
    );
  };

  return (
    <Section
      backgroundColor={theme.components.curricularUnit.backgroundColor}
      hasHighContrastBorderTop={false}
    >
      <div className="container">
        <div className="row no-gutters">
          <div className="col-xl-7 offset-xl-3 col-lg-8 offset-lg-4 col-12 order-0">
            <ContentContainer mdMargintop="50px">
              <Text
                as="h1"
                size="xLarge"
                fontWeight="500"
                text={data.subTitle}
                lineHeight="1.4"
                firstLetter="capitalize"
                marginTop="0"
                marginBottom="0"
              />
            </ContentContainer>
          </div>
        </div>
        <div className="row no-gutters">
          {loading ? (
            <div>A carregar...</div>
          ) : hasError ? (
            <div>No content to display</div>
          ) : (
            <Fragment>
              {/*
              -- Change column order for design and screen reader
              -- Use grid order classes with size for better window resize
              */}
              {size.width >= 1199 ? (
                <Fragment>
                  <LeftColumn />
                  <RightColumn />
                </Fragment>
              ) : (
                <Fragment>
                  <RightColumn />
                  <LeftColumn />
                </Fragment>
              )}
            </Fragment>
          )}
        </div>
      </div>
    </Section>
  );
});

const defaults = {
  data: {},
  lang: "pt",
  hasHash: false
};

const types = {
  data: PropTypes.object.isRequired,
  hasLine: PropTypes.bool,
  loading: PropTypes.bool,
  hasError: PropTypes.bool,
  lang: PropTypes.string,
  hasHash: PropTypes.bool,
  size: PropTypes.object,
  theme: PropTypes.object
};

CurricularUnit.propTypes = types;
CurricularUnit.defaultProps = defaults;

export default compose(withTheme, withSize())(CurricularUnit);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => <CurricularUnit {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
