import React, { Fragment } from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import { Link } from "../../../atoms";

const BorderedCard = ({
  children,
  className,
  href,
  onKeyPress,
  cardIsLink,
  target,
  hasHover,
  ...props
}) => {
  return (
    <Fragment>
      {cardIsLink ? (
        <Link
          className={className}
          tabIndex={0}
          href={href}
          onKeyPress={onKeyPress}
          target={target}
          hasHover={hasHover}
          {...props}
        >
          <div className="content-container">{children}</div>
        </Link>
      ) : (
        <div className={className}>
          <div className="content-container">{children}</div>
        </div>
      )}
    </Fragment>
  );
};

BorderedCard.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  href: PropTypes.string,
  onKeyPress: PropTypes.func,
  history: PropTypes.object,
  cardIsLink: PropTypes.bool,
  target: PropTypes.string,
  hasHover: PropTypes.bool
};

BorderedCard.defaultProps = {
  href: "/",
  cardIsLink: true,
  hasHover: false
};

const BorderedCardWithStyles = styled(BorderedCard)`
  display: block;
  text-decoration: none;
  &:focus {
    ${props => props.theme.mixins.focus};
  }
  button {
    background-color: transparent;
  }
  .content-container {
    padding: ${props => props.contentContainerPadding || "10px 0"};
    margin-top: ${props => props.contentContainerMarginTop || "0"};
    border-top: 1px solid
      ${props =>
        props.borderColor || props.theme.components.borderedCard.borderColor};
  }
  &:last-child {
    .content-container {
      border-bottom: 1px solid
        ${props =>
          props.borderColor || props.theme.components.borderedCard.borderColor};
    }
  }
`;

export default BorderedCardWithStyles;

//Only used for the Storybook Props Table
export const WithProps = props => <BorderedCardWithStyles {...props} />;

WithProps.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  href: PropTypes.string,
  onKeyPress: PropTypes.func,
  history: PropTypes.object,
  cardIsLink: PropTypes.bool,
  target: PropTypes.string,
  hasHover: PropTypes.bool,
  contentContainerPadding: PropTypes.string,
  borderColor: PropTypes.string
};

WithProps.defaultProps = {
  href: "/",
  cardIsLink: true,
  hasHover: false
};
