import React from "react";
import styled, { withTheme } from "styled-components";
import PropTypes from "prop-types";
import { AnimatedBackground, Icon, ContentContainer } from "../../../atoms";

const Mask = props => {
  return (
    <ContentContainer
      backgroundColor={props.theme.loadings.maskBgColor}
      position="absolute"
      {...props}
    />
  );
};

const LoadingCard = ({ ...props }) => {
  return (
    <ContentContainer
      height="398px"
      backgroundColor="transparent"
      mobileMdBgColor={props.theme.loadings.cardBgColor}
    >
      <ContentContainer
        backgroundColor="transparent"
        height="200px"
        className="loading-image-container"
      >
        <div className="image">
          <Icon
            name="fas fa-image"
            color={props.theme.loadings.iconColor}
            fontSize="32px"
          />
        </div>
      </ContentContainer>
      <ContentContainer
        height="198px"
        padding="20px"
        className="content-container"
      >
        <AnimatedBackground height="150px">
          <Mask height="15px" right="0" width="75%" {...props} />
          <Mask height="20px" top="15px" width="100%" {...props} />
          <Mask
            height="18px"
            top="35px"
            right="0"
            mobileSmWidth="20%"
            mobileXsWidth="10%"
            {...props}
          />
          <Mask height="8px" top="53px" width="100%" {...props} />
          <Mask
            height="18px"
            top="61px"
            right="0"
            mobileSmWidth="20%"
            mobileXsWidth="10%"
            {...props}
          />
          <Mask height="8px" top="79px" width="100%" {...props} />
          <Mask height="18px" top="87px" right="0" width="60%" {...props} />
          <Mask height="30px" top="105px" width="100%" {...props} />
          <Mask height="15px" top="135px" right="0" width="70%" {...props} />
        </AnimatedBackground>
      </ContentContainer>
    </ContentContainer>
  );
};

const Loading = ({ className, ...props }) => {
  return (
    <div className={className}>
      <div className="container">
        <div className="row">
          <div className="col-xl col-lg col-md-6 col-sm-12">
            <LoadingCard {...props} />
          </div>
          <div className="col-xl col-lg col-md-6 hide-sm">
            <LoadingCard {...props} />
          </div>
          <div className="col-xl col-lg hide-md">
            <LoadingCard {...props} />
          </div>
        </div>
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string
};

const LoadingWithStyles = styled(Loading)`
  .image {
    display: flex;
    justify-content: center;
    align-items: center;
    height: inherit;
    visibility: hidden;
  }
  // Medium devices (tablets, 768px and up)
  @media (max-width: 767px) {
    .hide-sm {
      display: none;
    }
  }

  // Large devices (desktops, 992px and up)
  @media (max-width: 991px) {
    .image {
      visibility: visible;
    }
    .loading-image-container {
      background-color: ${props => props.theme.loadings.iconContainerBgColor};
    }
    .hide-md {
      display: none;
    }
  }
`;

export default withTheme(LoadingWithStyles);
