import React, { Fragment } from "react";
import PropTypes from "prop-types";
import { Image, Text, Link, ContentContainer } from "../../../atoms";
import { withTheme } from "styled-components";
import { compose } from "../../../../utils/utilities";

const ImageCard = ({
  title,
  name,
  email,
  src,
  url,
  hasPhoto,
  theme,
  onClick
}) => {
  return (
    <Fragment>
      <ContentContainer marginBottom="10px">
        {hasPhoto && (
          <ContentContainer
            overflow="hidden"
            maxHeight="150px"
            minHeight="50px"
            marginBottom="10px"
            backgroundColor={theme.components.picture.backgroundColor}
          >
            <Image width="100%" height="100%" src={src} onClick={onClick} />
          </ContentContainer>
        )}
        <Text
          text={title}
          fontWeight="500"
          marginBottom="10px"
          size="small"
          lineHeight="1"
        />
      </ContentContainer>

      {url === null ? (
        <Text text={name} fontWeight="300" fontSize="14px" />
      ) : (
        <Link
          text={name}
          fontWeight="300"
          fontSize="14px"
          textDecoration="underline"
          color={theme.components.imageCard.link.color}
          hoverColor={theme.components.imageCard.link.hoverColor}
          href={`${url}`}
          title={"ver perfil"}
          default
        />
      )}

      <ContentContainer marginBottom="5px" marginTop="5px">
        <Link
          text={email}
          title={email}
          fontWeight="300"
          fontSize="14px"
          textDecoration="underline"
          color={theme.components.imageCard.link.color}
          hoverColor={theme.components.imageCard.link.hoverColor}
          href={`mailto:${email}`}
          target="_top"
          default
          native
        />
      </ContentContainer>
    </Fragment>
  );
};

ImageCard.defaultProps = {
  hasPhoto: false,
  title: "Título",
  name: "Nome",
  email: "Email"
};

ImageCard.propTypes = {
  title: PropTypes.string,
  name: PropTypes.string,
  email: PropTypes.string,
  src: PropTypes.string,
  url: PropTypes.string.isRequired,
  hasPhoto: PropTypes.bool,
  theme: PropTypes.object,
  onClick: PropTypes.func
};

export default compose(withTheme)(ImageCard);
