import React, { Fragment } from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//Components
import { ScreenReaderText, Text, Section } from "../../../atoms";
import {
  SectionHeader,
  NewsCard,
  MiniHorizontalNewsCard,
  Error
} from "../../../molecules";
import News from "../../News";
import { withSize } from "react-sizeme";
import moment from "moment";
import { compose } from "../../../../utils/utilities";
//Context
import { useConfig } from "../../../../context/ConfigContext";

const JournalNews = ({
  className,
  backgroundColor,
  titleProps,
  size,
  sectionTitle,
  smPaddingTop,
  loading,
  hasError,
  data,
  theme,
  arrowColor,
  ...props
}) => {
  const { formattedMessage: FormattedMessage } = useConfig();
  const newsProps = {
    backgroundColor:
      backgroundColor || theme.components.journalNews.backgroundColor,
    titleProps,
    textColor: theme.components.journalNews.titleColor,
    size,
    ...props
  };

  return size.width <= 767 ? (
    <Section
      className={className}
      backgroundColor={newsProps.backgroundColor}
      smPaddingTop={smPaddingTop}
    >
      <ScreenReaderText>
        <Text as="h2" text="Notícias" />
      </ScreenReaderText>
      <SectionHeader
        href={props.categoryUrl}
        textColor={theme.components.journalNews.titleColor}
        {...titleProps}
      >
        {props.resource ? (
          <FormattedMessage id={props.resource} defaultMessage={sectionTitle} />
        ) : (
          sectionTitle
        )}
      </SectionHeader>
      <div className="container">
        {loading ? (
          <div>A carregar...</div>
        ) : hasError ? (
          <Error />
        ) : (
          <div className="row">
            {data.listNews.length > 0 && (
              <Fragment>
                <div className="col-12">
                  {data.listNews
                    .filter((_, index) => index === 0)
                    .map(item => {
                      return (
                        <NewsCard
                          key={item.id}
                          data={item}
                          cardBgColor="#fff"
                          hasColAuto={false}
                          hasShareIcon={true}
                          hasCategoryTitle={
                            props.categoryId !== item.categoryID
                          }
                          mobileCardBgColor={
                            theme.components.journalNews.mobileNewsCardBgColor
                          }
                          onClick={props.onAction}
                          onKeyPress={props.onEnter}
                          size={size}
                          history={props.history}
                        />
                      );
                    })}
                </div>
                <div className="col-12">
                  <div className="mini-cards">
                    {data.listNews
                      .filter((_, index) => index >= 1 && index <= 7)
                      .map(item => {
                        return (
                          <MiniHorizontalNewsCard
                            key={item.id}
                            src={
                              item.imageCover !== null && item.imageCover.url
                            }
                            alt={
                              item.imageCover.alt !== undefined
                                ? item.imageCover.alt
                                : ""
                            }
                            category={item.category}
                            title={item.title}
                            date={moment(data.startDate).format("DD MMMM YYYY")}
                            href={`${props.hasHash}${item.url}`}
                            onClick={props.onAction}
                            onKeyPress={props.onEnter}
                            history={props.history}
                          />
                        );
                      })}
                  </div>
                </div>
              </Fragment>
            )}
          </div>
        )}
      </div>
    </Section>
  ) : (
    <News
      title={sectionTitle}
      data={data}
      loading={loading}
      hasError={hasError}
      showHeader
      arrowColor={theme.highContrast.action || arrowColor}
      {...newsProps}
    />
  );
};

const types = {
  title: PropTypes.string,
  sectionTitle: PropTypes.string,
  smPaddingTop: PropTypes.string,
  className: PropTypes.string,
  backgroundColor: PropTypes.string,
  titleProps: PropTypes.object,
  size: PropTypes.object,
  hasError: PropTypes.bool,
  loading: PropTypes.bool,
  categoryUrl: PropTypes.string,
  categoryId: PropTypes.number,
  onAction: PropTypes.func,
  onEnter: PropTypes.func,
  data: PropTypes.object,
  history: PropTypes.object,
  hasHash: PropTypes.string,
  resource: PropTypes.string,
  arrowColor: PropTypes.string,
  theme: PropTypes.object
};

JournalNews.propTypes = types;

const NewsWithStyles = styled(JournalNews)`
  .mini-cards {
    margin-top: 20px;
  }
  // Small devices (landscape phones, less than 768px)
  @media (max-width: 767px) {
    background-color: ${props =>
      props.theme.components.journalNews.mobileBackgroundColor};
    span.news-plus-button {
      background-color: ${props =>
        props.theme.components.journalNews.mobileNewsCardBgColor};
    }
  }
`;

export default compose(withSize(), withTheme)(NewsWithStyles);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => <NewsWithStyles {...props} />;

WithProps.propTypes = types;
