import React from "react";
import PropTypes from "prop-types";
import ReactPlayer from "react-player";
//Styles
import styled, { withTheme } from "styled-components";
//Components
import { Section, Text, ContentContainer } from "../../atoms/index";
import { SectionHeader, VideoPlayer } from "../../molecules/index";
//
const IframeContainer = styled.div`
  overflow: hidden;
  // 16:9 aspect ratio
  padding-top: 56.25%;
  position: relative;
  iframe {
    width: 100%;
    height: 100%;
    border: none;
    left: 0;
    position: absolute;
    top: 0;
  }
`;

const Streaming = ({ className, theme, title, description, url }) => {
  const textColor = theme.components.streaming.textColor;
  return (
    <Section
      className={className}
      backgroundColor={theme.components.streaming.backgroundColor}
    >
      <div className="container">
        <ContentContainer mobileXsPadding="0 15px">
          <SectionHeader
            textColor={textColor}
            hasLink={false}
            hasLine={false}
            hasContainer={false}
            marginBottom="50px"
          >
            {title}
          </SectionHeader>
        </ContentContainer>
        <div className="row">
          <div className="col-md-8 col-12">
            {ReactPlayer.canPlay(url) ? (
              <VideoPlayer className={className} url={url}></VideoPlayer>
            ) : (
              <IframeContainer>
                <iframe src={url} title={title}>
                  &nbsp;
                </iframe>
              </IframeContainer>
            )}
            <div></div>
          </div>
          <div className="col">
            <ContentContainer mobileXsPadding="0 15px" smMargintop="20px">
              <Text color={textColor} whiteSpace={"pre-line"}>
                {description}
              </Text>
            </ContentContainer>
          </div>
        </div>
      </div>
    </Section>
  );
};

const defaults = {
  title: "Title",
  description: "Text",
  url: ""
};

const types = {
  className: PropTypes.string,
  theme: PropTypes.object,
  title: PropTypes.string,
  description: PropTypes.string,
  url: PropTypes.string
};

Streaming.propTypes = types;
Streaming.defaultProps = defaults;

const StreamingWithStyles = styled(Streaming)`
  @media (max-width: 575.98px) {
    .container {
      padding-right: 0;
      padding-left: 0;
    }
  }
`;

export default withTheme(StreamingWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <Streaming {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
