import React from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
//Components
import { Text, ImageContainer } from "../../../atoms";
import ImageWrapper from "../../../organisms/ImageWrapper";

const StudentCard = ({
  className,
  text,
  src,
  alt,
  imageBgColor,
  name,
  about,
  onFocus,
  onKeyDown,
  theme,
  hasColAuto
}) => {
  return (
    <div className={className} onFocus={onFocus} onKeyDown={onKeyDown}>
      <div className={`${hasColAuto ? "col-auto" : ""}`}>
        <div className="student-card" tabIndex={0}>
          <div className="text-container">
            <div className="content-container">
              <Text text={text} size="article" lineHeight="1.6" />
            </div>
          </div>
          <div className="row no-gutters">
            <div className="col-auto">
              <ImageContainer
                height="120px"
                width="120px"
                backgroundColor={
                  imageBgColor || theme.components.picture.backgroundColor
                }
                className="student-card__image"
              >
                <ImageWrapper
                  absoluteSize="120"
                  backgroundColor={
                    imageBgColor || theme.components.picture.backgroundColor
                  }
                  data={{ url: src }}
                  alt={alt}
                  height="100%"
                  width="100%"
                  endpoint={() => null}
                  hasEndpoint={false}
                />
              </ImageContainer>
            </div>
            <div className="col">
              <div className="name-container">
                <Text text={name} size="mediumSmall" fontWeight="500" />
                <Text text={about} size="small" />
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

const types = {
  className: PropTypes.string,
  text: PropTypes.string,
  name: PropTypes.string,
  about: PropTypes.string,
  src: PropTypes.string,
  alt: PropTypes.string,
  imageBgColor: PropTypes.string,
  onFocus: PropTypes.func,
  onKeyDown: PropTypes.func,
  theme: PropTypes.object,
  hasColAuto: PropTypes.bool
};

const defaults = {
  text: "Texto",
  name: "Nome",
  about: "Aluno",
  onFocus: () => null,
  onKeyDown: () => null,
  hasColAuto: true
};

StudentCard.propTypes = types;
StudentCard.defaultProps = defaults;

const StudentCardWithStyles = styled(StudentCard)`
  .student-card {
    background-color: ${props => props.backgroundColor};
    user-select: text;
    &__image {
      div:first-child {
        height: inherit;
      }
    }

    &:focus {
      ${props => props.theme.mixins.focus};
    }

    .text-container {
      padding: 20px;
      border: 1px solid
        ${props => props.theme.components.studentCard.borderColor};
      height: 192px;
      .content-container {
        max-height: 150px;
        overflow: hidden;
      }
    }

    .name-container {
      padding: 20px;
    }
  }
`;

export default withTheme(StudentCardWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <StudentCardWithStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
