import React from "react";
import { AnimatedBackground, ContentContainer } from "../../../atoms";
import { withTheme } from "styled-components";

const Mask = props => {
  return (
    <ContentContainer
      className="loading-mask"
      backgroundColor={props.theme.loadings.maskBgColor}
      position="absolute"
      {...props}
    />
  );
};

const Loading = ({ ...props }) => {
  return (
    <ContentContainer
      className="loading-card"
      height="398px"
      backgroundColor={props.theme.loadings.cardBgColor}
    >
      <ContentContainer
        className="d-flex"
        backgroundColor={props.theme.loadings.iconContainerBgColor}
        height="200px"
      />
      <ContentContainer height="198px" padding="20px">
        <AnimatedBackground height="150px">
          <Mask height="15px" right="0" width="75%" {...props} />
          <Mask height="20px" top="15px" width="100%" {...props} />
          <Mask
            height="18px"
            top="35px"
            right="0"
            mobileSmWidth="20%"
            mobileXsWidth="10%"
            {...props}
          />
          <Mask height="8px" top="53px" width="100%" {...props} />
          <Mask
            height="18px"
            top="61px"
            right="0"
            mobileSmWidth="20%"
            mobileXsWidth="10%"
            {...props}
          />
          <Mask height="8px" top="79px" width="100%" {...props} />
          <Mask height="18px" top="87px" right="0" width="60%" {...props} />
          <Mask height="30px" top="105px" width="100%" {...props} />
          <Mask height="15px" top="135px" right="0" width="70%" {...props} />
        </AnimatedBackground>
      </ContentContainer>
    </ContentContainer>
  );
};

export default withTheme(Loading);
