import React from "react";
import PropTypes from "prop-types";
//Styles
import styled from "styled-components";
//theme
import theme from "../../../styles/theme.semantic";

const Line = styled.div`
  transition: width 0.2s;
  height: ${props => props.height};
  background-color: ${props => props.color};
  margin-top: ${props => props.marginTop};
  margin-bottom: ${props => props.marginBottom};
  width: ${props =>
    props.isHover
      ? props.theme.components.line.width +
        props.theme.components.line.widthDifference
      : props.theme.components.line.width -
        props.theme.components.line.widthDifference}px;
`;

const defaults = {
  width: theme.components.line.width + "px",
  height: theme.components.line.height + "px",
  color: theme.components.line.color,
  marginTop: theme.components.line.marginTop + "px",
  marginBottom: "0",
  isHover: false
};

const props = {
  width: PropTypes.string,
  height: PropTypes.string,
  color: PropTypes.string,
  marginTop: PropTypes.string,
  marginBottom: PropTypes.string,
  children: PropTypes.node,
  isHover: PropTypes.bool
};

Line.defaultProps = defaults;
Line.propTypes = props;

export default Line;

//Only used for the Storybook Props Table
export const WithProps = ({ children, ...props }) => (
  <Line {...props}>{children}</Line>
);

WithProps.defaultProps = defaults;
WithProps.propTypes = props;
