import React, { useState, useEffect } from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
import { Icon, ClickAwayListener } from "../../atoms";
import ReactCalendar from "../ReactCalendar";
import moment from "moment";

const DatePicker = ({
  className,
  value,
  theme,
  calendarPrimaryColor,
  placeholder,
  onChange
}) => {
  const [isOpen, setOpen] = useState(false);
  const [inputValue, setInputValue] = useState(value);

  useEffect(() => {
    setInputValue(value);
  }, [value]);

  const onHandleChange = value => {
    const date = moment(value).format("DD-MM-YYYY");
    setInputValue(date);
    onChange(date);
    setOpen(false);
  };

  const onClear = () => {
    setInputValue("");
    onChange(null);
  };

  return (
    <ClickAwayListener onChange={() => setOpen(false)}>
      <div className={className}>
        <input
          type="text"
          value={inputValue}
          placeholder={placeholder || "dd/mm/aaaa"}
          onFocus={() => setOpen(true)}
          readOnly
        />
        {inputValue && (
          <div className="icon-container clear" onClick={onClear}>
            <Icon
              name={theme.icons.fas_times}
              color={theme.components.datePicker.iconColor}
              size="xSmall"
              aria-label="Abrir calendário"
            />
          </div>
        )}
        <div className="icon-container">
          <Icon
            name={theme.icons.calendar}
            color={theme.components.datePicker.iconColor}
            size="xSmall"
            aria-label="Abrir calendário"
          />
        </div>
        {isOpen && (
          <ReactCalendar
            top="45px"
            primaryColor={calendarPrimaryColor}
            onChange={onHandleChange}
          />
        )}
      </div>
    </ClickAwayListener>
  );
};

const types = {
  className: PropTypes.string,
  value: PropTypes.string,
  theme: PropTypes.object,
  calendarPrimaryColor: PropTypes.string,
  placeholder: PropTypes.string,
  onChange: PropTypes.func,
  showClear: PropTypes.bool
};

const defaults = {
  isOpen: false
};

DatePicker.propTypes = types;
DatePicker.defaultProps = defaults;

const DatePickerWithStyles = styled(DatePicker)`
  position: relative;
  height: 38px;
  box-sizing: border-box;
  background-color: ${props =>
    props.theme.components.datePicker.backgroundColor};
  cursor: pointer;

  &:hover {
    input {
      border: 1px solid
        ${props => props.theme.components.datePicker.hoverBorderColor};
    }
  }

  input {
    height: inherit;
    width: 100%;
    border: 1px solid ${props => props.theme.components.datePicker.borderColor};
    padding: ${props =>
      props.showClear ? "2px 63px 2px 10px" : "2px 40px 2px 10px"};

    line-height: 1.4;
    cursor: pointer;
    transition: all 100ms;
    background-color: transparent;
    font-weight: ${props => props.theme.weight.xbase};
    font-size: ${props => props.theme.sizes.s};

    &:hover {
      border: 1px solid
        ${props => props.theme.components.datePicker.hoverBorderColor};
    }

    &:focus {
      outline: none;
      border: 1px solid
        ${props => props.theme.components.datePicker.borderColor};
      box-shadow: 0 0 0 1px
        ${props => props.theme.components.datePicker.shadowColor};
    }

    &::placeholder {
      color: ${props => props.theme.components.datePicker.textColor};
    }
  }

  .icon-container {
    position: absolute;
    width: 30px;
    top: 1px;
    bottom: 1px;
    right: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    pointer-events: none;

    &:before {
      content: "";
      position: absolute;
      background-color: ${props =>
        props.theme.components.datePicker.separatorColor};
      width: 1px;
      top: 8px;
      bottom: 8px;
      left: -1px;
    }

    &.clear {
      pointer-events: all;
      right: 30px;
      width: 36px;

      &:hover {
        i {
          color: ${props =>
            props.theme.components.datePicker.clearIconHoverColor};
        }
      }

      &:before {
        content: initial;
      }
    }
  }
`;

export default withTheme(DatePickerWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <DatePickerWithStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
