import React from "react";
import PropTypes from "prop-types";
import { ContentContainer } from "../../../atoms";
import { BackgroundImageNewsCardLoading } from "../../../molecules";

const Loading = ({ className }) => {
  return (
    <div className={className}>
      <div className="row">
        <div className="col-xl col-lg col-md col-12">
          <BackgroundImageNewsCardLoading />
        </div>
        <div className="col-xl col-lg col-md col-12">
          <BackgroundImageNewsCardLoading />
        </div>
      </div>
      <ContentContainer marginTop="30px">
        <div className="row">
          <div className="col-xl col-lg col-md col-12">
            <BackgroundImageNewsCardLoading />
          </div>
          <div className="col-xl col-lg col-md col-12">
            <BackgroundImageNewsCardLoading />
          </div>
        </div>
      </ContentContainer>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string
};

export default Loading;
