import React from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
import { darken } from "polished";
//Components
import { Text, Picture, Link, ContentContainer } from "../../../atoms";
import SocialMediaActions from "../../SocialMediaActions";
import { compose } from "../../../../utils/utilities";

//ImageBottom
const ImageBottom = ({
  title,
  src,
  alt,
  name,
  textColor,
  theme,
  hasSubTitle,
  subTitle,
  titleIsSerif,
  url,
  size,
  share,
  linkProps
}) => {
  return (
    <Link
      className="person-card d-flex flex-column justify-content-between"
      tabIndex={0}
      href={url}
      hasHover={false}
      {...linkProps}
    >
      <ContentContainer
        minHeight={hasSubTitle ? "auto" : "200px"}
        //maxHeight="230px"
        //overflow="hidden"
        padding="20px"
      >
        <Text
          as="h3"
          size="xLarge"
          color={textColor || theme.components.personCard.textColor}
          isSerif={titleIsSerif}
        >
          {title}
        </Text>
        {hasSubTitle && (
          <Text
            marginTop="20px"
            marginBottom="20px"
            color={textColor || theme.components.personCard.textColor}
          >
            {subTitle}
          </Text>
        )}
      </ContentContainer>
      <ContentContainer paddingBottom="20px">
        <div className="row no-gutters">
          <div className="col-auto">
            <div className="image-container">
              <Picture
                src={src}
                alt={alt}
                height={theme.components.personCard.imageSize}
              />
            </div>
          </div>
          <div className="col">
            <ContentContainer
              height="100%"
              margin="0 20px"
              className="d-flex flex-column justify-content-between"
            >
              <div>
                <Text
                  size="xSmall"
                  fontWeight="300"
                  color={textColor || theme.components.personCard.textColor}
                >
                  {name}
                </Text>
              </div>
              <div className="d-flex justify-content-end">
                <SocialMediaActions
                  size={size}
                  iconColor={textColor || theme.components.personCard.textColor}
                  share={share}
                />
              </div>
            </ContentContainer>
          </div>
        </div>
      </ContentContainer>
    </Link>
  );
};

ImageBottom.defaultProps = {
  share: {
    facebook: `https://www.facebook.com/sharer/sharer.php?u=url`,
    twitter: `http://twitter.com/share?url=url&text=title`,
    linkedin: `https://www.linkedin.com/shareArticle?mini=true&url=url`,
    url: "/"
  },
  url: "/"
};

ImageBottom.propTypes = {
  title: PropTypes.string,
  name: PropTypes.string,
  src: PropTypes.string,
  textColor: PropTypes.string,
  subTitle: PropTypes.string,
  titleIsSerif: PropTypes.bool,
  theme: PropTypes.object,
  hasSubTitle: PropTypes.bool,
  url: PropTypes.string,
  size: PropTypes.object,
  share: PropTypes.object,
  alt: PropTypes.string,
  linkProps: PropTypes.object
};

//ImageTop
const ImageTop = ({
  title,
  src,
  name,
  textColor,
  theme,
  titleIsSerif,
  url,
  history
}) => {
  return (
    <div className="person-card" tabIndex={0} onClick={() => history.push(url)}>
      <div className="row no-gutters">
        <div className="col-auto">
          <div className="image-container">
            <Picture src={src} height={theme.components.personCard.imageSize} />
          </div>
        </div>
        <div className="col">
          <ContentContainer padding="20px">
            <ContentContainer marginBottom="15px">
              <Text
                as="h3"
                fontSize="16px"
                color={textColor || theme.components.personCard.textColor}
                isSerif={titleIsSerif}
              >
                {title}
              </Text>
            </ContentContainer>
            <Text
              size="xSmall"
              fontWeight="300"
              color={textColor || theme.components.personCard.textColor}
            >
              {name}
            </Text>
          </ContentContainer>
        </div>
      </div>
    </div>
  );
};

ImageTop.propTypes = {
  title: PropTypes.string,
  name: PropTypes.string,
  src: PropTypes.string,
  textColor: PropTypes.string,
  theme: PropTypes.object,
  titleIsSerif: PropTypes.bool,
  url: PropTypes.string,
  history: PropTypes.object,
  linkProps: PropTypes.object
};

const PersonCard = ({ className, imageTop, size, ...props }) => {
  const personProps = {
    size,
    ...props
  };
  return (
    <div className={className}>
      {imageTop ? <ImageTop {...props} /> : <ImageBottom {...personProps} />}
    </div>
  );
};

PersonCard.propTypes = {
  className: PropTypes.string,
  title: PropTypes.string,
  name: PropTypes.string,
  text: PropTypes.string,
  src: PropTypes.string,
  imageTop: PropTypes.bool,
  size: PropTypes.object
};

PersonCard.defaultProps = {
  title: "Título",
  name: "Nome",
  text: "Texto",
  subTitle: "texto",
  src: "",
  imageTop: false
};

const PersonCardWithStyles = styled(PersonCard)`
  height: 100%;
  .person-card {
    height: inherit;
    min-height: ${props => props.minHeight};
    text-decoration: none;
    button {
      background-color: transparent;
      border: none;
      cursor: pointer;
      &:hover {
        i {
          color: ${darken(0.2, "#fff")};
        }
      }
    }
    background-color: ${props =>
      props.backgroundColor ||
      props.theme.components.personCard.backgroundColor};
    .image-container {
      background-color: ${props =>
        props.theme.components.personCard.imageBackgroundColor};
      width: ${props => props.theme.components.personCard.imageSize};
      overflow: hidden;
    }

    img {
      -webkit-transition: all 0.4s;
      -moz-transition: all 0.4s;
      -o-transition: all 0.4s;
      transition: all 0.4s;
    }

    &:hover {
      img {
        -ms-transform: scale(1.1);
        -moz-transform: scale(1.1);
        -webkit-transform: scale(1.1);
        -o-transform: scale(1.1);
        transform: scale(1.1);
      }
    }
    &:focus {
      ${props => props.theme.mixins.focus};
    }
  }
`;

export default compose(withTheme)(PersonCardWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <PersonCardWithStyles {...props} />;

WithProps.propTypes = {
  title: PropTypes.string,
  name: PropTypes.string,
  src: PropTypes.string,
  textColor: PropTypes.string,
  subTitle: PropTypes.string,
  titleIsSerif: PropTypes.bool,
  theme: PropTypes.object,
  hasSubTitle: PropTypes.bool,
  url: PropTypes.string,
  size: PropTypes.object,
  share: PropTypes.object,
  alt: PropTypes.string,
  history: PropTypes.object,
  linkProps: PropTypes.object,
  className: PropTypes.string,
  text: PropTypes.string,
  imageTop: PropTypes.bool,
  minHeight: PropTypes.string,
  backgroundColor: PropTypes.string
};

WithProps.defaultProps = {
  share: {
    facebook: `https://www.facebook.com/sharer/sharer.php?u=url`,
    twitter: `http://twitter.com/share?url=url&text=title`,
    linkedin: `https://www.linkedin.com/shareArticle?mini=true&url=url`,
    url: "/"
  },
  url: "/",
  title: "Título",
  name: "Nome",
  text: "Texto",
  subTitle: "texto",
  src: "",
  imageTop: false
};
