import React, { memo, Fragment } from "react";
import PropTypes from "prop-types";
//Components
import { Line, Text, Section, ContentContainer } from "../../atoms";
import { MarkdownRenderer } from "../../molecules";
import styled, { withTheme } from "styled-components";

const MarkdownContainer = styled.div`
  margin-top: 40px;
  img {
    max-height: 620px;
  }
`;

const Article = memo(function Presentation({
  className,
  hasSubTitle,
  hasLine,
  hasBottomSpacing,
  data,
  showLangMessage,
  lang,
  activeColor,
  gridClasses,
  hasContent,
  hasTitleDefault,
  defaultContent,
  hasTitle,
  theme,
  isMobile
}) {
  return (
    <Fragment>
      <Section
        autoFocus
        className={className}
        backgroundColor={theme.components.article.backgroundColor}
        hasBottomSpacing={hasBottomSpacing}
        hasHighContrastBorderTop={false}
      >
        <div className="container">
          <div className="row no-gutters">
            <div className={gridClasses}>
              {hasSubTitle && (
                <Text
                  as="h2"
                  size="small"
                  text={data.subtitle}
                  firstLetter="capitalize"
                  fontSize="14px"
                  fontWeight="400"
                />
              )}

              {!hasContent && showLangMessage && (
                <ContentContainer marginBottom="40px">
                  <Text color={activeColor}>
                    We are working in the translation of this content to
                    {lang === "pt" ? " Portuguese" : " English"}. The original
                    version of the content is presented below.
                  </Text>
                </ContentContainer>
              )}
              {(hasTitle || hasTitleDefault) && (
                <Text
                  as="h1"
                  fontWeight="500"
                  text={!hasTitle ? defaultContent.title : data.title}
                  size="xLarge"
                  firstLetter="capitalize"
                />
              )}

              {hasLine && <Line color="#000" marginBottom="25px" />}

              <MarkdownContainer>
                <MarkdownRenderer
                  content={!hasContent ? defaultContent.content : data.content}
                  activeColor={activeColor}
                  isMobile={isMobile}
                />
              </MarkdownContainer>
            </div>
          </div>
        </div>
      </Section>
    </Fragment>
  );
});

const defaults = {
  hasSubTitle: false,
  hasLine: false,
  data: {
    title: null,
    activeColor: null
  },
  hasBottomSpacing: true,
  showLangMessage: false,
  lang: "en"
};

const types = {
  data: PropTypes.shape({
    title: PropTypes.string,
    subtitle: PropTypes.string,
    activeColor: PropTypes.string,
    content: PropTypes.string
  }).isRequired,
  className: PropTypes.string,
  hasSubTitle: PropTypes.bool,
  hasLine: PropTypes.bool,
  hasBottomSpacing: PropTypes.bool,
  showLangMessage: PropTypes.bool,
  gridClasses: PropTypes.string,
  hasContent: PropTypes.bool,
  hasTitleDefault: PropTypes.bool,
  defaultContent: PropTypes.object,
  hasTitle: PropTypes.bool,
  theme: PropTypes.object,
  isMobile: PropTypes.bool,
  activeColor: PropTypes.string,
  lang: PropTypes.string
};

Article.propTypes = types;
Article.defaultProps = defaults;

export default withTheme(Article);

//Only used for the Storybook Props Table
export const WithProps = props => <Article {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
