import React from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../atoms";
import { withTheme } from "styled-components";

const Mask = props => {
  return (
    <ContentContainer
      backgroundColor={props.theme.loadings.bgColor}
      position="absolute"
      {...props}
    />
  );
};

const LoadingCard = props => {
  return (
    <AnimatedBackground height="170px">
      <Mask {...props} height="15px" right="0" width="85%" />
      <Mask {...props} height="6px" width="100%" top="15px" />
      <Mask
        {...props}
        height="6px"
        width="100%"
        top="21px"
        borderTop={`1px solid ${props.theme.loadings.lightBorderColor}`}
      />
      <Mask {...props} height="15px" right="0" top="27px" width="40%" />
      <Mask {...props} height="6px" width="100%" top="42px" />
      <Mask {...props} height="15px" right="0" top="48px" width="50%" />
      <Mask {...props} height="6px" width="100%" top="63px" />
      <Mask
        {...props}
        height="6px"
        width="100%"
        top="69px"
        borderTop={`1px solid ${props.theme.loadings.lightBorderColor}`}
      />
      <Mask {...props} height="15px" right="0" top="75px" width="50%" />
      <Mask {...props} height="6px" width="100%" top="90px" />
      <Mask
        {...props}
        height="6px"
        width="100%"
        top="96px"
        borderTop={`1px solid ${props.theme.loadings.lightBorderColor}`}
      />
      <Mask {...props} height="15px" right="0" top="102px" width="60%" />
      <Mask {...props} height="6px" width="100%" top="117px" />
      <Mask {...props} height="15px" right="0" top="123px" width="60%" />
      <Mask {...props} height="6px" width="100%" top="137px" />
      <Mask
        {...props}
        height="6px"
        width="100%"
        top="143px"
        borderTop={`1px solid ${props.theme.loadings.lightBorderColor}`}
      />
      <Mask {...props} height="15px" right="0" top="149px" width="60%" />
      <Mask
        {...props}
        height="6px"
        width="100%"
        top="164px"
        borderBottom={`1px solid ${props.theme.loadings.lightBorderColor}`}
      />
    </AnimatedBackground>
  );
};

const Loading = ({ className, ...props }) => {
  return (
    <div className={className}>
      <ContentContainer marginTop="40px">
        <LoadingCard {...props} />
      </ContentContainer>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  backgroundColor: PropTypes.string
};

export default withTheme(Loading);
