import React, { useEffect } from "react";
import PropTypes from "prop-types";
//components
import {
  Text,
  Icon,
  Button,
  ContentContainer,
  ClickAwayListener,
  Section
} from "../../atoms";
import {
  PlusButton,
  Select,
  AgendaCard,
  AgendaFeatured,
  ReactCalendar
} from "../../molecules";
import styled, { withTheme } from "styled-components";
import Loading from "./Loading";
//Utils
import moment from "moment";
//Context
import { useConfig } from "../../../context/ConfigContext";

const Events = ({
  className,
  isLoading,
  content,
  hasError,
  range,
  categories,
  channels,
  openCalendar,
  onChangeDate,
  onResetDate,
  onChangeCategory,
  onChangeChannel,
  onLoadMore,
  currentDate,
  setOpen,
  isOpen,
  isAccessibleFocus,
  theme
}) => {
  const { formattedMessage: FormattedMessage, useIntl } = useConfig();
  const intl = useIntl();
  let agendaCardRefs = [];

  useEffect(() => {
    if (isAccessibleFocus) {
      agendaCardRefs[0] && agendaCardRefs[0].focus();
    }
  }, [isAccessibleFocus, agendaCardRefs]);

  const onRenderList = () => {
    if (Object.keys(content.listEvents).length === 0) {
      return (
        <div>
          <FormattedMessage
            id="app.events.empty"
            defaultMessage="Sem Resultados de eventos"
          />
        </div>
      );
    }

    return (
      <div className="agenda-cards">
        {content.listEvents.map((item, index) => {
          const isLimitHour =
            moment(item.startDate).format("LT") === "00:00" ||
            moment(item.endDate).format("LT") === "23:59";
          const isSameDay = moment(item.startDate).isSame(item.endDate, "day");

          const agendaCard = theme.components.events.agendaCard;
          return (
            <AgendaCard
              key={index}
              href={item.url}
              outlineColor={agendaCard.outlineColor}
              buttonOutlineColorHover={agendaCard.buttonOutlineColorHover}
              iconColorHover={agendaCard.iconColorHover}
              iconColor={agendaCard.iconColor}
              category={item.category}
              categoryColor={agendaCard.categoryColor}
              title={item.title}
              titleColor={agendaCard.titleColor}
              text={item.local}
              textColor={agendaCard.textColor}
              date={
                isSameDay
                  ? `${moment(item.startDate).format("DD MMMM YYYY")}`
                  : `${moment(item.startDate).format("DD MMMM")} - ${moment(
                      item.endDate
                    ).format("DD MMMM YYYY")}`
              }
              dateColor={agendaCard.dateColor}
              squareColorHover={agendaCard.squareColorHover}
              textColorHover={agendaCard.textColorHover}
              borderColor={agendaCard.borderColor}
              agendaCardRef={ref => (agendaCardRefs[index] = ref)}
            >
              <div className="no-slider">
                <Text
                  size="small"
                  text={`${
                    isLimitHour ? "Todo" : moment(item.startDate).format("LT")
                  } `}
                  fontWeight="400"
                />
                <Text
                  size="small"
                  text={`${
                    isLimitHour ? "o dia" : moment(item.endDate).format("LT")
                  } `}
                  color={
                    isLimitHour
                      ? agendaCard.isLimitHour
                      : agendaCard.isNotLimitHour
                  }
                  fontWeight={isLimitHour ? "400" : "300"}
                />
              </div>
            </AgendaCard>
          );
        })}
      </div>
    );
  };

  return (
    <Section
      className={className}
      backgroundColor={theme.components.events.backgroundColor}
      hasHighContrastBorderTop={false}
    >
      <div className="container">
        <ContentContainer marginBottom="50px">
          <div className="row">
            <div className="col-auto align-self-center">
              <Text size="xLarge" fontWeight="500" as="h2">
                <FormattedMessage
                  id="app.nav.public.social.agenda"
                  defaultMessage="Agenda"
                />
              </Text>
            </div>
            <div className="col-sm col-12">
              <div className="row justify-content-end">
                <div className="col-xl-2 col-lg-3 col-md-4 col-sm-5">
                  <ContentContainer
                    mobileXsMarginTop="20px"
                    aria-label="Lista de categorias"
                  >
                    <Select
                      onChange={onChangeCategory}
                      isLoading={categories.length <= 0}
                      variant="black"
                      options={categories}
                      placeholder={intl({
                        id: "app.events.select.categories",
                        defaultMessage: "categorias"
                      })}
                      label="Lista de categorias"
                      isClearable
                    />
                  </ContentContainer>
                </div>
                <div className="col-xl-2 col-lg-3 col-md-4 col-sm-5">
                  <ContentContainer
                    mobileXsMarginTop="20px"
                    aria-label="Lista de canais"
                  >
                    <Select
                      variant="black"
                      onChange={onChangeChannel}
                      isLoading={channels.length <= 0}
                      options={channels}
                      placeholder={intl({
                        id: "app.events.select.channel",
                        defaultMessage: "canal"
                      })}
                      label="Lista de canais"
                      isClearable
                    />
                  </ContentContainer>
                </div>
              </div>
            </div>
          </div>
        </ContentContainer>
      </div>

      <div className="container">
        <div className="calendar-cards-container">
          <div className="row no-gutters align-items-end">
            {range.map((item, index) => {
              if (index === 0) {
                return (
                  <div key={index} className="col">
                    <div
                      tabIndex={0}
                      className="calendar-card-today"
                      style={{
                        cursor: moment(currentDate).isSame(new Date(), "day")
                          ? "default"
                          : "pointer"
                      }}
                    >
                      <div onClick={onResetDate} className="top">
                        <Text
                          textTransform="capitalize"
                          fontWeight="400"
                          fontSize="14px"
                          lineHeight="1.5"
                          color={
                            theme.components.events.calendarCardToday.titleColor
                          }
                        >
                          {moment(currentDate).isSame(new Date(), "day")
                            ? intl({
                                id: "app.events.action.today",
                                defaultMessage: "hoje"
                              })
                            : intl({
                                id: "app.events.action.back",
                                defaultMessage: "voltar"
                              })}
                        </Text>
                      </div>
                      <div className="bottom">
                        <Text
                          textTransform="capitalize"
                          fontWeight="400"
                          fontSize="18px"
                          lineHeight="1.5"
                          color={
                            theme.components.events.calendarCardToday.textColor
                          }
                        >
                          {item.day}
                        </Text>
                        <PlusButton
                          backgroundColor={
                            theme.components.events.calendarCardToday
                              .plusButtonBgColor
                          }
                          size="30px"
                          bottom="-15px"
                          aria-hidden="true"
                          tabIndex={-1}
                        >
                          <Text
                            fontSize="24px"
                            fontWeight="100"
                            color={
                              theme.components.events.calendarCardToday
                                .plusButtonTextColor
                            }
                            text={theme.icons.plus}
                            lineHeight="1"
                            tabIndex={-1}
                          />
                        </PlusButton>
                      </div>
                    </div>
                  </div>
                );
              } else {
                return (
                  <div key={index} className="col">
                    <button
                      onClick={() => onChangeDate(item.m, false)}
                      className="calendar-card"
                      tabIndex="0"
                    >
                      <Text
                        textTransform="capitalize"
                        fontWeight="400"
                        fontSize="14px"
                        lineHeight="1.5"
                        color={theme.components.events.calendarCard.textColor}
                      >
                        {item.day}
                      </Text>
                      <Text
                        textTransform="uppercase"
                        fontWeight="300"
                        fontSize="14px"
                        lineHeight="1.5"
                        color={theme.components.events.calendarCard.textColor}
                        aria-label={item.monthExtended}
                      >
                        {item.month}
                      </Text>
                    </button>
                  </div>
                );
              }
            })}
            {/*Calendar Button*/}
            <div className="col-auto">
              <ClickAwayListener onChange={() => setOpen(false)}>
                <button className="open-calendar" onClick={openCalendar}>
                  <Icon
                    name={theme.icons.calendar}
                    color={theme.components.events.calendarButton.iconColor}
                    fontSize="32px"
                    aria-label="Abrir calendário"
                  />
                </button>
                {isOpen && (
                  <ReactCalendar value={currentDate} onChange={onChangeDate} />
                )}
              </ClickAwayListener>
            </div>
          </div>
        </div>
        {isLoading ? (
          <Loading backgroundColor={theme.highContrast.backgroundColor} />
        ) : hasError ? (
          <div>Error..</div>
        ) : (
          <ContentContainer marginTop="65px">
            <div className="row">
              <div className="col-xl-6 col-lg-6 col-md-12 col-12">
                {onRenderList()}
              </div>

              <div className="col-xl-6 col-lg-6 col-md-12 col-12">
                {content.topEvent !== null
                  ? content.topEvent.map((item, index) => {
                      const isSameDay = moment(item.startDate).isSame(
                        item.endDate,
                        "day"
                      );

                      const agendaFeatured =
                        theme.components.events.agendaFeatured;

                      return (
                        <AgendaFeatured
                          className="events-agenda-featured"
                          key={index}
                          href={item.url}
                          dateContainerBgColor={
                            agendaFeatured.dateContainerBgColor
                          }
                          borderColor={agendaFeatured.borderColor}
                          dateContainerTextColor={
                            agendaFeatured.dateContainerTextColor
                          }
                          buttonBgColor={agendaFeatured.buttonBgColor}
                          buttonTextColor={agendaFeatured.buttonTextColor}
                          titleColor={agendaFeatured.titleColor}
                          textColor={agendaFeatured.textColor}
                          dateColor={agendaFeatured.dateColor}
                          textColorHover={agendaFeatured.textColorHover}
                          animationBgColor={agendaFeatured.animationBgColor}
                          plusButtonHoverBgColor={
                            agendaFeatured.plusButtonHoverBgColor
                          }
                          dateContainerDay={moment(
                            item.startDate,
                            "YYYY/MM/DD"
                          ).format("DD")}
                          dateContainerMonth={moment(
                            item.startDate,
                            "YYYY/MM/DD"
                          ).format("MMM")}
                          //Aria-Label
                          dateContainerMonthAriaLabel={moment(
                            item.startDate,
                            "YYYY/MM/DD"
                          ).format("MMMM")}
                          text={item.category}
                          title={item.title}
                          date={
                            isSameDay
                              ? `${moment(item.startDate).format(
                                  "DD MMMM YYYY"
                                )}`
                              : `${moment(item.startDate).format(
                                  "DD MMMM"
                                )} - ${moment(item.endDate).format(
                                  "DD MMMM YYYY"
                                )}`
                          }
                          marginTop={`${index === 0 ? "0" : "50px"}`}
                        />
                      );
                    })
                  : onRenderList()}
              </div>
            </div>
          </ContentContainer>
        )}
        {!isLoading &&
          Object.keys(content.listEvents).length !==
            content.pagination.totalResults && (
            <div className="row justify-content-center">
              <div className="col-auto">
                <ContentContainer marginTop="50px">
                  <Button
                    onClick={onLoadMore}
                    variant="default"
                    aria-label="Carregar mais"
                  >
                    <FormattedMessage
                      id="app.organism.button.LoadMore"
                      defaultMessage="Carregar mais"
                    />
                  </Button>
                </ContentContainer>
              </div>
            </div>
          )}
      </div>
    </Section>
  );
};

const defaults = {
  content: {},
  range: [],
  categories: {},
  channels: [],
  openCalendar: () => null,
  onChangeDate: () => null,
  onResetDate: () => null,
  onChangeCategory: () => null,
  onLoadMore: () => null,
  setOpen: () => null,
  isOpen: false,
  onChangeChannel: () => null,
  theme: PropTypes.object
};

const types = {
  className: PropTypes.string,
  intl: PropTypes.object,
  isLoading: PropTypes.bool,
  content: PropTypes.object.isRequired,
  hasError: PropTypes.bool,
  range: PropTypes.array.isRequired,
  categories: PropTypes.array.isRequired,
  channels: PropTypes.array.isRequired,
  openCalendar: PropTypes.func.isRequired,
  onChangeDate: PropTypes.func.isRequired,
  onResetDate: PropTypes.func.isRequired,
  onChangeCategory: PropTypes.func.isRequired,
  onLoadMore: PropTypes.func.isRequired,
  currentDate: PropTypes.instanceOf(Date),
  setOpen: PropTypes.func.isRequired,
  isOpen: PropTypes.bool.isRequired,
  onChangeChannel: PropTypes.func.isRequired,
  theme: PropTypes.object
};

Events.propTypes = types;
Events.defaultProps = defaults;

const EventsWithStyles = styled(Events)`
  /*Calendar Styles*/
  .calendar-card-today {
    .top {
      height: 30px;
      background-color: ${props =>
        props.theme.components.events.calendarCardToday.backgroundColor};
      position: relative;
      &:after {
        content: "";
        width: 1px;
        height: inherit;
        background-color: ${props =>
          props.theme.components.events.calendarCardToday.afterBgColor};
        position: absolute;
        right: -1px;
      }
    }
    .bottom {
      height: 70px;
      background-color: ${props =>
        props.theme.components.events.calendarCardToday.bottomBgColor};
    }

    &:focus {
      ${props => props.theme.mixins.focus};
    }
  }

  .calendar-card {
    height: 70px;
    width: 100%;
    background-color: ${props =>
      props.theme.components.events.calendarCard.backgroundColor};
    border: ${props => props.theme.components.events.calendarCard.border};
    border-right: none;

    &:hover {
      background-color: ${props =>
        props.theme.components.events.calendarCard.hoverBgColor};
      cursor: pointer;
      h1,
      p {
        color: ${props =>
          props.theme.components.events.calendarCard.textColorHover};
      }
    }

    &:focus {
      ${props => props.theme.mixins.focus};
      outline-offset: -2px;
    }

    &:active {
      background-color: ${props =>
        props.theme.components.events.calendarCard.activeBgColor};
    }
  }

  .calendar-card,
  .calendar-card-today .top,
  .calendar-card-today .bottom {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  button.open-calendar {
    height: 70px;
    width: 70px;
    background-color: ${props =>
      props.theme.components.events.calendarButton.backgroundColor};
    color: ${props => props.theme.components.events.calendarButton.color};
    border: none;
    cursor: pointer;

    &:hover {
      background-color: ${props =>
        props.theme.components.events.calendarButton.hoverBgColor};
    }

    &:focus {
      ${props => props.theme.mixins.focus};
    }
  }

  .calendar-cards-container {
    position: relative;
  }

  // Medium devices (tablets, less than 992px)
  @media (max-width: 991px) {
    .events-agenda-featured {
      margin-top: 50px;
    }
    .calendar-cards-container {
      .col:nth-child(7),
      .col:nth-child(8) {
        display: none;
      }
    }
  }

  // Small devices (landscape phones, less than 768px)
  @media (max-width: 767px) {
    .calendar-cards-container {
      .col:nth-child(5),
      .col:nth-child(6) {
        display: none;
      }
    }
  }

  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    .calendar-cards-container {
      .col:nth-child(3),
      .col:nth-child(4) {
        display: none;
      }
    }
  }
`;

export default withTheme(EventsWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <EventsWithStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
