import React from "react";
import PropTypes from "prop-types";
import { ContentContainer } from "../../../atoms";
import { PersonCardLoading } from "../../../molecules";
import { withSize } from "react-sizeme";
import { compose } from "../../../../utils/utilities";
import { withTheme } from "styled-components";

const Loading = ({ className, size, theme }) => {
  return (
    <div className={className}>
      <div className="row">
        <div className="col-xl col-lg col-md col-12">
          <ContentContainer smMarginbottom="15px">
            <PersonCardLoading
              CardBgColor={theme.loadings.journalCards.personCard.bgColor}
            />
          </ContentContainer>
        </div>
        <div className="col-xl col-lg col-md col-12">
          <PersonCardLoading
            CardBgColor={theme.loadings.journalCards.personCard.bgColor}
          />
        </div>
        {size.width >= 992 && (
          <div className="col-xl col-lg col-md col-12">
            <PersonCardLoading
              CardBgColor={theme.loadings.journalCards.personCard.bgColor}
            />
          </div>
        )}
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  size: PropTypes.object,
  theme: PropTypes.object
};

export default compose(withSize(), withTheme)(Loading);
