import React from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../atoms";
import { withTheme } from "styled-components";

const Loading = ({
  backgroundColor,
  height,
  mdHeight,
  linksGutter,
  hasIcons,
  mobileMenuIconOnly,
  theme
}) => {
  return (
    <ContentContainer
      height={height}
      mdHeight={mdHeight}
      backgroundColor={
        theme.highContrast.backgroundColor ||
        backgroundColor ||
        theme.loadings.bgColor
      }
      borderBottom={theme.highContrast.border}
      className="d-flex align-items-center"
    >
      <div className="container">
        <div className="row no-gutters">
          <div className="col-xl-3 col-auto">
            <ContentContainer marginRight="30px">
              <AnimatedBackground height="15px" width="100px" />
            </ContentContainer>
          </div>
          <div className="col">
            <ContentContainer display="flex" mobileMdDisplay="none">
              <ContentContainer marginRight={linksGutter}>
                <AnimatedBackground height="15px" width="77px" />
              </ContentContainer>
              <ContentContainer marginRight={linksGutter}>
                <AnimatedBackground height="15px" width="61px" />
              </ContentContainer>
              <ContentContainer marginRight={linksGutter}>
                <AnimatedBackground height="15px" width="38px" />
              </ContentContainer>
              <AnimatedBackground height="15px" width="78px" />
            </ContentContainer>
          </div>
          {hasIcons && (
            <div className="col-auto">
              <div className="d-flex">
                <ContentContainer
                  height="14px"
                  width="14px"
                  borderRadius="50%"
                  backgroundColor={theme.loadings.mainBgColor}
                />
                <ContentContainer
                  marginLeft="12px"
                  height="14px"
                  width="14px"
                  borderRadius="50%"
                  backgroundColor={theme.loadings.mainBgColor}
                />
                <ContentContainer
                  marginLeft="12px"
                  height="14px"
                  width="14px"
                  borderRadius="50%"
                  backgroundColor={theme.loadings.mainBgColor}
                />
              </div>
            </div>
          )}
          {!hasIcons && mobileMenuIconOnly && (
            <ContentContainer
              className="col-auto"
              display="none"
              mobileMdDisplay="block"
            >
              <div className="d-flex">
                <ContentContainer
                  marginLeft="12px"
                  height="14px"
                  width="14px"
                  borderRadius="50%"
                  backgroundColor={theme.loadings.mainBgColor}
                />
              </div>
            </ContentContainer>
          )}
        </div>
      </div>
    </ContentContainer>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  height: PropTypes.string,
  mdHeight: PropTypes.string,
  linksGutter: PropTypes.string,
  backgroundColor: PropTypes.string,
  hasIcons: PropTypes.bool,
  mobileMenuIconOnly: PropTypes.bool,
  theme: PropTypes.object
};

Loading.defaultProps = {
  height: "105px",
  mdHeight: "85px",
  linksGutter: "30px",
  hasIcons: true,
  mobileMenuIconOnly: false
};

export default withTheme(Loading);
