import React from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//Components
import { Section } from "../../atoms/index";
import { SectionHeader, InjectHtml } from "../../molecules/index";

const TablesContainer = styled.div`
  overflow-x: auto;

  table {
    font-family: ${props => props.theme.type.base};
    font-size: ${props => props.theme.sizes.sm};
    line-height: 1.6;
    width: 100%;
    border-collapse: collapse;
    border-spacing: 0;
    color: ${props => props.theme.components.table.textColor};
    font-weight: 300;
  }

  caption,
  th,
  td {
    text-align: left;
  }

  th,
  strong,
  b {
    font-weight: 400;
  }

  caption {
    font-size: ${props => props.theme.sizes.xl3m};
    font-weight: 500;
    line-height: 1.4;
    margin-bottom: 25px;
  }

  th {
    padding: 0 5px 0 5px;
    &:first-child {
      padding: 0 5px 0 0;
    }
    &:last-child {
      padding: 0 0 0 5px;
    }
  }

  td {
    padding: 10px 5px;
    min-width: 75px;
    border-top: ${props =>
      `1px solid ${props.theme.components.table.borderLightColor}`};
    border-bottom: ${props =>
      `1px solid ${props.theme.components.table.borderLightColor}`};
    &:first-child {
      padding: 10px 5px 10px 0;
    }
    &:last-child {
      padding: 10px 0 10px 5px;
    }
  }

  a {
    color: ${props => props.theme.components.tables.linkColor};
    &:hover {
      color: ${props => props.activeColor};
    }
  }
`;

const Tables = ({
  className,
  hasSectionTitle,
  title,
  theme,
  data,
  isFullWidth,
  activeColor
}) => {
  return (
    <Section className={className}>
      <div className="container">
        <div className="row no-gutters">
          <div
            className={
              isFullWidth
                ? "col-12"
                : "col-xl-6 offset-xl-3 col-lg-8 offset-lg-4 col-12 offset-0"
            }
          >
            {hasSectionTitle && (
              <SectionHeader
                textColor={theme.components.tables.titleColor}
                hasLink={false}
                hasLine={false}
                hasContainer={false}
              >
                {title}
              </SectionHeader>
            )}
            <TablesContainer activeColor={activeColor}>
              <InjectHtml variant="removeStyles" content={data} />
            </TablesContainer>
          </div>
        </div>
      </div>
    </Section>
  );
};

const defaults = {
  title: "Title",
  hasSectionTitle: false,
  data: "",
  isFullWidth: false,
  activeColor: "#0EB4BD"
};

const types = {
  className: PropTypes.string,
  title: PropTypes.string,
  data: PropTypes.string,
  isFullWidth: PropTypes.bool,
  activeColor: PropTypes.bool
};

Tables.propTypes = types;
Tables.defaultProps = defaults;

export default withTheme(Tables);

//Only used for the Storybook Props Table
export const WithProps = props => <Tables {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
