import React from "react";
import { AnimatedBackground, ContentContainer } from "../../../atoms";
import { withTheme } from "styled-components";

const Mask = props => {
  return (
    <ContentContainer
      backgroundColor={props.theme.loadings.bgImageNewsCard.maskBgColor}
      position="absolute"
      {...props}
    />
  );
};

const Loading = ({ ...props }) => {
  return (
    <ContentContainer
      height="360px"
      backgroundColor={props.theme.loadings.iconContainerBgColor}
      className="background-image-card"
    >
      <ContentContainer
        position="relative"
        backgroundColor={props.theme.loadings.iconContainerBgColor}
        height="inherit"
        display="flex"
      >
        <ContentContainer
          position="absolute"
          bottom="20px"
          left="20px"
          right="20px"
          padding="23px 20px"
          backgroundColor={
            props.theme.loadings.bgImageNewsCard.contentContainerBgColor
          }
        >
          <AnimatedBackground height="21px">
            <Mask height="21px" right="0" width="30%" {...props} />
          </AnimatedBackground>
        </ContentContainer>
      </ContentContainer>
    </ContentContainer>
  );
};

export default withTheme(Loading);
