import React, { Fragment, useRef, useState } from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
//Components
import { Text, Icon } from "../../../atoms/index";

const AgendaCard = ({
  children,
  className,
  category,
  title,
  text,
  date,
  href,
  target,
  categoryColor,
  titleColor,
  textColor,
  dateColor,
  iconColor,
  hasDateSlider,
  hasText,
  theme,
  agendaCardRef
}) => {
  const [isSlideRight, setSlideRight] = useState(false);
  const [isSlideLeft, setSlideLeft] = useState(false);
  const agendaCardLeft = useRef(null);
  const sliderContainer = useRef(null);

  const slideRight = e => {
    e.preventDefault();
    if (isSlideLeft) {
      sliderContainer.current.style.transform = "translateX(-45px)";
      setSlideRight(true);
      setSlideLeft(false);
      agendaCardLeft.current.classList.remove("slide-is-right");
      agendaCardLeft.current.classList.remove("slide-is-left");
    } else {
      sliderContainer.current.style.transform = "translateX(-90px)";
      setSlideRight(true);
      setSlideLeft(false);
      agendaCardLeft.current.classList.add("slide-is-right");
      agendaCardLeft.current.classList.remove("slide-is-left");
    }
  };
  const slideLeft = e => {
    e.preventDefault();
    if (isSlideRight) {
      sliderContainer.current.style.transform = "translateX(-45px)";
      setSlideLeft(true);
      setSlideRight(false);
      agendaCardLeft.current.classList.remove("slide-is-right");
      agendaCardLeft.current.classList.remove("slide-is-left");
    } else {
      sliderContainer.current.style.transform = "translateX(0)";
      setSlideLeft(true);
      setSlideRight(false);
      agendaCardLeft.current.classList.add("slide-is-left");
      agendaCardLeft.current.classList.remove("slide-is-right");
    }
  };

  return (
    <a
      className={className}
      href={href}
      target={target}
      ref={agendaCardRef}
      tabIndex="0"
    >
      <div className="row no-gutters">
        <div className="col-auto">
          <div ref={agendaCardLeft} className="agenda-card-left">
            {hasDateSlider ? (
              <div className="row no-gutters">
                <div className="col-auto">
                  <div className="arrow-container">
                    <button
                      onClick={slideLeft}
                      aria-label="Ir para a data inicial"
                    >
                      <Icon
                        className="fa-chevron-left"
                        name={theme.icons.chevron_left}
                        color={
                          iconColor || theme.components.agendaCard.iconColor
                        }
                      />
                    </button>
                  </div>
                </div>
                <div className="col">
                  <div className="text">
                    <div ref={sliderContainer} className="text-slider">
                      {children}
                    </div>
                  </div>
                </div>
                <div className="col-auto">
                  <div className="arrow-container">
                    <button
                      onClick={slideRight}
                      aria-label="Ir para a data final"
                    >
                      <Icon
                        className="fa-chevron-right"
                        name={theme.icons.chevron_right}
                        color={
                          iconColor || theme.components.agendaCard.iconColor
                        }
                      />
                    </button>
                  </div>
                </div>
              </div>
            ) : (
              <Fragment>{children}</Fragment>
            )}
          </div>
        </div>
        <div className="col">
          <div className="agenda-card-right">
            <Text
              size="article"
              text={category}
              color={categoryColor || theme.components.agendaCard.categoryColor}
              fontWeight="300"
            />
            <Text
              as="h3"
              text={title || theme.components.agendaCard.titleColor}
              size="article"
              fontWeight="400"
              color={titleColor}
              marginBottom="0"
            />
            <div className="bottom-content">
              {hasText && (
                <Text
                  text={text}
                  color={textColor || theme.components.agendaCard.textColor}
                  size="article"
                  fontWeight="300"
                />
              )}
              <Text
                textTransform="lowercase"
                text={date}
                color={dateColor || theme.components.agendaCard.dateColor}
                size="xSmall"
                fontWeight="300"
              />
            </div>
          </div>
        </div>
      </div>
    </a>
  );
};

AgendaCard.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  category: PropTypes.string,
  title: PropTypes.string,
  text: PropTypes.string,
  date: PropTypes.string,
  href: PropTypes.string,
  target: PropTypes.string,
  categoryColor: PropTypes.string,
  titleColor: PropTypes.string,
  textColor: PropTypes.string,
  dateColor: PropTypes.string,
  iconColor: PropTypes.string,
  hasDateSlider: PropTypes.bool,
  hasText: PropTypes.bool,
  theme: PropTypes.object
};

AgendaCard.defaultProps = {
  category: "Categoria",
  title: "Título",
  text: "Texto",
  date: "Data",
  target: "_blank",
  hasText: true
};

const AgendaCardWithStyles = styled(AgendaCard)`
  display: block;
  text-decoration: none;
  padding-top: 20px;
  padding-bottom: 20px;
  cursor: pointer;
  border-bottom: 1px solid
    ${props =>
      props.borderColor || props.theme.components.agendaCard.borderColor};
  &:focus {
    ${props => props.theme.mixins.focus};
  }
  &:first-child {
    border-top: 1px solid
      ${props =>
        props.borderColor || props.theme.components.agendaCard.borderColor};
  }

  .agenda-card-right {
    padding-right: 15px;
    .bottom-content {
      margin-top: 15px;
    }
  }

  //Agenda Card Animation
  .agenda-card-left {
    position: relative;
    z-index: 0;
    text-align: center;
    margin-right: 30px;
    min-width: 93px;

    .arrow-container {
      height: 100%;
      display: flex;
      align-items: center;
      padding: 4px;
    }

    button {
      background-color: transparent;
      border: none;
      &:focus {
        ${props => props.theme.mixins.focus};
      }
      &:hover {
        cursor: pointer;
      }
      i {
        height: 24px;
        width: 16px;
        line-height: 24px;
      }
    }

    .no-slider {
      padding: 10px 0;
    }

    //Text date, overflow, slide
    .text {
      max-width: 45px;
      overflow: hidden;

      .text-slider {
        display: flex;
        padding: 10px 0;
        //Start center container
        transform: translateX(-45px);
        transition: all 0.4s;
      }

      .initial-date,
      .today,
      .end-date {
        display: inline-block;
        min-width: 45px;
      }
    }

    &:after {
      content: "";
      -webkit-transition: all 0.2s;
      -moz-transition: all 0.2s;
      -o-transition: all 0.2s;
      transition: all 0.2s;
      position: absolute;
      left: 0;
      top: 0;
      width: 0;
      height: 100%;
      background-color: ${props =>
        props.squareColorHover ||
        props.theme.components.agendaCard.squareColorHover};
      z-index: -1;
    }

    &:hover {
      .arrow-button {
        background-color: ${props =>
          props.squareColorHover ||
          props.theme.components.agendaCard.squareColorHover};
      }
    }
  }

  /*
  -- Arrows states for opacity feedback
  */

  .slide-is-right {
    .fa-chevron-right {
      opacity: 0.5;
    }
    .fa-chevron-left {
      opacity: 1;
    }
  }

  .slide-is-left {
    .fa-chevron-left {
      opacity: 0.5;
    }
    .fa-chevron-right {
      opacity: 1;
    }
  }

  /*
  -- Agenda white block animation when hover all card for content
  */
  &:hover {
    .agenda-card-left {
      &:after {
        width: 100%;
      }
      p {
        color: ${props =>
          props.textColorHover ||
          props.theme.components.agendaCard.textColorHover};
      }
    }
    i {
      color: ${props =>
        props.iconColorHover ||
        props.theme.components.agendaCard.iconColorHover};
    }
    button {
      &:focus {
        ${props => props.theme.mixins.focus};
      }
    }
  }
`;

export default withTheme(AgendaCardWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <AgendaCardWithStyles {...props} />;

WithProps.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  category: PropTypes.string,
  title: PropTypes.string,
  text: PropTypes.string,
  date: PropTypes.string,
  href: PropTypes.string,
  target: PropTypes.string,
  categoryColor: PropTypes.string,
  titleColor: PropTypes.string,
  textColor: PropTypes.string,
  dateColor: PropTypes.string,
  iconColor: PropTypes.string,
  hasDateSlider: PropTypes.bool,
  hasText: PropTypes.bool,
  theme: PropTypes.object,
  borderColor: PropTypes.string,
  squareColorHover: PropTypes.string,
  textColorHover: PropTypes.string,
  iconColorHover: PropTypes.string
};
WithProps.defaultProps = {
  category: "Categoria",
  title: "Título",
  text: "Texto",
  date: "Data",
  target: "_blank",
  hasText: true
};
