import React from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../../atoms";
import { withTheme } from "styled-components";

const Mask = props => {
  return (
    <ContentContainer
      backgroundColor={props.theme.loadings.maskBgColor}
      position="absolute"
      {...props}
    />
  );
};

const Loading = ({ ...props }) => {
  return (
    <ContentContainer
      height="300px"
      backgroundColor={props.theme.loadings.cardBgColor}
      padding="20px"
    >
      <AnimatedBackground height="125px">
        <Mask height="15px" right="0" width="70%" {...props} />
        <Mask height="10px" top="15px" width="100%" {...props} />
        <Mask height="5px" top="25px" right="0" width="90%" {...props} />
        <Mask height="20px" top="30px" width="100%" {...props} />

        <Mask height="15px" top="50px" right="0" width="50%" {...props} />
        <Mask height="5px" top="65px" width="100%" {...props} />
        <Mask height="15px" top="70px" right="0" width="30%" {...props} />
        <Mask height="5px" top="85px" width="100%" {...props} />
        <Mask height="15px" top="90px" right="0" width="75%" {...props} />
        <Mask height="5px" top="105px" width="100%" {...props} />
        <Mask height="15px" top="110px" right="0" width="40%" {...props} />
      </AnimatedBackground>
    </ContentContainer>
  );
};

Loading.propTypes = {
  className: PropTypes.string
};

export default withTheme(Loading);
