import React from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//Components
import { Text, Line, ScreenReaderText, Section } from "../../atoms";
//Context
import { useConfig } from "../../../context/ConfigContext";

const Numbers = ({
  className,
  backgroundColor,
  titleColor,
  textColor,
  lineColor,
  data,
  removeCol01,
  removeCol04,
  sectionTitle,
  hasSectionTitle,
  sectionTitleColor,
  resource,
  hasDate,
  theme,
  date
}) => {
  const { formattedMessage: FormattedMessage } = useConfig();
  const highContrastTextColor = theme.highContrast.text;

  const getClasses = (position = 0) => {
    switch (position) {
      case 0:
        return removeCol01
          ? "hide-column"
          : "col-xl-auto col-lg-4 offset-lg-0 col-sm-4 offset-sm-0 col-8 offset-1";
      case 1:
        return removeCol01
          ? "col-xl-auto col-lg-4 offset-lg-0 col-md-4 col-sm-4 offset-sm-0 col-8 offset-1"
          : "col-xl-auto offset-xl-0 col-lg-4 offset-lg-4 col-md-4 offset-md-4 col-sm-4 offset-sm-4 col offset-5";

      case 2:
        return removeCol01
          ? "col-xl-auto offset-xl-0 col-lg-4 offset-lg-4 col-md-4 offset-md-4 col-sm-4 offset-sm-4 col offset-5"
          : "col-xl-auto offset-xl-0 col-lg-4 offset-lg-0 col-md-4 col-sm-4 offset-sm-0 col-8 offset-1";

      case 3:
        return removeCol04
          ? "hide-column"
          : "col-xl-auto offset-xl-0 col-lg-4 offset-lg-4 col-md-4 offset-md-4 col-sm-4 offset-sm-4 col offset-5";
      default:
        return "col-xl-auto col-lg-4 offset-lg-0 col-sm-4 offset-sm-0 col-8 offset-1";
    }
  };

  return (
    <Section
      className={className}
      paddingTop="120px"
      paddingBottom="120px"
      backgroundColor={theme.highContrast.backgroundColor || backgroundColor}
    >
      <ScreenReaderText>
        <Text as="h2" text="Estatísticas" />
      </ScreenReaderText>
      <div className="container">
        {hasSectionTitle && (
          <Text
            as="h3"
            color={
              highContrastTextColor ||
              sectionTitleColor ||
              theme.components.numbers.sectionTitleColor
            }
            size="xLarge"
            fontWeight="500"
            marginBottom={hasDate ? "20px" : "50px"}
          >
            {resource ? (
              <FormattedMessage id={resource} defaultMessage={sectionTitle} />
            ) : (
              sectionTitle
            )}
          </Text>
        )}
        {hasDate && (
          <Text size="article" marginBottom="50px">
            {date}
          </Text>
        )}

        <div className="row">
          {data.map((item, index) => {
            return (
              <div key={index} className={getClasses(index)}>
                <div
                  className={`number number-${index + 1}`}
                  aria-label={item.title + " " + item.number}
                >
                  <Text
                    as="h3"
                    text={item.number}
                    fontSize="80px"
                    mobileSmFontSize="40px"
                    color={highContrastTextColor || titleColor}
                    fontWeight="900"
                    marginBottom="0"
                    lineHeight="1"
                    aria-hidden="true"
                  />
                  <div className="text-container">
                    <Text
                      text={item.title}
                      color={highContrastTextColor || textColor}
                      size="article"
                      aria-hidden="true"
                    />
                  </div>
                  <Line color={highContrastTextColor || lineColor} />
                </div>
              </div>
            );
          })}
        </div>

        {/* <div className="row">
          {removeCol01 ? null : (
            <div className="col-xl-auto col-lg-4 offset-lg-0 col-sm-4 offset-sm-0 col-8 offset-1">
              <div
                className="number number-1"
                aria-label={data[0].title + " " + data[0].number}
              >
                <Text
                  as="h3"
                  text={data[0].number}
                  fontSize="80px"
                  mobileSmFontSize="40px"
                  color={highContrastTextColor || titleColor}
                  fontWeight="900"
                  marginBottom="0"
                  lineHeight="1"
                  aria-hidden="true"
                />
                <div className="text-container">
                  <Text
                    text={data[0].title}
                    color={highContrastTextColor || textColor}
                    size="article"
                    aria-hidden="true"
                  />
                </div>
                <Line color={highContrastTextColor || lineColor} />
              </div>
            </div>
          )}

          <div
            className={`${
              removeCol01
                ? "col-xl-auto col-lg-4 offset-lg-0 col-md-4 col-sm-4 offset-sm-0 col-8 offset-1"
                : "col-xl-auto offset-xl-0 col-lg-4 offset-lg-4 col-md-4 offset-md-4 col-sm-4 offset-sm-4 col offset-5"
            }`}
          >
            <div
              className="number number-2"
              aria-label={data[1].title + " " + data[1].number}
            >
              <Text
                as="h3"
                text={data[1].number}
                color={highContrastTextColor || titleColor}
                fontSize="80px"
                mobileSmFontSize="40px"
                fontWeight="900"
                marginBottom="0"
                lineHeight="1"
                aria-hidden="true"
              />
              <div className="text-container">
                <Text
                  text={data[1].title}
                  color={highContrastTextColor || textColor}
                  size="article"
                  aria-hidden="true"
                />
              </div>
              <Line color={highContrastTextColor || lineColor} />
            </div>
          </div>

          <div
            className={`${
              removeCol01
                ? "col-xl-auto offset-xl-0 col-lg-4 offset-lg-4 col-md-4 offset-md-4 col-sm-4 offset-sm-4 col offset-5"
                : "col-xl-auto offset-xl-0 col-lg-4 offset-lg-0 col-md-4 col-sm-4 offset-sm-0 col-8 offset-1"
            }`}
          >
            <div
              className="number number-3"
              aria-label={data[2].title + " " + data[2].number}
            >
              <Text
                as="h3"
                text={data[2].number}
                fontSize="80px"
                mobileSmFontSize="40px"
                color={highContrastTextColor || titleColor}
                fontWeight="900"
                marginBottom="0"
                lineHeight="1"
                aria-hidden="true"
              />
              <div className="text-container">
                <Text
                  text={data[2].title}
                  color={highContrastTextColor || textColor}
                  size="article"
                  aria-hidden="true"
                />
              </div>
              <Line color={highContrastTextColor || lineColor} />
            </div>
          </div>

          {removeCol04 ? null : (
            <div className="col-xl-auto offset-xl-0 col-lg-4 offset-lg-4 col-md-4 offset-md-4 col-sm-4 offset-sm-4 col offset-5">
              <div
                className="number number-4"
                aria-label={data[3].title + " " + data[3].number}
              >
                <Text
                  as="h3"
                  text={data[3].number}
                  color={highContrastTextColor || titleColor}
                  fontSize="80px"
                  mobileSmFontSize="40px"
                  fontWeight="900"
                  marginBottom="0"
                  lineHeight="1"
                  aria-hidden="true"
                />
                <div className="text-container">
                  <Text
                    text={data[3].title}
                    color={highContrastTextColor || textColor}
                    size="article"
                    aria-hidden="true"
                  />
                </div>
                <Line color={highContrastTextColor || lineColor} />
              </div>
            </div>
          )}
          </div>*/}
      </div>
    </Section>
  );
};

const types = {
  backgroundColor: PropTypes.string,
  titleColor: PropTypes.string,
  textColor: PropTypes.string,
  lineColor: PropTypes.string,
  data: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),
  className: PropTypes.string,
  removeCol01: PropTypes.bool,
  removeCol04: PropTypes.bool,
  hasSectionTitle: PropTypes.bool,
  sectionTitle: PropTypes.string,
  sectionTitleColor: PropTypes.string,
  resource: PropTypes.string,
  date: PropTypes.string,
  hasDate: PropTypes.bool,
  theme: PropTypes.object
};

const defaults = {
  hasDate: true,
  date: "Data"
};

Numbers.propTypes = types;
Numbers.defaultProps = defaults;

const NumbersWithStyles = styled(Numbers)`
  .hide-column {
    display: none;
  }

  .text-container {
    margin-bottom: ${props => props.theme.spacing.xs};
    max-width: 160px;
  }

  // Extra large devices (large desktops, 1200px and up)
  @media (min-width: 1200px) {
    .number {
      width: 250px;
    }
  }

  // Extra large devices (large desktops, 1200px and down)
  @media (max-width: 1200px) {
    .number-1,
    .number-2 {
      margin-bottom: 80px;
    }
  }

  // Small devices (landscape phones, 576px and down)
  @media (max-width: 576px) {
    .number-1,
    .number-2,
    .number-3 {
      margin-bottom: 60px;
    }
  }
`;

export default withTheme(NumbersWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <NumbersWithStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
