import React from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
//Components
import {
  Link,
  ListItem,
  Picture,
  Text,
  VerticalList,
  ContentContainer
} from "../../../atoms";

import MarkdownRenderer from "../../MarkdownRenderer";
import PlusButton from "../../PlusButton";
import SectionHeader from "../../SectionHeader";

//import { MarkdownRenderer, PlusButton, SectionHeader } from "../../index";

//Context
import { useConfig } from "../../../../context/ConfigContext";

const CardContainer = styled.div`
  background-color: ${props => props.backgroundColor};
  overflow: hidden;
  display: grid;
  grid-auto-flow: column;
  grid-template-rows: 350px;

  @media (max-width: 575.98px) {
    grid-template-columns: 15px repeat(10, 1fr) 15px;
  }

  @media (min-width: 576px) and (max-width: 767.98px) {
    //540px / 10
    grid-template-columns: 1fr repeat(10, 54px) 1fr;
  }

  @media (min-width: 768px) and (max-width: 991.98px) {
    //720px / 10
    grid-template-columns: 1fr repeat(10, 72px) 1fr;
  }

  @media (min-width: 992px) and (max-width: 1199.98px) {
    //960px / 10
    grid-template-columns: 1fr repeat(10, 96px) 1fr;
  }

  @media (min-width: 1200px) {
    //1140px / 10
    grid-template-columns: 1fr repeat(10, 114px) 1fr;
  }
`;

const ImageContainer = styled.div`
  grid-column: ${props => props.gridColumn};

  @media (max-width: 767.98px) {
    grid-column: ${props => props.smGridColumn};
  }
`;

const TextContainer = styled.div`
  grid-column: ${props => props.gridColumn};
  align-self: center;

  @media (min-width: 576px) {
    padding-left: ${props => props.paddingLeft};
    padding-right: ${props => props.paddingRight};
  }

  @media (max-width: 575.98px) {
    .campus-items {
      display: none;
    }
  }

  @media (max-width: 767.98px) {
    grid-column: ${props => props.smGridColumn};
  }
`;

const TextContentContainer = styled.div`
  position: relative;
  padding-right: ${props => props.paddingRight};
  padding-left: ${props => props.paddingLeft};
  @media (max-width: 575px) {
    padding-right: ${props => props.xsPaddingRight};
    padding-left: ${props => props.xsPaddingLeft};
  }
`;

const OverflowContainer = styled.div`
  max-height: 200px;
  overflow: hidden;
`;

const ColumnsCard = ({
  textGridColumn,
  imageGridColumn,
  theme,
  textColor,
  backgroundColor,
  alt,
  src,
  showPlusButton,
  inverted,
  title,
  href,
  links,
  externalLink,
  description,
  hasText,
  isMarkdown,
  activeColor
}) => {
  const { formattedMessage: FormattedMessage } = useConfig();
  const {
    textColor: themeTextColor,
    linkColor: themeLinkColor,
    backgroundColor: themeBgColor
  } = theme.components.columnsCard;
  return (
    <CardContainer
      backgroundColor={
        theme.highContrast.backgroundColor || backgroundColor || themeBgColor
      }
    >
      <TextContainer
        paddingLeft={inverted ? "0" : "15px"}
        paddingRight={inverted ? "15px" : "0"}
        gridColumn={textGridColumn}
        smGridColumn={inverted ? "7 / 12" : "2 / 7"}
      >
        <TextContentContainer
          paddingLeft={inverted ? "55px" : "0"}
          paddingRight={inverted ? "0" : "55px"}
          xsPaddingLeft={inverted ? "30px" : "0"}
          xsPaddingRight={inverted ? "0" : "30px"}
        >
          <ContentContainer zIndex="2" position="relative">
            <SectionHeader
              href={href}
              tag="h3"
              fontSize="28px"
              mobileSmFontSize="26px"
              mobileXsFontSize="20px"
              hasContainer={false}
              hasLine={true}
              hasLink={true}
              textColor={theme.highContrast.link || textColor || themeTextColor}
              marginBottom="15px"
              externalLink={externalLink}
            >
              {title}
            </SectionHeader>
            <OverflowContainer>
              {hasText ? (
                isMarkdown ? (
                  <MarkdownRenderer
                    content={description}
                    activeColor={activeColor}
                  />
                ) : (
                  <Text
                    lineHeight="1.6"
                    size="article"
                    fontWeight="300"
                    text={description}
                  />
                )
              ) : (
                <VerticalList className="campus-items">
                  {links.map((item, index) => (
                    <ListItem key={index} marginBottom="5px">
                      <Link
                        size="medium"
                        fontWeight="400"
                        color={
                          theme.highContrast.link || textColor || themeLinkColor
                        }
                        href={item.href}
                      >
                        {item.resource ? (
                          <FormattedMessage
                            id={item.resource}
                            defaultMessage={item.text}
                          />
                        ) : (
                          item.text
                        )}
                      </Link>
                    </ListItem>
                  ))}
                </VerticalList>
              )}
            </OverflowContainer>
          </ContentContainer>
          {showPlusButton && (
            <PlusButton
              backgroundColor={
                theme.highContrast.backgroundColor ||
                backgroundColor ||
                themeBgColor
              }
              size="60px"
              position="absolute"
              height="100%"
              right={inverted ? "initial" : "-30px"}
              left={inverted ? "-30px" : "initial"}
              top="0"
              zIndex="1"
              as="a"
              href={href}
            >
              <Text
                fontSize="50px"
                fontWeight="100"
                color={theme.highContrast.link || textColor || themeLinkColor}
                text={theme.icons.plus}
                lineHeight="1"
              />
            </PlusButton>
          )}
        </TextContentContainer>
      </TextContainer>
      <ImageContainer
        gridColumn={imageGridColumn}
        smGridColumn={inverted ? "1 / 7" : "7 / -1"}
      >
        <Picture src={src} alt={alt} height="100%" />
      </ImageContainer>
    </CardContainer>
  );
};

const types = {
  backgroundColor: PropTypes.string,
  textGridColumn: PropTypes.string,
  imageGridColumn: PropTypes.string,
  showPlusButton: PropTypes.bool,
  inverted: PropTypes.bool,
  alt: PropTypes.string,
  src: PropTypes.string,
  title: PropTypes.string,
  links: PropTypes.array,
  href: PropTypes.string,
  externalLink: PropTypes.bool,
  description: PropTypes.string,
  hasText: PropTypes.bool,
  isMarkdown: PropTypes.bool,
  activeColor: PropTypes.string
};

const defaults = {
  textGridColumn: "2 / 7",
  imageGridColumn: "7 / -1",
  href: "#",
  showPlusButton: true,
  title: "Título",
  description: "Texto",
  alt: "imagem sem alt",
  src: "https://static.ua.pt/images/ua/placeholder-bw.png",
  inverted: false,
  links: [
    {
      text: "texto",
      href: "#"
    }
  ],
  hasText: false,
  isMarkdown: false,
  activeColor: "#0EB4BD",
  externalLink: false
};

ColumnsCard.propTypes = types;
ColumnsCard.defaultProps = defaults;

export default withTheme(ColumnsCard);

//Only used for the Storybook Props Table
export const WithProps = props => <ColumnsCard {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
