import React from "react";
import PropTypes from "prop-types";
//components
import { Text, Button, ContentContainer } from "../../atoms";
import { withTheme } from "styled-components";
import { useConfig } from "./../../../context/ConfigContext";

const OneCol = ({ data, theme, oneColAction }) => {
  const { formattedMessage: FormattedMessage } = useConfig();

  return (
    <div className="row">
      <div className="col-12">
        <Text
          color={theme.components.contents.oneCol.textColor}
          size="article"
          lineHeight="1.6"
          text={data}
        />
        <div className="row">
          <div className="col-auto">
            <ContentContainer marginTop="25px">
              <Button
                variant="light"
                backgroundColor={theme.components.contents.oneCol.buttonBgColor}
                onClick={oneColAction}
              >
                <FormattedMessage
                  id="app.course.curricular"
                  defaultMessage="Plano Curricular"
                />
              </Button>
            </ContentContainer>
          </div>
        </div>
      </div>
    </div>
  );
};

OneCol.propTypes = {
  data: PropTypes.string.isRequired,
  backgroundColor: PropTypes.string.isRequired,
  theme: PropTypes.object,
  oneColAction: PropTypes.func.isRequired
};

export default withTheme(OneCol);
