import React from "react";
import PropTypes from "prop-types";
//Styles
import styled from "styled-components";
import theme from "../../../styles/theme.semantic";
//Components
import {
  Accordion as StyledAccordion,
  AccordionItem,
  AccordionItemTitle,
  AccordionItemBody
} from "react-accessible-accordion";
import SectionHeader from "../../molecules/SectionHeader";

const AccordionHeader = ({ children, className }) => {
  return (
    <div className={`${className} u-position-relative`}>
      <p className="header">{children}</p>
    </div>
  );
};

const AccordionHeaderWithStyles = styled(AccordionHeader)`
  .header {
    color: ${props =>
      props.color || props.theme.components.generalAccordion.header};
    font-size: ${props => props.fontSize || props.theme.sizes.m};
    font-weight: ${props => props.fontWeight || props.theme.weight.light};
  }
`;

const AccordionTitle = ({ children, className, isOpen, theme, url }) => {
  return (
    <div className={`${className} u-position-relative col-auto pl-0 pr-5`}>
      <SectionHeader
        href={url}
        hasContainer={false}
        hasLine={isOpen}
        hasLink={isOpen}
        textColor={theme.components.generalAccordion.title}
        fontSize="20px"
        fontWeight="400"
        marginBottom="0px"
      >
        {children}
      </SectionHeader>
      <i className="fal fa-minus" role="presentation" />
      <i
        className={`fal fa-minus ${isOpen ? "animate" : "normal"}`}
        role="presentation"
      />
    </div>
  );
};

const AccordionTitleWithStyles = styled(AccordionTitle)`
  .title {
    color: ${props =>
      props.color || props.theme.components.generalAccordion.title};
    font-size: ${props => props.fontSize || props.theme.sizes.m};
  }
`;

AccordionHeader.propTypes = {
  color: PropTypes.string,
  className: PropTypes.string,
  children: PropTypes.node,
  uppercase: PropTypes.string,
  fontSize: PropTypes.string,
  fontWeight: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
};
AccordionTitle.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  isOpen: PropTypes.bool,
  theme: PropTypes.object,
  url: PropTypes.string
};

const Accordion = styled(StyledAccordion)`
  .u-position-absolute {
    position: absolute;
  }

  .u-position-relative {
    position: relative;
    i {
      transition: transform 0.25s ease-in-out;
    }
  }

  .accordion {
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 2px;
  }

  .accordion__item + .accordion__item {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }

  .accordion__item--has-icon {
    position: relative;
  }
  .accordion__item {
    border-top: ${props => props.theme.components.generalAccordion.borderColor};
  }

  .accordion__title {
    cursor: pointer;
    padding-top: 18px;
    width: 100%;
    text-align: left;
    border: none;
    outline: none;
    margin-bottom: 18px;
  }

  .accordion__body {
    margin: 0 0 12px 0;
    display: block;
    animation: fadein 0.35s ease-in;
  }

  .accordion__body--hidden {
    display: none;
    opacity: 0;
    animation: fadein 0.35s ease-in;
  }

  .accordion__title > *:last-child,
  .accordion__body > *:last-child {
    margin-bottom: 0;
  }

  .fal {
    display: inline-block;
    position: absolute;
    top: 0%;
    right: 0;
    margin-right: 9px;
    font-weight: 100;
    color: ${props => props.theme.components.generalAccordion.title};

    &.animate {
      position: absolute;
      transform: rotate(0deg);
    }
    &.normal {
      position: absolute;
      transform: rotate(90deg);
    }
  }

  /* -------------------------------------------------- */
  /* ---------------- Animation part ------------------ */
  /* -------------------------------------------------- */

  @keyframes fadein {
    0% {
      opacity: 0;
    }

    100% {
      opacity: 1;
    }
  }

  @keyframes move-down {
    0% {
      transform: translateY(0);
    }
    10% {
      transform: translateY(0);
    }
    20% {
      transform: translateY(5px);
    }
    30% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(0);
    }
  }

  @keyframes move-up {
    0% {
      transform: translateY(0);
    }
    10% {
      transform: translateY(0);
    }
    20% {
      transform: translateY(-5px);
    }
    30% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(0);
    }
  }

  .accordion__title--animated:hover .fal {
    animation-name: move-down;
    animation-duration: 1.5s;
  }

  .accordion__title--animated[aria-expanded="true"]:hover .fal {
    animation-name: move-up;
    animation-duration: 1.5s;
  }
`;

export {
  Accordion as GeneralAccordion,
  AccordionItem as GeneralAccordionItem,
  AccordionItemTitle as GeneralAccordionItemTitle,
  AccordionItemBody as GeneralAccordionItemBody,
  AccordionTitleWithStyles as GeneralAccordionTitle,
  AccordionHeaderWithStyles as GeneralAccordionHeader
};

//Only used for the Storybook Props Table
export const WithProps = props => <div {...props} />;

WithProps.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  isOpen: PropTypes.bool,
  color: PropTypes.string,
  fontSize: PropTypes.string,
  fontWeight: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  url: PropTypes.string
};
WithProps.defaultProps = {
  color: theme.components.generalAccordion.title,
  fontSize: "14px",
  fontWeight: 300,
  isOpen: false
};
