import React, { useState } from "react";
import PropTypes from "prop-types";
import styled from "styled-components";

const AlertBar = ({ children, isOpen, onClose, hasAction, ...props }) => {
  const [open, setOpen] = useState(isOpen);
  const handleClick = () => {
    setOpen(false);
    onClose();
  };
  if (open)
    return (
      <Bar {...props}>
        <div className="container">
          <div className="row">
            <Bar.Message className="col">{children}</Bar.Message>
            {hasAction && (
              <Bar.Action className="col-auto" onClick={handleClick}>
                <button>
                  <i className="fal fa-times" />
                </button>
              </Bar.Action>
            )}
          </div>
        </div>
      </Bar>
    );
  return null;
};

const Bar = styled.div`
  width: 100%;
  padding: ${props => props.theme.spacing.xs};
  background-color: ${props =>
    props.backgroundColor ? props.backgroundColor : props.theme.colors.green};

  ${props =>
    !props.noAnimation &&
    ` animation: slide-in 1s ease-in-out;

@keyframes slide-in {
  0% {
    transform: translateY(100%);
  }
  100% {
    transform: translateY(0%);
  }
}`}
`;

Bar.Message = styled.div``;

Bar.Action = styled.div`
  button {
    cursor: pointer;
    background-color: transparent;
    border: none;
    height: 100%;
    width: 75px;
    &:hover {
      opacity: 0.5;
    }
  }
`;

AlertBar.propTypes = {
  children: PropTypes.node.isRequired,
  isOpen: PropTypes.bool,
  onClose: PropTypes.func,
  hasAction: PropTypes.bool
};

AlertBar.defaultProps = {
  isOpen: true,
  onClose: () => null,
  noAnimation: false,
  hasAction: true
};

export default AlertBar;
