import React from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../../atoms";
import { withTheme } from "styled-components";

const Loading = ({
  cardHeight,
  cardBgColor,
  columnClasses,
  orderClasses,
  theme
}) => {
  return (
    <ContentContainer className="row no-gutters d-flex align-items-center justify-content-center">
      {/*Triangle*/}
      <ContentContainer
        height="45.2px"
        width="45.2px"
        position="absolute"
        zIndex="1"
        transform="rotate(45deg)"
        backgroundColor={cardBgColor || theme.loadings.cardBgColor}
      />
      <div className={`${columnClasses[0]} ${orderClasses[0]}`}>
        <ContentContainer
          className="d-flex flex-column justify-content-between"
          height={cardHeight}
          padding="30px"
          mobileXsPadding="10px"
          backgroundColor={cardBgColor || theme.loadings.cardBgColor}
        >
          <AnimatedBackground height="54px" width="96px" />
          <AnimatedBackground height="60px" width="60%">
            <ContentContainer
              height="10px"
              width="100%"
              position="absolute"
              top="calc(50% - 5px)"
              backgroundColor={cardBgColor || theme.loadings.cardBgColor}
            />
          </AnimatedBackground>
        </ContentContainer>
      </div>
      <div className={`${columnClasses[1]} ${orderClasses[1]}`}>
        <ContentContainer
          height={cardHeight}
          backgroundColor={theme.loadings.mainBgColor}
        />
      </div>
    </ContentContainer>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  cardBgColor: PropTypes.string,
  cardHeight: PropTypes.string,
  theme: PropTypes.object,
  columnClasses: PropTypes.array,
  orderClasses: PropTypes.array
};

Loading.defaultProps = {
  cardHeight: "300px",
  orderClasses: ["", ""],
  columnClasses: ["col", "col"]
};

export default withTheme(Loading);
