import React, { Fragment } from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
import {
  Text,
  Picture,
  ContentContainer,
  Triangle,
  Icon
} from "../../../atoms";
//Utils
import moment from "moment";

const CardContainer = styled.a`
  display: block;
  cursor: pointer;
  text-decoration: none;
  background-color: ${props => props.backgroundColor};

  //Image Animation
  img {
    -webkit-transition: all 0.4s;
    -moz-transition: all 0.4s;
    -o-transition: all 0.4s;
    transition: all 0.4s;
  }

  &:hover {
    img {
      -ms-transform: scale(1.1);
      -moz-transform: scale(1.1);
      -webkit-transform: scale(1.1);
      -o-transform: scale(1.1);
      transform: scale(1.1);
    }
  }
`;

const EventCard = ({
  backgroundColor,
  className,
  image,
  alt,
  theme,
  textColor,
  orderClasses,
  padding,
  mdPadding,
  xsPadding,
  hasEndDate,
  title,
  description,
  columnClasses,
  href,
  target,
  cardHeight,
  changeXlTriangleOrder,
  startDate,
  endDate
}) => {
  const color =
    theme.highContrast.text || textColor || theme.components.eventCard.color;
  const bgColor = backgroundColor || theme.components.eventCard.backgroundColor;
  return (
    <CardContainer
      target={target}
      href={href}
      tabIndex="0"
      backgroundColor={bgColor}
      className={className}
    >
      <div className="row no-gutters">
        <div className={`${columnClasses[0]} ${orderClasses[0]}`}>
          <ContentContainer
            className="d-flex flex-column"
            height="100%"
            padding={padding}
            mdPadding={mdPadding}
            mobileXsPadding={xsPadding}
            position="relative"
          >
            <Triangle
              className="triangle"
              transform={
                changeXlTriangleOrder ? "rotate(-90deg)" : "rotate(90deg)"
              }
              right={changeXlTriangleOrder ? null : "-48px"}
              left={changeXlTriangleOrder ? "-48px" : null}
              withContent={true}
              contentColor={color}
              color={bgColor}
              size="32px"
              top="calc(50% - 16px)"
              contentSize="36px"
              zIndex="1"
            />
            {/*Date*/}
            <div className="d-flex">
              <ContentContainer className="flex-shrink-0" marginRight="10px">
                <Text
                  size="xLarge"
                  fontWeight="900"
                  lineHeight="1"
                  color={color}
                >
                  {moment(startDate).format("DD")}
                </Text>
                <Text size="medium" color={color} textTransform="uppercase">
                  {moment(startDate).format("MMM")}
                </Text>
              </ContentContainer>
              {hasEndDate && (
                <Fragment>
                  <Icon
                    marginTop="8px"
                    name="fal fa-chevron-right"
                    size="small"
                    color={color}
                    opacity="0.6"
                  />
                  <ContentContainer className="flex-shrink-0" marginLeft="10px">
                    <Text
                      size="xLarge"
                      fontWeight="900"
                      lineHeight="1"
                      color={color}
                      opacity="0.6"
                    >
                      {moment(endDate).format("DD")}
                    </Text>
                    <Text
                      size="medium"
                      color={color}
                      opacity="0.6"
                      textTransform="uppercase"
                    >
                      {moment(endDate).format("MMM")}
                    </Text>
                  </ContentContainer>
                </Fragment>
              )}
            </div>

            {/*Bottom*/}
            <ContentContainer
              className="d-flex flex-column justify-content-end"
              marginTop="auto"
              minHeight="140px"
              maxHeight="140px"
              overflow="hidden"
            >
              <Text size="large" fontWeight="500" color={color}>
                {title}
              </Text>
              <Text size="medium" color={color}>
                {description}
              </Text>
            </ContentContainer>
          </ContentContainer>
        </div>
        <div className={`${columnClasses[1]} ${orderClasses[1]}`}>
          <ContentContainer height={cardHeight} overflow="hidden">
            <Picture src={image} alt={alt} height="100%" />
          </ContentContainer>
        </div>
      </div>
    </CardContainer>
  );
};

const types = {
  className: PropTypes.string,
  backgroundColor: PropTypes.string,
  theme: PropTypes.object,
  image: PropTypes.string,
  alt: PropTypes.string,
  textColor: PropTypes.string,
  orderClasses: PropTypes.array,
  columnClasses: PropTypes.array,
  padding: PropTypes.string,
  mdPadding: PropTypes.string,
  xsPadding: PropTypes.string,
  title: PropTypes.string,
  description: PropTypes.string,
  href: PropTypes.string,
  target: PropTypes.string,
  cardHeight: PropTypes.string,
  hasEndDate: PropTypes.bool,
  changeXlTriangleOrder: PropTypes.bool,
  startDate: PropTypes.string.isRequired,
  endDate: PropTypes.string.isRequired
};

const defaults = {
  href: "#",
  image: "https://static.ua.pt/images/ua/placeholder-bw.png",
  alt: "Imagem sem alt",
  orderClasses: ["", ""],
  columnClasses: ["col", "col"],
  padding: "30px",
  mdPadding: "30px",
  xsPadding: "10px",
  title: "Título",
  description: "Descrição",
  cardHeight: "300px",
  hasEndDate: true,
  changeXlTriangleOrder: false,
  startDate: "2020-01-15T00:00:00",
  endDate: "2020-01-15T00:00:00"
};

EventCard.propTypes = types;
EventCard.defaultProps = defaults;

export default withTheme(EventCard);

//Only used for the Storybook Props Table
export const WithProps = props => <EventCard {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
