import React from "react";
import PropTypes from "prop-types";
import Select, { components } from "react-select";
import styled, { withTheme } from "styled-components";
import { Icon } from "../../atoms";

const DropdownIndicator = props => {
  const color = props.selectProps.variant === "black" ? "#000" : "#fff";
  return (
    components.DropdownIndicator && (
      <components.DropdownIndicator {...props}>
        <Icon size="xSmall" color={color} name="far fa-chevron-down" />
      </components.DropdownIndicator>
    )
  );
};

DropdownIndicator.propTypes = {
  selectProps: PropTypes.object
};

const NoOptionsMessage = props => {
  return (
    <components.NoOptionsMessage {...props}>
      Sem opções
    </components.NoOptionsMessage>
  );
};
function SelectWithStyles(props) {
  const color = `${
    props.variant === "black"
      ? props.theme.components.select.variantBlack.color
      : "#fff"
  }`;
  const background = `${
    props.variant === "black"
      ? props.theme.components.select.variantBlack.bgColor
      : "transparent"
  }`;
  return (
    <Select
      {...props}
      components={{
        DropdownIndicator,
        NoOptionsMessage
      }}
      styles={{
        singleValue: provided => ({
          ...provided,
          color: `${color}`
        }),
        control: provided => ({
          ...provided,
          background: `${background}`,
          border: `1px solid ${color}`,
          cursor: "pointer",
          fontSize: "14px"
        }),
        option: (provided, state) => ({
          ...provided,
          cursor: "pointer",
          fontSize: "12px",
          color: state.isSelected
            ? props.theme.components.select.optionIsSelectedColor
            : state.isFocused &&
              props.theme.components.select.optionIsFocusColor,
          backgroundColor: state.isSelected
            ? props.theme.components.select.optionIsSelected
            : state.isFocused &&
              props.theme.components.select.optionIsFocusBgColor,
          "&:hover": {
            color:
              !state.isSelected && props.theme.components.select.optionHover,
            backgroundColor:
              !state.isSelected &&
              props.theme.components.select.optionBgColorHover
          }
        }),
        indicatorSeparator: provided => ({
          ...provided,
          backgroundColor: props.theme.components.select.indicator
        }),
        clearIndicator: provided => ({
          ...provided,
          color: props.theme.components.select.clearIndicator
        }),
        menuList: provided => ({
          ...provided,
          backgroundColor: props.theme.components.select.menuListBgColor,
          color: props.theme.components.select.menuListColor,
          border: props.theme.components.select.menuListBorder
        }),
        placeholder: provided => ({
          ...provided,
          color: `${color}`,
          fontSize: props.theme.sizes.s
        }),
        noOptionsMessage: provided => ({
          ...provided,
          fontSize: "12px"
        }),
        indicatorsContainer: provided => ({
          ...provided,
          position: "relative"
        }),
        valueContainer: provided =>
          props.hasMultiCustomStyles
            ? {
                ...provided,
                overflow: "auto",
                padding: "0",
                margin: `0 0 0 ${props.theme.sizes.xs3}`,
                "&::-webkit-scrollbar": {
                  height: "0px"
                }
              }
            : {
                ...provided
              },

        multiValue: provided =>
          props.hasMultiCustomStyles
            ? {
                ...provided,
                minWidth: "none",
                backgroundColor: props.theme.components.select.grey5,
                padding: props.theme.sizes.xs3,
                "&:hover": {
                  backgroundColor: props.theme.components.select.grey10,
                  borderColor: props.theme.components.select.grey10,
                  div: {
                    backgroundColor: props.theme.components.select.grey10,
                    borderColor: props.theme.components.select.grey10,
                    svg: {
                      fill: props.theme.components.select.indicator,
                      "&:hover": {
                        fill: props.theme.components.select.grey60
                      }
                    }
                  }
                }
              }
            : {
                ...provided
              }
      }}
      theme={theme => ({
        ...theme,
        borderRadius: 0,
        colors: {
          ...theme.colors,
          primary25: props.theme.components.select.primary25,
          primary: props.theme.components.select.primary,
          primary50: props.theme.components.select.primary50
        }
      })}
    />
  );
}

SelectWithStyles.propTypes = {
  theme: PropTypes.object,
  variant: PropTypes.string,
  hasMultiCustomStyles: PropTypes.bool
};

export default withTheme(SelectWithStyles);
