import React from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
//Components
import { Text } from "../../../atoms/index";

const CandidatureCard = ({ children, className, titleColor, title, theme }) => {
  return (
    <div className={className} tabIndex={0}>
      <div className="content-container">
        <Text
          as="h2"
          text={title}
          size="large"
          color={
            theme.highContrast.text ||
            titleColor ||
            theme.components.candidatureCard.titleColor
          }
        />
        {children}
      </div>
    </div>
  );
};

const types = {
  children: PropTypes.node,
  className: PropTypes.string,
  buttonBgColor: PropTypes.string,
  titleColor: PropTypes.string,
  title: PropTypes.string,
  theme: PropTypes.object
};

const defaults = {
  titleColor: "#000",
  title: "Título"
};

CandidatureCard.propTypes = types;
CandidatureCard.defaultProps = defaults;

const CandidatureCardWithStyles = styled(CandidatureCard)`
  height: inherit;
  position: relative;
  background-color: ${props =>
    props.theme.components.candidatureCard.cardBackgroundColor};
  word-break: break-word;

  .content-container {
    padding: 20px;
    height: 100%;
    display: flex;
    flex-direction: column;
  }
`;

export default withTheme(CandidatureCardWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <CandidatureCardWithStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
