import React from "react";
import styled from "styled-components";
import PropTypes from "prop-types";

const Triangle = styled.div.attrs(() => ({
  "aria-hidden": "true"
}))`
  position: ${props => props.position};
  transform: ${props => props.transform};
  z-index: ${props => props.zIndex};
  right: ${props => props.right};
  top: ${props => props.top};
  left: ${props => props.left};
  bottom: ${props => props.bottom};
  width: 0;
  height: 0;
  border-left: ${props => props.size} solid transparent;
  border-right: ${props => props.size} solid transparent;
  border-bottom: ${props => props.size} solid
    ${props => props.color || props.theme.components.triangle.color};

  ${props =>
    props.withContent &&
    `
    &:before {
      content: "${props.content || props.theme.icons.plus}";
      font-size: ${props.contentSize};
      font-weight: ${props.contentFontWeight};
      color: ${props.contentColor ||
        props.theme.components.triangle.contentColor};
      height: ${props.size};
      width: calc(${props.size} * 2);
      top: calc(${props.size} / 2);
      right: ${props.size};
      background-color: transparent;
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      align-self: center;
    }
  `};

  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    border-left: ${props => props.XsSize || props.size} solid transparent;
    border-right: ${props => props.XsSize || props.size} solid transparent;
    border-bottom: ${props => props.XsSize || props.size} solid ${props =>
  props.color};
    top: ${props => props.XsTop};

    ${props =>
      props.withContent &&
      `
      &:before {
        font-size: ${props.XsContentSize || props.contentSize};
        height: ${props.XsSize || props.size};
        width: calc(${props.XsSize || props.size} * 2);
        top: calc(${props.XsSize || props.size} / 2);
        right: ${props.XsSize || props.size};
      }
  `};
`;

const types = {
  children: PropTypes.node,
  position: PropTypes.string,
  transform: PropTypes.string,
  zIndex: PropTypes.string,
  right: PropTypes.string,
  top: PropTypes.string,
  left: PropTypes.string,
  bottom: PropTypes.string,
  size: PropTypes.string,
  color: PropTypes.string,
  withContent: PropTypes.bool,
  content: PropTypes.string,
  contentSize: PropTypes.string,
  contentFontWeight: PropTypes.string,
  contentColor: PropTypes.string,
  XsSize: PropTypes.string,
  XsTop: PropTypes.string,
  XsContentSize: PropTypes.string
};

const defaults = {
  withContent: false,
  position: "absolute",
  contentFontWeight: "100",
  contentSize: "14px"
};

Triangle.propTypes = types;
Triangle.defaultProps = defaults;

export default Triangle;

//Only used for the Storybook Props Table
export const WithProps = ({ children, ...props }) => (
  <Triangle {...props}>{children}</Triangle>
);

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
