import React from "react";
import PropTypes from "prop-types";
import { ContentLoading } from "../../../molecules";
import styled, { withTheme } from "styled-components";
import { ContentContainer } from "../../../atoms";

const Loading = ({ className, maskBgColor, theme }) => {
  return (
    <div className={className}>
      <div className="container">
        <div className="row no-gutters">
          <div className="col-sm-6 col-12">
            <ContentContainer padding="70px 0">
              <ContentLoading maskBgColor={maskBgColor} />
            </ContentContainer>
          </div>
        </div>
      </div>
      <ContentContainer
        className="image-container"
        backgroundColor={theme.loadings.iconContainerBgColor}
      />
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  maskBgColor: PropTypes.string,
  theme: PropTypes.object
};

const LoadingWithStyles = styled(Loading)`
  position: relative;
  min-height: ${props => props.theme.components.subWebDepartment.height};

  .image-container {
    height: ${props => props.theme.components.subWebDepartment.height};
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;
  }

  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    .image-container {
      position: relative;
      width: 100%;
    }
  }
`;

export default withTheme(LoadingWithStyles);
