import React from "react";
import { withTheme } from "styled-components";
import PropTypes from "prop-types";
//Components
import { AnimatedBackground, ContentContainer } from "./../../../atoms";
import { PeopleCardLoading } from "./../../../molecules";

const Loading = ({ className }) => {
  return (
    <ContentContainer className={className} marginBottom="70px">
      <div className="container">
        <div className="row no-gutters">
          <div className="col-lg-3 col-12">
            <ContentContainer lgMarginBottom="30px">
              <AnimatedBackground height="30px" width="110px" />
            </ContentContainer>
          </div>
          <div className="col-lg col-12">
            <div className="row">
              <div className="col-lg-3 col-md-3 col-sm-4 col-6">
                <PeopleCardLoading />
              </div>
              <div className="col-lg-3 col-md-3 col-sm-4 col-6">
                <PeopleCardLoading />
              </div>
              <div className="col-lg-3 col-md-3 col-sm-4 col-6">
                <ContentContainer mobileXsMarginTop="70px">
                  <PeopleCardLoading />
                </ContentContainer>
              </div>
            </div>
          </div>
        </div>
        <ContentContainer className="row no-gutters" marginTop="110px">
          <div className="col-lg-3 col-12">
            <ContentContainer lgMarginBottom="30px">
              <AnimatedBackground height="30px" width="110px" />
            </ContentContainer>
          </div>
          <div className="col-lg col-12">
            <div className="row">
              <div className="col-lg-3 col-md-3 col-sm-4 col-6">
                <PeopleCardLoading />
              </div>
              <div className="col-lg-3 col-md-3 col-sm-4 col-6">
                <PeopleCardLoading />
              </div>
              <div className="col-lg-3 col-md-3 col-sm-4 col-6">
                <ContentContainer mobileXsMarginTop="70px">
                  <PeopleCardLoading />
                </ContentContainer>
              </div>
            </div>
          </div>
        </ContentContainer>
      </div>
    </ContentContainer>
  );
};

Loading.propTypes = {
  className: PropTypes.string
};

export default withTheme(Loading);
