import React, { Fragment } from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//components
import { Text, Section } from "./../../../atoms";
import {
  SectionHeader,
  BorderedCard,
  Select,
  Error
} from "./../../../molecules";
import Loading from "./Loading";
import { compose } from "../../../../utils/utilities";

const Card = ({ title, theme, data, url }) => {
  return (
    <BorderedCard
      borderColor={theme.components.subWebDepartmentCourses.card.borderColor}
      contentContainerPadding="20px 0"
      cardIsLink={false}
    >
      <SectionHeader
        href={url}
        hasContainer={false}
        hasLine={true}
        hasLink={true}
        textColor={theme.components.subWebDepartmentCourses.card.titleColor}
        fontSize="18px"
        marginBottom="20px"
        fontWeight="400"
      >
        {title}
      </SectionHeader>

      <div className="row no-gutters">
        {data.map((item, index) => {
          const isLastChild = data.length === index + 1;
          const props = isLastChild
            ? { marginBottom: "2px", mobileXsMarginTop: "10px" }
            : {};
          return (
            <div key={index} className={isLastChild ? "col-sm col-12" : "col"}>
              <Text
                size="article"
                color={theme.components.subWebDepartmentCourses.card.subTitle}
                marginBottom="2px"
                {...props}
              >
                {item.value}
              </Text>
              <Text size="article">{item.label}</Text>
            </div>
          );
        })}
      </div>
    </BorderedCard>
  );
};

Card.propTypes = {
  title: PropTypes.string,
  theme: PropTypes.object,
  data: PropTypes.object,
  url: PropTypes.string
};

Card.defaultProps = {
  title: "Título",
  scientificArea: "Texto",
  regime: "Texto",
  semesters: "Texto",
  time: "Texto"
};

const DepartmentCourses = ({
  className,
  theme,
  isLoading,
  hasError,
  data,
  lang,
  filters,
  onChangeFilter
}) => {
  return (
    <Section className={className} hasHighContrastBorderTop={false}>
      {isLoading ? (
        <Loading theme={theme} />
      ) : hasError ? (
        <Error />
      ) : (
        <Fragment>
          <div className="container">
            <div className="row no-gutters">
              <div className="offset-xl-3 col-xl-7 col">
                <div className="row no-gutters">
                  <div className="col-xl-4 col-lg-6 col-md-4 col-sm-4 col-12">
                    <Text
                      as="h2"
                      size="xLarge"
                      fontWeight="500"
                      mobileXsMarginBottom="40px"
                    >
                      Cursos
                    </Text>
                  </div>
                  <div className="col d-flex flex-column justify-content-center">
                    <div className="row">
                      {filters.map((res, index) => {
                        return (
                          <div key={index} className="col">
                            <Select
                              variant="black"
                              onChange={e => onChangeFilter(e, res.jsonField)}
                              //isLoading={channels.length <= 0}
                              defaultValue={res.defaultValues}
                              options={res.options}
                              placeholder={res.title}
                              label={res.title}
                              isClearable
                            />
                          </div>
                        );
                      })}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="container">
            <div className="row no-gutters">
              <div className="offset-xl-3 col-xl-7 col">
                {Object.keys(data).length <= 0 ? (
                  <Error>Sem conteúdos para o filtro.</Error>
                ) : (
                  data
                    .filter(result => result.list.length > 0)
                    .map(section => {
                      return (
                        <div key={section.title}>
                          <Text
                            as="h3"
                            size="large"
                            fontWeight="400"
                            marginBottom="30px"
                            marginTop="50px"
                            color={
                              theme.components.subWebDepartmentCourses
                                .subTitleColor
                            }
                          >
                            {section.title}
                            {section.subtitle && (
                              <Text
                                as="span"
                                size="large"
                                fontWeight="200"
                                marginLeft="10px"
                                color={
                                  theme.components.subWebDepartmentCourses
                                    .subTitleColor
                                }
                              >
                                {section.subtitle}
                              </Text>
                            )}
                          </Text>

                          {section.list.map(item => {
                            return (
                              <Card
                                url={`/${lang}/${
                                  lang === "pt" ? "curso" : "course"
                                }/${item.id}`}
                                key={item.id}
                                title={item.name}
                                theme={theme}
                                data={item.list}
                              />
                            );
                          })}
                        </div>
                      );
                    })
                )}
              </div>
            </div>
          </div>
        </Fragment>
      )}
    </Section>
  );
};

const types = {
  className: PropTypes.string,
  theme: PropTypes.object,
  isLoading: PropTypes.bool,
  hasError: PropTypes.bool,
  data: PropTypes.object,
  lang: PropTypes.string,
  filters: PropTypes.array,
  onChangeFilter: PropTypes.func
};

DepartmentCourses.propTypes = types;

const DepartmentCoursesWithStyles = styled(DepartmentCourses)``;

export default compose(withTheme)(DepartmentCoursesWithStyles);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => (
  <DepartmentCoursesWithStyles {...props} />
);

WithProps.propTypes = types;
