import React, { useContext } from "react";
import PropTypes from "prop-types";
//Components
import {
  Text,
  Section,
  ContentContainer,
  VerticalList,
  ListItem
} from "../../atoms/index";
import { withTheme } from "styled-components";
import { ConfigContext } from "../../../context/ConfigContext";

const Departments = ({
  title,
  titleSize,
  titleWeight,
  description,
  textWeight,
  textSize,
  textLineHeight,
  className,
  resource,
  subTitle,
  polytechnics,
  data,
  theme
}) => {
  const { link: Link, formattedMessage: FormattedMessage } = useContext(
    ConfigContext
  );
  return (
    <Section
      className={className}
      backgroundColor={theme.components.departments.backgroundColor}
      hasHighContrastBorderTop={false}
    >
      <div className="container">
        <div className="row no-gutters">
          <div className="offset-xl-3 col-xl-7 offset-lg-4 col-lg col-12">
            <Text
              as="h1"
              fontWeight="500"
              size="xLarge"
              text={title}
              marginBottom="40px"
              firstLetter="capitalize"
            >
              {resource.title ? (
                <FormattedMessage id={resource.title} defaultMessage={title} />
              ) : (
                title
              )}
            </Text>
            <Text
              as="h2"
              size="large"
              color={theme.components.departments.titleColor}
              fontWeight={titleWeight}
              marginBottom="10px"
              firstLetter="uppercase"
              fontSize={titleSize}
            >
              {resource.subTitle ? (
                <FormattedMessage
                  id={resource.subTitle}
                  defaultMessage={subTitle}
                />
              ) : (
                subTitle
              )}
            </Text>

            <ContentContainer>
              <Text
                fontWeight={textWeight}
                marginBottom="15px"
                fontSize={textSize}
                lineHeight={textLineHeight}
              >
                {resource.description ? (
                  <FormattedMessage
                    id={resource.description}
                    defaultMessage={description}
                  />
                ) : (
                  description
                )}
              </Text>
            </ContentContainer>

            <ContentContainer
              marginTop="40px"
              marginRight="0"
              marginBottom="80px"
              mobileXsMarginRight="0"
            >
              {/*Lista de Departamentos*/}
              <VerticalList>
                {data.departments.map((item, index) => {
                  return (
                    <ListItem
                      key={index}
                      paddingTop="10px"
                      paddingBottom="10px"
                      borderBottom={`1px solid ${theme.components.departments.borderColor}`}
                      borderTop={
                        index === 0
                          ? `1px solid ${theme.components.departments.borderColor}`
                          : "none"
                      }
                    >
                      <Link
                        color={theme.components.departments.linkColor}
                        href={item.url}
                        fontSize="14px"
                        fontWeight="300"
                        aria-label={item.text}
                      >
                        {item.text}
                      </Link>
                    </ListItem>
                  );
                })}
              </VerticalList>
            </ContentContainer>
            <Text
              as="h2"
              size="large"
              color={theme.components.departments.titleColor}
              fontWeight={titleWeight}
              marginBottom="10px"
              firstLetter="uppercase"
              fontSize={titleSize}
            >
              {resource.polytechnics ? (
                <FormattedMessage
                  id={resource.polytechnics}
                  defaultMessage={polytechnics}
                />
              ) : (
                polytechnics
              )}
            </Text>
            {data.politec.description.map((item, index) => {
              return (
                <ContentContainer key={index} mobileMdDisplay="none">
                  <Text
                    as="h2"
                    size="small"
                    text={item}
                    fontWeight={textWeight}
                    marginBottom="15px"
                    fontSize={textSize}
                    lineHeight={textLineHeight}
                  />
                </ContentContainer>
              );
            })}
            <ContentContainer
              marginTop="40px"
              marginRight="0"
              mobileXsMarginRight="0"
            >
              {/*Lista de Politecnicos*/}
              <VerticalList>
                {data.politec.data.map((item, index) => {
                  return (
                    <ListItem
                      key={index}
                      paddingTop="10px"
                      paddingBottom="10px"
                      borderBottom={`1px solid ${theme.components.departments.borderColor}`}
                      borderTop={
                        index === 0
                          ? `1px solid ${theme.components.departments.borderColor}`
                          : "none"
                      }
                    >
                      <Link
                        color={theme.components.departments.linkColor}
                        href={item.url}
                        fontSize="14px"
                        fontWeight="300"
                        aria-label={item.text}
                      >
                        {item.text}
                      </Link>
                    </ListItem>
                  );
                })}
              </VerticalList>
            </ContentContainer>
          </div>
        </div>
      </div>
    </Section>
  );
};

const defaults = {
  title: "Title",
  subTitle: "SubTitle",
  description: "Description",
  data: {},
  polytechnics: "Another title"
};

const types = {
  title: PropTypes.string,
  subTitle: PropTypes.string,
  resource: PropTypes.object,
  titleColor: PropTypes.string,
  titleSize: PropTypes.string,
  titleWeight: PropTypes.string,
  description: PropTypes.string,
  data: PropTypes.object,
  textWeight: PropTypes.string,
  textSize: PropTypes.string,
  textLineHeight: PropTypes.string,
  className: PropTypes.string,
  polytechnics: PropTypes.string,
  theme: PropTypes.object
};

Departments.propTypes = types;
Departments.defaultProps = defaults;

export default withTheme(Departments);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => <Departments {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
