import React from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../../atoms";
import { withTheme } from "styled-components";

const Mask = ({ backgroundColor, ...props }) => {
  return (
    <ContentContainer
      backgroundColor={backgroundColor || props.theme.loadings.maskBgColor}
      position="absolute"
      {...props}
    />
  );
};

Mask.propTypes = {
  backgroundColor: PropTypes.string
};

const Loading = ({ borderBottom, ...props }) => {
  return (
    <ContentContainer
      height="139px"
      borderTop={`1px solid ${props.theme.loadings.cardBorderColor}`}
      padding="20px 0"
      borderBottom={borderBottom}
    >
      <div className="row">
        <div className="col-auto">
          <ContentContainer padding="10px 0" width="93px">
            <div className="row no-gutters">
              <div className="col-auto">
                <ContentContainer
                  width="24px"
                  height="100%"
                  display="flex"
                  justifyContent="center"
                  alignItems="center"
                >
                  <ContentContainer
                    height="16px"
                    width="8px"
                    backgroundColor={
                      props.theme.loadings.agendaCard.miniSliderColor
                    }
                  />
                </ContentContainer>
              </div>
              <div className="col">
                <ContentContainer
                  display="flex"
                  alignItems="center"
                  flexDirection="column"
                >
                  <ContentContainer
                    height="15px"
                    width="30px"
                    backgroundColor={
                      props.theme.loadings.agendaCard.miniSliderColor
                    }
                  />
                  <ContentContainer
                    height="15px"
                    width="30px"
                    backgroundColor={
                      props.theme.loadings.agendaCard.miniSliderColor
                    }
                    marginTop="5px"
                  />
                </ContentContainer>
              </div>
              <div className="col-auto">
                <ContentContainer
                  width="24px"
                  height="100%"
                  display="flex"
                  justifyContent="center"
                  alignItems="center"
                >
                  <ContentContainer
                    height="16px"
                    width="8px"
                    backgroundColor={
                      props.theme.loadings.agendaCard.miniSliderColor
                    }
                  />
                </ContentContainer>
              </div>
            </div>
          </ContentContainer>
        </div>
        <div className="col">
          <AnimatedBackground height="97px">
            <Mask {...props} height="15px" right="0" width="50%" />
            <Mask {...props} height="6px" top="15px" width="100%" />
            <Mask {...props} height="15px" top="21px" right="0" width="40%" />
            <Mask {...props} height="25px" top="36px" width="100%" />

            <Mask {...props} height="15px" top="61px" right="0" width="50%" />
            <Mask {...props} height="6px" top="76px" width="100%" />
            <Mask {...props} height="15px" top="82px" right="0" width="60%" />
          </AnimatedBackground>
        </div>
      </div>
    </ContentContainer>
  );
};

Loading.propTypes = {
  borderBottom: PropTypes.string
};

export default withTheme(Loading);
