import React from "react";
import PropTypes from "prop-types";
//Styles
import styled from "styled-components";
import theme from "../../../styles/theme.semantic";

const ListItem = styled.li`
  width: ${props => props.width};
  margin-top: ${props => props.marginTop};
  margin-bottom: ${props => props.marginBottom};
  margin-right: ${props => props.marginRight};
  margin-left: ${props => props.marginLeft};
  padding-right: ${props => props.paddingRight};
  padding-left: ${props => props.paddingLeft};
  padding-top: ${props => props.paddingTop};
  padding-bottom: ${props => props.paddingBottom};
  border-top: ${props => props.borderTop};
  border-bottom: ${props => props.borderBottom};
  border-right: ${props => props.borderRight};
  border-left: ${props => props.borderLeft};
  line-height: ${props => props.lineHeight};
  height: ${props => props.height};
  vertical-align: ${props => props.verticalAlign};

  ${props =>
    props.separationLine
      ? `
    border-right: 1px solid ${props.lineColor ||
      props.theme.components.listItem.lineColor};
    padding: ${props.padding || "0 12px 0 12px"};
    &:last-child{
      padding-right: 0;
      border-right: none;
    }
    &:first-child{
      padding-left: 0;
    }
    `
      : null};
`;

const types = {
  width: PropTypes.string,
  marginTop: PropTypes.string,
  marginBottom: PropTypes.string,
  marginRight: PropTypes.string,
  marginLeft: PropTypes.string,
  paddingRight: PropTypes.string,
  paddingLeft: PropTypes.string,
  paddingTop: PropTypes.string,
  paddingBottom: PropTypes.string,
  borderTop: PropTypes.string,
  borderBottom: PropTypes.string,
  borderRight: PropTypes.string,
  borderLeft: PropTypes.string,
  lineHeight: PropTypes.string,
  height: PropTypes.string,
  verticalAlign: PropTypes.string,
  separationLine: PropTypes.bool,
  lineColor: PropTypes.string,
  padding: PropTypes.string,
  children: PropTypes.node
};

const defaults = {
  separationLine: false,
  lineColor: theme.components.listItem.lineColor,
  padding: "0 12px 0 12px"
};

ListItem.propTypes = types;
ListItem.defaultProps = defaults;

export default ListItem;

//Only used for the Storybook Props Table
export const WithProps = ({ children, ...props }) => (
  <ListItem {...props}>{children}</ListItem>
);

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
