import React from "react";
import PropTypes from "prop-types";
import { withTheme } from "styled-components";
import { ContentLoading, SelectLoading } from "../../../molecules";
import { AnimatedBackground, ContentContainer } from "../../../atoms";

const Selects = ({ ...theme }) => {
  return (
    <ContentContainer
      height="42px"
      marginBottom="54px"
      className="d-flex flex-column justify-content-center"
      mobileXsHeight="auto"
    >
      <div className="row no-gutters">
        <div className="col-xl-4 col-lg-6 col-md-4 col-sm-4 col-12 d-flex align-items-center">
          <ContentContainer width="80%" mobileXsMarginBottom="40px">
            <AnimatedBackground height="30px" />
          </ContentContainer>
        </div>
        <div className="col">
          <div className="row">
            <div className="col">
              <SelectLoading {...theme} />
            </div>
            <div className="col">
              <SelectLoading {...theme} />
            </div>
          </div>
        </div>
      </div>
    </ContentContainer>
  );
};

const Card = ({ borderBottom, ...theme }) => {
  return (
    <ContentContainer
      borderTop={`1px solid ${theme.loadings.lightBorderColor}`}
      borderBottom={borderBottom}
      padding="20px 0"
      height="140px"
    >
      <ContentLoading fullSize={false} />
    </ContentContainer>
  );
};

Card.propTypes = {
  borderBottom: PropTypes.string
};

const Loading = ({ className, theme }) => {
  return (
    <div className={className}>
      <div className="container">
        <div className="row no-gutters">
          <div className="offset-xl-3 col-xl-7 col">
            <Selects {...theme} />
            <ContentContainer width="100px" marginBottom="33px">
              <AnimatedBackground height="26px" />
            </ContentContainer>
            <Card {...theme} />
            <Card {...theme} />
            <Card {...theme} />
            <Card
              {...theme}
              borderBottom={`1px solid ${theme.loadings.lightBorderColor}`}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  theme: PropTypes.object
};

export default withTheme(Loading);
