import React from "react";
import styled, { withTheme } from "styled-components";
import PropTypes from "prop-types";
import { AnimatedBackground, Icon, ContentContainer } from "../../../atoms";

const Mask = props => {
  return (
    <ContentContainer
      backgroundColor={props.theme.loadings.bgColor}
      position="absolute"
      {...props}
    />
  );
};

const LoadingCard = ({ ...props }) => {
  return (
    <ContentContainer
      height="105px"
      backgroundColor={props.theme.loadings.bgColor}
    >
      <div className="container">
        <div className="triangle">&nbsp;</div>
        <AnimatedBackground height="105px">
          <Mask height="20px" right="0" width="100%" {...props} />
          <Mask height="20px" top="20px" right="0" width="85%" {...props} />
          <Mask height="15px" top="40px" width="100%" {...props} />
          <Mask height="30px" top="55px" right="0" width="70%" {...props} />
          <Mask height="20px" top="85px" right="0" width="100%" {...props} />
        </AnimatedBackground>
      </div>
    </ContentContainer>
  );
};

const Loading = ({ className, ...props }) => {
  return (
    <div className={`${className} d-flex flex-column`}>
      <div className="d-flex justify-content-center align-items-center flex-grow-1">
        {props.isVideo && (
          <Icon
            name={props.theme.icons.youtube_play}
            color={props.theme.loadings.iconColor}
            size="xxxLarge"
          />
        )}
      </div>
      {!props.isVideo && <LoadingCard {...props} />}
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  theme: PropTypes.object,
  isVideo: PropTypes.bool
};

const LoadingWithStyles = styled(Loading)`
  height: ${props => props.theme.components.header.height};
  max-height: ${props => props.theme.components.header.maxHeight};
  background-color: ${props => props.theme.loadings.iconContainerBgColor};
  .container {
    position: relative;
  }
  .triangle {
    width: 60px;
    height: 60px;
    position: absolute;
    right: 25px;
    background-color: ${props => props.theme.loadings.bgColor};
    top: -30px;
    z-index: 2;
    transform: rotate(45deg);
  }

  /* Medium devices (tablets, less than 992px)*/
  @media (max-width: 991px) {
    height: ${props => props.theme.components.header.mobileHeight};
  }
`;

export default withTheme(LoadingWithStyles);
