import React from "react";
import PropTypes from "prop-types";
//Styles
import styled from "styled-components";
import theme from "../../../styles/theme.semantic";

const ImageContainer = styled.div`
  min-width: ${props => props.minWidth || ""};
  height: ${props => props.height};
  width: ${props => props.width};
  background-image: url(${props => (props.url === "" ? "" : props.url)});
  background-color: ${props => props.backgroundColor};
  background-size: ${props => props.backgroundSize};
  -webkit-background-size: ${props => props.backgroundSize};
  -moz-background-size: ${props => props.backgroundSize};
  -o-background-size: ${props => props.backgroundSize};
  background-position: ${props => props.backgroundPosition};
  background-repeat: ${props => props.backgroundRepeat};
  background-attachment: ${props => props.backgroundAttachment};
  overflow: ${props => props.overflow};
  // Medium devices (tablets, less than 992px)
  @media (max-width: 991px) {
    height: ${props => props.mdHeight};
  }
`;

const types = {
  width: PropTypes.string,
  height: PropTypes.string,
  url: PropTypes.string,
  backgroundColor: PropTypes.string,
  backgroundSize: PropTypes.string,
  backgroundPosition: PropTypes.string,
  backgroundRepeat: PropTypes.string,
  backgroundAttachment: PropTypes.string,
  children: PropTypes.node
};

const defaults = {
  height: "auto",
  width: "auto",
  url: "",
  backgroundColor: theme.components.picture.backgroundColor,
  backgroundSize: "cover",
  backgroundPosition: "center 40%",
  backgroundRepeat: "no-repeat",
  backgroundAttachment: "initial"
};

ImageContainer.propTypes = types;

ImageContainer.defaultProps = defaults;

export default ImageContainer;

//Only used for the Storybook Props Table
export const WithProps = ({ children, ...props }) => (
  <ImageContainer {...props}>{children}</ImageContainer>
);

WithProps.propTypes = types;

WithProps.defaultProps = defaults;
