import React from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../../atoms";
import { withTheme } from "styled-components";

const Mask = ({ MaskBgColor, ...props }) => {
  return (
    <ContentContainer
      backgroundColor={MaskBgColor || props.theme.loadings.maskBgColor}
      position="absolute"
      {...props}
      className="loading-mask"
    />
  );
};

const Loading = ({ CardBgColor, ...props }) => {
  return (
    <ContentContainer
      height="398px"
      backgroundColor={CardBgColor || props.theme.loadings.cardBgColor}
      className="loading-card d-flex flex-column justify-content-between"
      padding="30px 20px 20px 0"
    >
      <ContentContainer marginLeft="20px">
        <AnimatedBackground height="108px">
          <Mask
            height="26px"
            right="0"
            width="15%"
            MaskBgColor={CardBgColor}
            {...props}
          />
          <Mask
            height="15px"
            top="26px"
            width="100%"
            MaskBgColor={CardBgColor}
            {...props}
          />
          <Mask
            height="26px"
            top="41px"
            right="0"
            width="10%"
            MaskBgColor={CardBgColor}
            {...props}
          />
          <Mask
            height="15px"
            top="67px"
            width="100%"
            MaskBgColor={CardBgColor}
            {...props}
          />
          <Mask
            height="26px"
            top="82px"
            right="0"
            width="80%"
            MaskBgColor={CardBgColor}
            {...props}
          />
        </AnimatedBackground>
      </ContentContainer>
      <div className="row no-gutters">
        <div className="col-auto">
          <ContentContainer
            width="115px"
            height="115px"
            backgroundColor={props.theme.loadings.iconContainerBgColor}
            marginRight="20px"
          />
        </div>
        <div className="col">
          <AnimatedBackground height="35px">
            <Mask
              height="15px"
              right="0"
              width="15%"
              MaskBgColor={CardBgColor}
              {...props}
            />
            <Mask
              height="5px"
              top="15px"
              width="100%"
              MaskBgColor={CardBgColor}
              {...props}
            />
            <Mask
              height="15px"
              top="20px"
              right="0"
              width="25%"
              MaskBgColor={CardBgColor}
              {...props}
            />
          </AnimatedBackground>
        </div>
      </div>
    </ContentContainer>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  CardBgColor: PropTypes.string
};

export default withTheme(Loading);
