import React from "react";
import PropTypes from "prop-types";
//Components
import {
  ScreenReaderText,
  Text,
  Title,
  ContentContainer,
  Section
} from "../../atoms/index";
import { withTheme } from "styled-components";
//Context
import { useConfig } from "../../../context/ConfigContext";

const Facts = ({
  className,
  resource,
  backgroundColor,
  source,
  text,
  titleStyles,
  title,
  hasSource,
  theme
}) => {
  const { formattedMessage: FormattedMessage } = useConfig();
  return (
    <Section
      backgroundColor={theme.highContrast.backgroundColor || backgroundColor}
      paddingTop="107px"
      paddingBottom="107px"
      className={className}
    >
      <ScreenReaderText>
        <Title text="Citação" />
      </ScreenReaderText>
      <div className="container">
        <div className="row">
          <div className="col-12">
            <Text
              tabIndex={0}
              fontFamily="'Miller Display', 'Playfair Display', 'Georgia', serif"
              {...titleStyles}
              lineHeight="1.6"
              color={theme.highContrast.text || titleStyles.color}
            >
              {resource ? (
                <FormattedMessage id={resource} defaultMessage={title} />
              ) : (
                title
              )}
            </Text>
          </div>
        </div>
        {hasSource && (
          <ContentContainer marginTop="65px">
            <div className="row">
              <div className="col-12">
                <Text
                  tabIndex={0}
                  text={source}
                  fontWeight="500"
                  fontSize="16px"
                  lineHeight="1.6"
                  marginBottom="5px"
                />
                <Text
                  tabIndex={0}
                  text={text}
                  textTransform="uppercase"
                  fontSize="16px"
                  lineHeight="1.6"
                />
              </div>
            </div>
          </ContentContainer>
        )}
      </div>
    </Section>
  );
};

Facts.defaultProps = {
  text: "Text",
  source: "Source",
  title: "Content",
  hasSource: true
};

Facts.propTypes = {
  text: PropTypes.string,
  backgroundColor: PropTypes.string,
  source: PropTypes.string,
  titleStyles: PropTypes.object,
  className: PropTypes.string,
  title: PropTypes.string,
  resource: PropTypes.string,
  hasSource: PropTypes.bool,
  theme: PropTypes.object
};

export default withTheme(Facts);
