import React, { Fragment } from "react";
import { Section } from "../../atoms";
import { SectionHeader, MarkdownRenderer } from "../../molecules";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";

const MultiColumns = ({
  content,
  data,
  title,
  hasLine,
  href,
  backgroundColor,
  isThemeReverse,
  columns,
  activeColor,
  titleColor,
  theme,
  externalLink
}) => {
  return (
    <Fragment>
      <Section
        backgroundColor={theme.highContrast.backgroundColor || backgroundColor}
      >
        {title && (
          <SectionHeader
            title={title}
            hasLine={hasLine}
            hasLink={hasLine}
            textColor={theme.highContrast.link || titleColor}
            href={href}
            externalLink={externalLink}
          />
        )}
        <div className="container">
          <div className="row">
            {content && (
              <Content className="col-xl col-lg col-md-12" columns={columns}>
                <MarkdownRenderer
                  content={content}
                  isThemeReverse={isThemeReverse}
                  activeColor={activeColor}
                />
              </Content>
            )}
            {data &&
              data.map(({ title, content, href }, index) => (
                <Content
                  className="col-xl-6 col-md-12"
                  key={index}
                  columns={columns}
                  isArray
                >
                  {title && (
                    <SectionHeader
                      hasContainer={false}
                      title={title}
                      hasLine={!!href}
                      hasLink={!!href}
                      textColor={theme.highContrast.link || titleColor}
                      href={href}
                    />
                  )}
                  <MarkdownRenderer
                    content={content}
                    isThemeReverse={isThemeReverse}
                    className="stick-bottom"
                    activeColor={activeColor}
                  />
                </Content>
              ))}
          </div>
        </div>
      </Section>
    </Fragment>
  );
};

MultiColumns.defaultProps = {
  backgroundColor: "#fff",
  titleColor: "#000",
  href: null,
  columns: 2,
  content: "",
  isThemeReverse: false,
  activeColor: null,
  externalLink: false
};

MultiColumns.propTypes = {
  title: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
  backgroundColor: PropTypes.string,
  columns: PropTypes.number,
  href: PropTypes.string,
  content: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
  isThemeReverse: PropTypes.bool,
  data: PropTypes.oneOfType([PropTypes.array, PropTypes.object]),
  activeColor: PropTypes.string,
  hasLine: PropTypes.bool,
  titleColor: PropTypes.string,
  theme: PropTypes.object,
  externalLink: PropTypes.string
};

const Content = styled.div`
  column-count: ${props => props.columns || 2};

  @media (max-width: 767px) {
    column-count: 1;
  }
  ${props =>
    props.isArray &&
    `
    display: flex;
    flex-direction: column;
  `}
`;

export default withTheme(MultiColumns);
