import React from "react";
import PropTypes from "prop-types";
//Styles
import styled from "styled-components";
import { Text } from "../../atoms";
import {
  Accordion as StyledAccordion,
  AccordionItem,
  AccordionItemTitle,
  AccordionItemBody
} from "react-accessible-accordion";
import theme from "../../../styles/theme.semantic";
//Context
import { useConfig } from "../../../context/ConfigContext";

const AccordionTitleStyles = ({
  children,
  className,
  hasLinks,
  baseLinkUrl,
  accordionTitleHasLink,
  color,
  native
}) => {
  const { link: Link } = useConfig();
  return (
    <div className={`${className} u-position-relative`}>
      {accordionTitleHasLink ? (
        <Link
          href={baseLinkUrl}
          color={color}
          size="mediumSmall"
          fontDecoration="none"
          fontWeight="400"
          lineHeight="1.4"
          native={native}
        >
          {children}
        </Link>
      ) : (
        <Text
          color={theme.highContrast.text || color}
          size="mediumSmall"
          fontWeight="400"
          lineHeight="1.4"
        >
          {children}
        </Text>
      )}

      {hasLinks && <div className="accordion__arrow" role="presentation" />}
    </div>
  );
};

AccordionTitleStyles.defaultProps = {
  hasLinks: true,
  accordionTitleHasLink: true,
  native: false
};

AccordionTitleStyles.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  accordionTitleHasLink: PropTypes.bool,
  baseLinkUrl: PropTypes.string,
  color: PropTypes.string,
  native: PropTypes.bool,
  hasLinks: PropTypes.oneOfType([
    PropTypes.bool,
    PropTypes.instanceOf("undefined")
  ])
};

const AccordionTitle = styled(AccordionTitleStyles)`
  color: ${props => props.color};
  text-transform: ${props => props.uppercase};
  font-size: ${props => props.fontSize};
  font-weight: ${props => props.fontWeight};
  text-decoration: none;
`;

AccordionTitle.propTypes = {
  color: PropTypes.string,
  uppercase: PropTypes.string,
  fontSize: PropTypes.string,
  fontWeight: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
};

AccordionTitle.defaultProps = {
  color: theme.components.accessibleAccordion.color,
  textTransform: "uppercase",
  fontSize: "14px",
  fontWeight: 300
};

const Accordion = styled(StyledAccordion)`
  .u-position-absolute {
    position: absolute;
  }

  .u-position-relative {
    position: relative;
  }

  .accordion {
    border: 1px solid rgba(0, 0, 0, 0.1);
    border-radius: 2px;
  }

  .accordion__item + .accordion__item {
    border-top: 1px solid rgba(0, 0, 0, 0.1);
  }

  .accordion__item--has-icon {
    position: relative;
  }

  .accordion__title {
    cursor: pointer;
    padding: 5px 0;
    width: 100%;
    text-align: left;
    border: none;
    outline: none;
    margin-bottom: 5px;
  }

  // Medium devices (tablets, less than 992px)
  @media (max-width: 991px) {
    .accordion__title {
      padding: 10px 0;
    }
  }

  .accordion__body {
    margin: 0 0 12px 0;
    display: block;
    animation: fadein 0.35s ease-in;
  }

  /*
  .accordion__body li {
    margin-bottom: 5px;
  }
  */

  .accordion__body--hidden {
    display: none;
    opacity: 0;
    animation: fadein 0.35s ease-in;
  }

  .accordion__title > *:last-child,
  .accordion__body > *:last-child {
    margin-bottom: 0;
  }

  .accordion__arrow {
    display: inline-block;
    width: 24px;
    height: 12px;
    position: absolute;
    top: 50%;
    right: 0;
    margin-top: -6px;
  }

  .accordion__arrow::after,
  .accordion__arrow::before {
    display: block;
    position: absolute;
    top: 50%;
    width: 10px;
    height: 2px;
    background-color: ${props =>
      props.theme.components.accessibleAccordion.arrowColor};
    border-radius: 2px;
    content: "";
  }

  .accordion__arrow::before {
    left: 4px;
    transform: rotate(45deg);
  }

  [aria-expanded="true"] .accordion__arrow::before,
  [aria-selected="true"] .accordion__arrow::before {
    transform: rotate(-45deg);
  }

  .accordion__arrow::after {
    right: 4px;
    transform: rotate(-45deg);
  }

  [aria-expanded="true"] .accordion__arrow::after,
  [aria-selected="true"] .accordion__arrow::after {
    transform: rotate(45deg);
  }

  .accordion__arrow::before,
  .accordion__arrow::after {
    transition: transform 0.25s ease, -webkit-transform 0.25s ease;
  }

  /* -------------------------------------------------- */
  /* ---------------- Animation part ------------------ */
  /* -------------------------------------------------- */

  @keyframes fadein {
    0% {
      opacity: 0;
    }

    100% {
      opacity: 1;
    }
  }

  @keyframes move-down {
    0% {
      transform: translateY(0);
    }
    10% {
      transform: translateY(0);
    }
    20% {
      transform: translateY(5px);
    }
    30% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(0);
    }
  }

  @keyframes move-up {
    0% {
      transform: translateY(0);
    }
    10% {
      transform: translateY(0);
    }
    20% {
      transform: translateY(-5px);
    }
    30% {
      transform: translateY(0);
    }
    100% {
      transform: translateY(0);
    }
  }

  .accordion__title--animated:hover .accordion__arrow {
    animation-name: move-down;
    animation-duration: 1.5s;
  }

  .accordion__title--animated[aria-expanded="true"]:hover .accordion__arrow {
    animation-name: move-up;
    animation-duration: 1.5s;
  }
`;

export {
  Accordion,
  AccordionItem,
  AccordionItemTitle,
  AccordionItemBody,
  AccordionTitle
};

//Only used for the Storybook Props Table
export const WithProps = props => <div {...props} />;

WithProps.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  baseLinkUrl: PropTypes.string,
  color: PropTypes.string,
  hasLinks: PropTypes.oneOfType([
    PropTypes.bool,
    PropTypes.instanceOf("undefined")
  ]),
  uppercase: PropTypes.string,
  fontSize: PropTypes.string,
  fontWeight: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  accordionTitleHasLink: PropTypes.bool
};
WithProps.defaultProps = {
  hasLinks: true,
  color: theme.components.accessibleAccordion.color,
  textTransform: "uppercase",
  fontSize: "14px",
  fontWeight: 300,
  accordionTitleHasLink: true
};
