import React from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
//Components
import { Text, ImageContainer, LinkExternal, Link } from "../../../atoms";
import ImageWrapper from "../../../organisms/ImageWrapper";

const StudentCardLink = ({
  className,
  text,
  src,
  alt,
  imageBgColor,
  name,
  about,
  onFocus,
  onKeyDown,
  theme,
  href,
  isEditor
}) => {
  const isInternal = href ? href.includes("ua.pt") : false;

  const renderContent = () => {
    return (
      <div className="student-card-link">
        <div className="row">
          <div className="col">
            <div className="text-container">
              <Text text={text} lineHeight="30px" size="mediumLarge" />
            </div>
            <div className="image-name-container d-flex ">
              <ImageContainer
                height="96px"
                width="96px"
                minWidth="96px"
                maxWidth="96px"
                overflow="hidden"
                backgroundColor={
                  imageBgColor || theme.components.picture.backgroundColor
                }
                className="student-card-link__image"
              >
                <ImageWrapper
                  absoluteSize="120"
                  backgroundColor={
                    imageBgColor || theme.components.picture.backgroundColor
                  }
                  data={{ url: src }}
                  alt={alt}
                  height="100%"
                  width="100%"
                  endpoint={() => null}
                  hasEndpoint={false}
                />
              </ImageContainer>
              <div className="col pl-0 pr-0">
                <div className="name-container">
                  <Text size="small" wordBreak="break-word">
                    {name}
                  </Text>
                  <Text size="small" wordBreak="break-word">
                    {about}
                  </Text>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  };
  return (
    <div className={className} onFocus={onFocus} onKeyDown={onKeyDown}>
      {isEditor ? (
        <div className="editor-links">{renderContent()}</div>
      ) : isInternal ? (
        <Link fontDecoration="none" href={href} hasHover={false}>
          {renderContent()}
        </Link>
      ) : (
        <LinkExternal
          textDecoration="none"
          href={href}
          isHover={false}
          hasPop={false}
        >
          {renderContent()}
        </LinkExternal>
      )}
    </div>
  );
};

const types = {
  className: PropTypes.string,
  text: PropTypes.string,
  name: PropTypes.string,
  about: PropTypes.string,
  src: PropTypes.string,
  alt: PropTypes.string,
  imageBgColor: PropTypes.string,
  onFocus: PropTypes.func,
  onKeyDown: PropTypes.func,
  theme: PropTypes.object,
  href: PropTypes.string,
  isEditor: PropTypes.bool
};

const defaults = {
  text: "Texto",
  name: "Nome",
  about: "Aluno",
  onFocus: () => null,
  onKeyDown: () => null,
  hasColAuto: true
};

StudentCardLink.propTypes = types;
StudentCardLink.defaultProps = defaults;

const StudentCardLinkWithStyles = styled(StudentCardLink)`
  .student-card-link {
    background-color: ${props => props.backgroundColor};
    user-select: text;
    padding: 18px;
    margin: 2px 0;
    border: 1px solid
      ${props => props.theme.components.studentCardLink.borderColor};
    .text-container {
      height: 176px;
      overflow: hidden;
      margin-bottom: 40px;
    }

    img {
      -webkit-transition: all 0.4s;
      -moz-transition: all 0.4s;
      -o-transition: all 0.4s;
      transition: all 0.4s;
    }
    &:hover {
      border: 1px solid
        ${props => props.theme.components.studentCardLink.borderColorHover};

      img {
        -ms-transform: scale(1.1);
        -moz-transform: scale(1.1);
        -webkit-transform: scale(1.1);
        -o-transform: scale(1.1);
        transform: scale(1.1);
      }
    }
    &:active {
      outline: 2px solid
        ${props => props.theme.components.studentCardLink.borderColorPressed};
    }
    .name-container {
      padding: 0 0 0 18px;
      max-height: 87px;
      overflow: hidden;
    }
  }
`;

export default withTheme(StudentCardLinkWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <StudentCardLinkWithStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
