import React, { useState, useContext } from "react";
import PropTypes from "prop-types";
//Components
import Link from "../components/atoms/Link";
import { FormattedMessage } from "react-intl";
import { useFormatMessage } from "../hooks";

export const ConfigContext = React.createContext({});

export const useConfig = () => useContext(ConfigContext);

export const ConfigProvider = ({ children, config, components, hooks }) => {
  const initialState = { ...config, ...components, ...hooks };
  const [state] = useState(initialState);

  return (
    <ConfigContext.Provider value={{ ...state }}>
      {children}
    </ConfigContext.Provider>
  );
};

ConfigProvider.defaultProps = {
  config: {
    imageAPI: "https://api-assets.dev.ua.pt/v1",
    portalWWW: "https://www.dev.ua.pt",
    portalAPI: "https://api-portal.dev.ua.pt/api/v1"
  },
  components: {
    link: Link,
    formattedMessage: FormattedMessage,
    isIE: () => false
  },
  hooks: {
    useIntl: useFormatMessage
  }
};

ConfigProvider.propTypes = {
  children: PropTypes.node.isRequired,
  components: PropTypes.shape({
    link: PropTypes.any,
    formattedMessage: PropTypes.any,
    isIE: PropTypes.func
  }),
  hooks: PropTypes.shape({
    useIntl: PropTypes.any
  }),
  config: PropTypes.shape({
    imageAPI: PropTypes.string,
    portalWWW: PropTypes.string,
    portalAPI: PropTypes.string
  }).isRequired
};
