import React from "react";
import PropTypes from "prop-types";
//Styles
import styled from "styled-components";

const Image = styled.img.attrs(({ className, src, alt }) => ({
  src: src,
  className: className,
  alt: alt
}))`
  width: ${props => props.width || "auto"};
  height: ${props => props.height || "auto"};
  max-height: ${props => props.maxHeight};
  min-height: ${props => props.minHeight};
  object-fit: ${props => props.objectFit};
  border-radius: ${props => props.borderRadius};
`;

const types = {
  src: PropTypes.string.isRequired,
  alt: PropTypes.string,
  width: PropTypes.string,
  height: PropTypes.string,
  borderRadius: PropTypes.string,
  objectFit: PropTypes.string,
  minHeight: PropTypes.string,
  children: PropTypes.node
};

const defaults = {
  src: "",
  alt: "image",
  objectFit: "cover"
};

Image.propTypes = types;

Image.defaultProps = defaults;

export default Image;

//Only used for the Storybook Props Table
export const WithProps = ({ children, ...props }) => (
  <Image {...props}>{children}</Image>
);

WithProps.propTypes = types;

WithProps.defaultProps = defaults;
