import React from "react";
import PropTypes from "prop-types";
//Styles
import styled from "styled-components";
import theme from "../../../styles/theme.semantic";

function Tab({ className, name, title, children }) {
  return (
    <div className={`tab ${className}`}>
      <input id={name} alt={name} type="checkbox" name="tabs" />
      <label htmlFor={name}>{title}</label>
      {children}
    </div>
  );
}

Tab.propTypes = {
  className: PropTypes.string,
  name: PropTypes.string,
  title: PropTypes.string,
  children: PropTypes.node
};

Tab.defaultProps = {
  name: "Nome",
  title: "Título"
};

const TabStyles = styled(Tab)`
  input {
    position: absolute;
    opacity: 0;
    z-index: -1;
  }
  label {
    opacity: 0.8;
    cursor: pointer;
    position: relative;
    display: block;
    color: ${props => props.color};
    background-color: ${props => props.backgroundColor};
    font-weight: ${props => props.fontWeight};
    font-size: ${props => props.fontSize};
    padding-top: 5px;
    padding-bottom: 5px;
    :hover,
    :active {
      background-color: ${props => props.backgroundColor};
    }
    ${props => props.uppercase && "text-transform: uppercase"};
  }
  /* :checked */
  input:checked ~ .tab-content {
    max-height: 10em;
    padding-bottom: 12px;
  }
  /* Icon */
  label::after {
    position: absolute;
    right: 0;
    top: 0;
    display: block;
    padding-top: 5px;
    padding-bottom: 5px;
    text-align: center;
    -webkit-transition: all 0.2s;
    -o-transition: all 0.2s;
    transition: all 0.2s;
    cursor: pointer;
  }
  input[type="checkbox"] + label::after {
    font-family: entypo;
    content: "\\e764";
  }
  input[type="radio"] + label::after {
    font-family: entypo;
    content: "\\e764";
  }
  input[type="checkbox"]:checked + label::after {
    transform: rotate(180deg);
  }
  input[type="radio"]:checked + label::after {
    transform: rotateX(180deg);
  }
`;

const defaults = {
  color: theme.components.tab.color,
  backgroundColor: theme.components.tab.backgroundColor,
  uppercase: false,
  fontSize: "inherit",
  fontWeight: 400
};

const types = {
  color: PropTypes.string,
  backgroundColor: PropTypes.string,
  uppercase: PropTypes.bool,
  fontSize: PropTypes.string,
  fontWeight: PropTypes.number
};

TabStyles.propTypes = types;
TabStyles.defaultProps = defaults;

export default TabStyles;

//Only used for the Storybook Props Table
export const WithProps = props => <TabStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
