import React from "react";
import styled, { withTheme } from "styled-components";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../../atoms";

const Mask = props => {
  return (
    <ContentContainer
      backgroundColor={props.theme.loadings.bgColor}
      position="absolute"
      {...props}
    />
  );
};

Mask.propTypes = {
  theme: PropTypes.string
};

const LoadingCard = ({ ...props }) => {
  return (
    <AnimatedBackground height="105px">
      <Mask height="25px" top="0" right="0" width="10%" {...props} />
      <Mask height="15px" top="25px" width="100%" {...props} />
      <Mask height="25px" top="40px" right="0" width="15%" {...props} />
      <Mask height="15px" top="65px" width="100%" {...props} />
      <Mask height="25px" top="80px" right="0" width="20%" {...props} />
    </AnimatedBackground>
  );
};

const LoadingWarning = ({ ...props }) => {
  return (
    <AnimatedBackground height="70px">
      <Mask height="15px" top="0" right="0" width="10%" {...props} />
      <Mask height="20px" top="15px" width="100%" {...props} />
      <Mask height="15px" top="35px" right="0" width="15%" {...props} />
      <Mask height="5px" top="50px" width="100%" {...props} />
      <Mask height="15px" top="55px" right="0" width="20%" {...props} />
    </AnimatedBackground>
  );
};

const SubWebLoading = ({ className, ...props }) => {
  return (
    <div
      className={`${className} d-flex align-items-center justify-content-center`}
    >
      <div className="content-container d-flex flex-column">
        <div className="text-container d-flex flex-column flex-grow-1">
          <div className="triangle-container d-flex align-items-center">
            <div className="triangle">&nbsp;</div>
          </div>
          <LoadingCard {...props} />
        </div>
        <div className="warning">
          <LoadingWarning
            backgroundColor={props.theme.loadings.iconColor}
            {...props}
          />
        </div>
      </div>
    </div>
  );
};

SubWebLoading.propTypes = {
  className: PropTypes.string,
  theme: PropTypes.object
};

const SubWebLoadingWithStyles = styled(SubWebLoading)`
  height: ${props => props.theme.components.header.subWeb.height};
  max-height: ${props => props.theme.components.header.maxHeight};
  background-color: ${props => props.theme.loadings.iconContainerBgColor};
  position: relative;

  .content-container {
    height: 100%;
    position: absolute;
    top: 0;
    right: 0;
    width: 350px;
    background-color: ${props => props.theme.loadings.bgColor};

    .text-container,
    .warning {
      padding: 30px;
    }

    .warning {
      min-height: 175px;
      background-color: ${props => props.theme.loadings.iconColor};
    }

    .text-container {
      position: relative;
      .triangle-container {
        position: absolute;
        left: -30px;
        height: 100%;
        top: 0;
        z-index: 0;
        .triangle {
          width: 60px;
          height: 60px;
          background-color: ${props => props.theme.loadings.bgColor};
          z-index: 2;
          transform: rotate(45deg);
        }
      }
    }
  }

  /* Medium devices (tablets, less than 992px)*/
  @media (max-width: 991px) {
    height: ${props => props.theme.components.header.subWeb.mobileHeight};
  }

  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    .content-container {
      height: auto;
      position: absolute;
      top: initial;
      bottom: 0;
      right: 0;
      width: 100%;

      .warning {
        min-height: initial;
      }

      .text-container {
        flex-grow: initial;
        .triangle-container {
          left: 0;
          height: auto;
          top: -30px;
          right: 0;
          justify-content: center;
        }
      }
    }
  }
`;

export default withTheme(SubWebLoadingWithStyles);
