import React, { Fragment } from "react";
import styled from "styled-components";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../atoms";
import theme from "../../../styles/theme.semantic";

const Mask = ({ maskBgColor, ...props }) => {
  return (
    <ContentContainer
      backgroundColor={maskBgColor || theme.loadings.bgColor}
      position="absolute"
      {...props}
    />
  );
};

Mask.propTypes = {
  maskBgColor: PropTypes.string
};

const LoadingCard = ({ fullSize, ...props }) => {
  return (
    <AnimatedBackground height={fullSize ? "110px" : "80px"}>
      <Mask height="20px" right="0" width="10%" {...props} />
      <Mask height="10px" top="20px" width="100%" {...props} />
      <Mask height="20px" top="30px" right="0" width="20%" {...props} />
      <Mask height="10px" top="50px" width="100%" {...props} />
      <Mask height="20px" top="60px" right="0" width="30%" {...props} />
      {fullSize && (
        <Fragment>
          <Mask height="10px" top="80px" width="100%" {...props} />
          <Mask height="20px" top="90px" right="0" width="30%" {...props} />
        </Fragment>
      )}
    </AnimatedBackground>
  );
};

LoadingCard.propTypes = {
  fullSize: PropTypes.bool
};

LoadingCard.defaultProps = {
  fullSize: true
};

const Loading = ({ className, ...props }) => {
  return (
    <div className={className}>
      <LoadingCard {...props} />
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string
};

const LoadingWithStyles = styled(Loading)`
  max-width: 800px;
`;

export default LoadingWithStyles;

//Only used for the Storybook Props Table
export const WithProps = props => <LoadingWithStyles {...props} />;

WithProps.propTypes = {
  className: PropTypes.string,
  maskBgColor: PropTypes.string,
  fullSize: PropTypes.bool
};

WithProps.defaultProps = {
  fullSize: true
};
