import React, { Fragment } from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//components
import { Text, Button, Picture, Section } from "./../../../atoms";
import { SectionHeader, Error } from "./../../../molecules";
import Loading from "./Loading";
//Context
import { useConfig } from "../../../../context/ConfigContext";

const Department = ({ className, isLoading, hasError, data, theme }) => {
  const { formattedMessage: FormattedMessage } = useConfig();
  return (
    <Section
      className={className}
      paddingTop="0"
      hasBottomSpacing={false}
      backgroundColor={theme.components.subWebDepartment.backgroundColor}
    >
      {isLoading ? (
        <Loading
          maskBgColor={theme.components.subWebDepartment.backgroundColor}
        />
      ) : hasError ? (
        <Error />
      ) : (
        <Fragment>
          <div className="container">
            <div className="row no-gutters">
              <div className="col-sm-6 col-12">
                <div className="content-container">
                  <SectionHeader
                    hasContainer={false}
                    hasLine={false}
                    hasLink={false}
                    textColor={theme.components.subWebDepartment.titleColor}
                  >
                    {data.title}
                  </SectionHeader>
                  <Text size="small" marginBottom="50px">
                    {data.description}
                  </Text>
                  <Button
                    as="a"
                    href={data.link}
                    textDecoration="none"
                    variant="default"
                    backgroundColor={
                      theme.components.subWebDepartment.backgroundColor
                    }
                  >
                    <FormattedMessage
                      id="app.organism.button.knowMore"
                      defaultMessage="Saber mais"
                    />
                  </Button>
                </div>
              </div>
            </div>
          </div>
          <Picture
            className="dptGE"
            src={data.imageUrl}
            alt={data.title}
            isLocal={false}
            hasEndpoint={false}
            height="100%"
          />
        </Fragment>
      )}
    </Section>
  );
};

const types = {
  className: PropTypes.string,
  isLoading: PropTypes.bool,
  hasError: PropTypes.bool,
  data: PropTypes.object,
  theme: PropTypes.object
};

const defaults = {
  isLoading: false
};

Department.propTypes = types;
Department.defaultProps = defaults;

const DepartmentWithStyles = styled(Department)`
  position: relative;
  min-height: ${props => props.theme.components.subWebDepartment.height};

  .content-container {
    padding: 70px 30px 70px 0;
  }

  //Department Image
  .dptGE {
    position: absolute;
    top: 0;
    right: 0;
    width: 50%;

    img {
      min-height: ${props => props.theme.components.subWebDepartment.height};
    }
  }

  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    .content-container {
      padding: 70px 0;
    }
    //Department Image
    .dptGE {
      position: relative;
      width: 100%;
    }
  }
`;

export default withTheme(DepartmentWithStyles);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => <DepartmentWithStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
