import React from "react";
//Styles
import styled, { withTheme } from "styled-components";
import PropTypes from "prop-types";
//Components
import { Button } from "../../atoms/index";
//Context
import { useConfig } from "../../../context/ConfigContext";

const Placeholder = ({ className, onHandlePlay }) => {
  const { formattedMessage: FormattedMessage } = useConfig();
  return (
    <div className={className}>
      {/*Placeholder*/}
      <div className="noughts-crosses">
        <div className="board-row">
          <button className="square">&nbsp;</button>
          <button className="square">&nbsp;</button>
          <button className="square">&nbsp;</button>
        </div>

        <div className="board-row">
          <button className="square">4</button>
          <button className="square">0</button>
          <button className="square">&nbsp;</button>
        </div>

        <div className="board-row">
          <button className="square">&nbsp;</button>
          <button className="square">&nbsp;</button>
          <button className="square">4</button>
        </div>

        <div className="play-button-container">
          <Button variant="default" onClick={onHandlePlay}>
            <FormattedMessage
              id="app.journal.errorHandler.button.play"
              defaultMessage="Jogar"
            />
          </Button>
        </div>
      </div>
    </div>
  );
};

Placeholder.propTypes = {
  className: PropTypes.string,
  onHandlePlay: PropTypes.func
};

const PlaceholderWithStyles = styled(Placeholder)`
  .noughts-crosses {
    position: relative;

    //Hover fade in Play button
    &:hover {
      .play-button-container {
        opacity: 1;
      }
    }

    //Button Container
    .play-button-container {
      position: absolute;
      left: 0;
      top: 0;
      height: 100%;
      width: 100%;
      background-color: ${props =>
        props.theme.components.errorHandler.playButtonContainerBgColor};
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      opacity: 0;
      -webkit-transition: opacity 0.5s ease-in-out;
      transition: opacity 0.5s ease-in-out;
    }

    .board-row:after {
      clear: both;
      content: "";
      display: table;
    }

    //Square
    .square {
      background: transparent;
      color: ${props => props.theme.components.errorHandler.color};
      border: none;
      float: left;
      font-size: ${props => props.theme.components.errorHandler.gameFontSize};
      font-weight: ${props =>
        props.theme.components.errorHandler.gameFontWeight};
      line-height: 34px;
      height: ${props => props.theme.components.errorHandler.gameSquareSize};
      width: ${props => props.theme.components.errorHandler.gameSquareSize};
      padding: 0;
      text-align: center;
      font-family: Roboto;
      &:focus {
        outline: none;
      }
    }

    .board-row {
      &:nth-child(2) {
        border-bottom: ${props =>
            props.theme.components.errorHandler.gameBorderSize}
          solid ${props => props.theme.components.errorHandler.borderColor};
        border-top: ${props =>
            props.theme.components.errorHandler.gameBorderSize}
          solid ${props => props.theme.components.errorHandler.borderColor};
      }
      .square {
        &:nth-child(2) {
          border-right: ${props =>
              props.theme.components.errorHandler.gameBorderSize}
            solid ${props => props.theme.components.errorHandler.borderColor};
          border-left: ${props =>
              props.theme.components.errorHandler.gameBorderSize}
            solid ${props => props.theme.components.errorHandler.borderColor};
        }
      }
    }
  }
  /*
  -- Adjust Square Sizes for mobile
  */
  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    .noughts-crosses {
      .square {
        height: ${props =>
          props.theme.components.errorHandler.gameSquareMobileSize};
        width: ${props =>
          props.theme.components.errorHandler.gameSquareMobileSize};
      }
    }
  }
  // Extra small devices (portrait phones, less than 300px)
  @media (max-width: 300px) {
    .noughts-crosses {
      .square {
        height: ${props =>
          props.theme.components.errorHandler.gameSquareXXXSmallMobileSize};
        width: ${props =>
          props.theme.components.errorHandler.gameSquareXXXSmallMobileSize};
      }
    }
  }
`;

export default withTheme(PlaceholderWithStyles);
