import React from "react";
import PropTypes from "prop-types";
import styled, { css } from "styled-components";
import DOMPurify from "dompurify";

const defaultStyles = css`
  font-size: ${props => props.theme.sizes.sm};
  color: ${props => props.theme.components.injectHtml.default.color};
  line-height: 1.6;
  font-weight: 300;
  text-transform: none;
  word-break: break-word;
  ul {
    list-style-type: none;
  }
`;

const presentationStyles = css`
  font-size: ${props => props.theme.sizes.sm};
  color: ${props => props.theme.components.injectHtml.presentationStyles.color};
  line-height: 1.6;
  font-weight: 300;
  p,
  ul {
    margin-bottom: 10px;
  }

  ol {
    list-style-position: inside;
    text-indent: -16px;
    padding-left: 16px;
  }

  ul {
    list-style-type: none;
    padding-left: 15px;
    li {
      transform: translateX(10px);
      position: relative;
      &:before {
        content: "";
        display: inline-block;
        vertical-align: middle;
        width: 2px;
        height: 2px;
        background-color: ${props =>
          props.theme.components.injectHtml.presentationStyles.before};
        transform: translateX(-10px);
        border-radius: 50%;
        position: absolute;
        top: 9px;
      }
    }
  }
`;

const lightStyles = css`
  color: ${props => props.theme.components.injectHtml.lightStyles.color};
  font-size: ${props => props.theme.sizes.sm} !important;
  strong,
  span {
    font-weight: 400 !important;
  }
`;

function Modifier(variant) {
  switch (variant) {
    case "presentation":
      return presentationStyles;
    case "light":
      return lightStyles;
    default:
      return defaultStyles;
  }
}

function InjectHtml({ content, className }) {
  return (
    <div
      className={className}
      dangerouslySetInnerHTML={{
        __html: DOMPurify.sanitize(content)
      }}
    />
  );
}

InjectHtml.propTypes = {
  content: PropTypes.any,
  className: PropTypes.string
};

export default styled(InjectHtml)`
  ${props =>
    props.variant !== "removeStyles" &&
    css`
      strong,
      span {
        font-weight: 300 !important;
      }
      span {
        font-family: Roboto, sans-serif !important;
        font-size: ${props => props.theme.sizes.sm} !important;
        color: ${props => props.theme.components.injectHtml.span} !important;
        line-height: 1.6 !important;
      }
      a {
        color: ${props => props.theme.components.injectHtml.link.color};
        text-decoration: underline;
        cursor: pointer;
        &:hover {
          color: ${props => props.theme.components.injectHtml.link.hoverColor};
        }
      }
      ${Modifier(props.variant)}
    `}
`;
