/* eslint-disable no-unused-vars */
import React, { useMemo } from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
//utils
import { useConfig } from "../../../context/ConfigContext";

const breakpoints = {
  names: ["xs", "sm", "md", "lg"],
  mediaQueries: {
    xs: 576,
    sm: 768,
    md: 992,
    lg: 1200
  },
  sizes: {
    lg: {
      xs: 768,
      sm: 992,
      md: 1200,
      lg: 1920
    },
    md: {
      xs: 576,
      sm: 768,
      md: 992,
      lg: 1200
    },
    sm: {
      xs: 432,
      sm: 576,
      md: 768,
      lg: 992
    },
    xs: {
      xs: 324,
      sm: 432,
      md: 576,
      lg: 768
    }
  }
};

function isEmpty(str) {
  return !str || 0 === str.length;
}

function Picture({
  src,
  className,
  alt,
  size,
  useHeight,
  absoluteSize,
  onCatchError,
  hasResize,
  ...props
}) {
  const { imageAPI, portalWWW } = useConfig();

  const { sizes } = breakpoints;

  const onHandleSrc = size => {
    const param = useHeight ? "height" : "width";

    if (!isEmpty(src)) {
      if (!hasResize) return src;

      if (src.includes("http")) {
        return `${imageAPI}/image/resizer?imageUrl=${encodeURIComponent(
          src
        )}&${param}=${size}`;
      } else {
        return `${imageAPI}/image/resizer?imageUrl=${portalWWW}/${encodeURIComponent(
          src
        )}&${param}=${size}`;
      }
    }
  };

  // When the absoluteSize prop is defined it uses that size for the image query
  const isAbsolute = useMemo(() => !!absoluteSize, [absoluteSize]);
  const defaultSrc = useMemo(() => {
    if (!isAbsolute) return onHandleSrc(sizes[size].lg);
    return onHandleSrc(absoluteSize);
  }, [isAbsolute, src]);

  const pictureProps = {
    height: props.height,
    onClick: props.onClick
  };

  const onError = e => {
    e.target.style.display = "none";
    onCatchError(e);
  };

  return (
    <div className={className}>
      <picture>
        {!isAbsolute &&
          breakpoints.names.map(breakpoint => (
            <source
              key={breakpoint}
              media={`(max-width: ${breakpoints.mediaQueries[breakpoint]}px)`}
              srcSet={onHandleSrc(sizes[size][breakpoint])}
              {...pictureProps}
            />
          ))}
        <img src={defaultSrc} alt={alt} onError={onError} {...pictureProps} />
      </picture>
    </div>
  );
}

Picture.defaultProps = {
  backgroundColor: "transparent",
  backgroundSize: "fill",
  imagePosition: "center",
  size: "md",
  useHeight: false,
  absoluteSize: null,
  onCatchError: () => null,
  hasResize: true
};

Picture.propTypes = {
  src: PropTypes.string.isRequired,
  alt: PropTypes.string,
  imagePosition: PropTypes.string,
  size: PropTypes.oneOf(["xs", "sm", "md", "lg"]),
  useHeight: PropTypes.bool,
  absoluteSize: PropTypes.string,
  height: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  onClick: PropTypes.func,
  className: PropTypes.string,
  onCatchError: PropTypes.func,
  hasResize: PropTypes.bool
};

const PictureWithStyles = styled(Picture)`
  ${props =>
    props.hasImageBgColor &&
    `
  background: ${props.theme.highContrast.lightBackgroundColor ||
    props.backgroundColor ||
    props.theme.components.picture.backgroundColor};

  `};
  height: ${props => props.height};
  color: ${props => props.theme.components.picture.color};
  picture {
    display: flex;
    align-items: flex-start;
    height: 100%;
    width: ${props => props.width || "100%"};
    max-width: ${props => props.maxWidth};
  }
  img {
    object-fit: ${props => props.backgroundSize || "cover"};
    object-position: ${props => props.imagePosition};
    width: ${props => props.width || "100%"};
    max-width: ${props => props.maxWidth};
    max-height: ${props => props.maxHeight};
    height: ${props => props.height};
  }
  // Small devices (landscape phones, less than 768px)
  @media (max-width: 767px) {
    img {
      height: ${props => props.smHeight};
    }
  }
  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    img {
      height: ${props => props.xsHeight};
    }
  }
`;

PictureWithStyles.defaultProps = {
  hasImageBgColor: true
};

const PictureContainer = props => {
  const { isIE } = useConfig();

  return <PictureWithStyles isIE={isIE} {...props} />;
};

export default PictureContainer;
