import React from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../../atoms";
import { withTheme } from "styled-components";

const Mask = ({ backgroundColor, ...props }) => {
  return (
    <ContentContainer
      backgroundColor={backgroundColor || props.theme.loadings.maskBgColor}
      position="absolute"
      {...props}
    />
  );
};

Mask.propTypes = {
  backgroundColor: PropTypes.string
};

const Loading = ({ mdMarginBottom, ...props }) => {
  return (
    <ContentContainer mdMarginBottom={mdMarginBottom}>
      <div className="row no-gutters">
        <div className="col-auto">
          <ContentContainer
            width="120px"
            height="120px"
            backgroundColor={
              props.theme.loadings.agendaFeaturedCard.dateContainerBgColor
            }
            position="relative"
            className="d-flex align-items-center"
          >
            <ContentContainer
              height="30px"
              width="30px"
              transform="rotate(45deg)"
              backgroundColor={props.backgroundColor}
              position="absolute"
              right="-16px"
              zIndex="2"
            />
          </ContentContainer>
        </div>
        <div className="col">
          <ContentContainer
            border={`1px solid ${props.theme.loadings.agendaFeaturedCard.borderColor}`}
            padding="30px"
          >
            <AnimatedBackground height="100px">
              <Mask {...props} height="15px" right="0" width="40%" />
              <Mask {...props} height="10px" top="15px" width="100%" />
              <Mask {...props} height="22px" top="25px" right="0" width="20%" />
              <Mask {...props} height="6px" top="47px" width="100%" />
              <Mask {...props} height="22px" top="53px" right="0" width="30%" />
              <Mask {...props} height="10px" top="75px" width="100%" />
              <Mask {...props} height="15px" top="85px" right="0" width="60%" />
            </AnimatedBackground>
          </ContentContainer>
        </div>
      </div>
    </ContentContainer>
  );
};

Loading.propTypes = {
  backgroundColor: PropTypes.string,
  mdMarginBottom: PropTypes.string
};

export default withTheme(Loading);
