import React from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//components
import { Section, ContentContainer } from "./../../../atoms";
import { SectionHeader, ShowMoreCard, Error } from "./../../../molecules";
import Loading from "./Loading";

const Container = styled.div`
  @media (min-width: 576px) {
    .row {
      margin-left: -25px;
      margin-right: -25px;
    }

    [class^="col-"] {
      padding-right: 25px;
      padding-left: 25px;
    }
  }
`;

const Courses = ({
  className,
  theme,
  isLoading,
  hasError,
  data,
  title,
  href,
  activeColor
}) => {
  return (
    <Section
      className={className}
      paddingBottom="20px"
      smPaddingBottom="55px"
      backgroundColor={
        theme.highContrast.backgroundColor ||
        activeColor ||
        theme.components.subWebCourses.backgroundColor
      }
    >
      <SectionHeader
        href={href}
        hasContainer={true}
        hasLine={true}
        hasLink={true}
        textColor={theme.components.subWebCourses.titleColor}
      >
        {title}
      </SectionHeader>
      {isLoading ? (
        <Loading />
      ) : hasError ? (
        <Error />
      ) : (
        <Container className="container">
          <div className="row">
            {data.map((course, index) => {
              return (
                <div key={index} className="col-md-4 col-6">
                  <ContentContainer
                    height="100%"
                    padding="0 0 50px 0"
                    mobileSmPadding="0 0 15px 0"
                  >
                    <ShowMoreCard
                      title={course.info.value}
                      href={course.extra}
                      titleHoverColor={activeColor}
                    />
                  </ContentContainer>
                </div>
              );
            })}
          </div>
        </Container>
      )}
    </Section>
  );
};

const types = {
  className: PropTypes.string,
  theme: PropTypes.object,
  isLoading: PropTypes.bool,
  hasError: PropTypes.bool,
  data: PropTypes.object,
  title: PropTypes.string.isRequired,
  activeColor: PropTypes.string,
  href: PropTypes.string
};

const defaults = {
  title: "Cursos",
  href: "/"
};

Courses.defaultProps = defaults;
Courses.propTypes = types;

export default withTheme(Courses);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => <Courses {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
