import React, { Fragment, memo } from "react";
import PropTypes from "prop-types";
//Components
import { Text, Section, ContentContainer } from "../../atoms";
import {
  SectionHeader,
  AgendaCard,
  AgendaFeatured,
  Error
} from "../../molecules";
import Loading from "./Loading";
//Utils
import moment from "moment";
//locales
import { withSize } from "react-sizeme";
import { compose } from "../../../utils/utilities";
import { withTheme } from "styled-components";
//Context
import { useConfig } from "../../../context/ConfigContext";

const Agenda = memo(function Agenda({
  className,
  showHeader,
  title,
  resource,
  backgroundColor,
  eventStyles,
  hasError,
  href,
  loading,
  data,
  size,
  theme
}) {
  const { formattedMessage: FormattedMessage } = useConfig();
  const onRenderNews = (data, start = 0, end = 3) => {
    return data.listEvents
      .filter((items, index) => index >= start && index <= end && items)
      .map((item, index) => {
        const isSameDay = moment(item.startDate).isSame(item.endDate, "day");

        const highContrastBgColor = theme.highContrast.backgroundColor;
        const agendaCardTextColor = theme.components.agenda.agendaCardTextColor;
        const highContrastTextColor = theme.highContrast.text;
        const highContrastActionColor = theme.highContrast.action;
        return (
          <AgendaCard
            key={index}
            textColorHover={highContrastBgColor || backgroundColor}
            iconColorHover={highContrastBgColor || backgroundColor}
            squareColorHover={
              highContrastActionColor ||
              theme.components.agenda.squareColorHover
            }
            buttonOutlineColorHover={highContrastBgColor || backgroundColor}
            href={item.url}
            category={item.category}
            title={item.title}
            text={item.local}
            date={
              isSameDay
                ? `${moment(item.startDate).format("DD MMMM YYYY")}`
                : `${moment(item.startDate).format("DD MMMM")} - ${moment(
                    item.endDate
                  ).format("DD MMMM YYYY")}`
            }
            hasDateSlider={!isSameDay}
            iconColor={highContrastActionColor || agendaCardTextColor}
            categoryColor={agendaCardTextColor}
            titleColor={agendaCardTextColor}
            textColor={highContrastTextColor || agendaCardTextColor}
          >
            {!isSameDay ? (
              <Fragment>
                {/* Se tiver slider */}
                <div className="initial-date">
                  <Text
                    fontSize="28px"
                    text={moment(item.startDate, "YYYY/MM/DD").format("DD")}
                    color={highContrastTextColor || eventStyles.date.textColor}
                    fontWeight="900"
                    lineHeight="1"
                  />
                  <Text
                    fontSize="18px"
                    text={moment(item.startDate, "YYYY/MM/DD").format("MMM")}
                    color={highContrastTextColor || eventStyles.date.textColor}
                    textTransform="uppercase"
                    lineHeight="1"
                    aria-label={moment(item.startDate, "YYYY/MM/DD").format(
                      "MMMM"
                    )}
                  />
                </div>
                <div className="today">
                  <Text
                    fontSize="28px"
                    text={moment(new Date(), "YYYY/MM/DD").format("DD")}
                    color={highContrastTextColor || eventStyles.date.textColor}
                    fontWeight="900"
                    lineHeight="1"
                  />
                  <Text
                    fontSize="18px"
                    text={moment(new Date(), "YYYY/MM/DD").format("MMM")}
                    color={highContrastTextColor || eventStyles.date.textColor}
                    textTransform="uppercase"
                    lineHeight="1"
                    aria-label={moment(new Date(), "YYYY/MM/DD").format("MMMM")}
                  />
                </div>
                <div className="end-date">
                  <Text
                    fontSize="28px"
                    text={moment(item.endDate, "YYYY/MM/DD").format("DD")}
                    color={highContrastTextColor || eventStyles.date.textColor}
                    fontWeight="900"
                    lineHeight="1"
                  />
                  <Text
                    fontSize="18px"
                    text={moment(item.endDate, "YYYY/MM/DD").format("MMM")}
                    color={highContrastTextColor || eventStyles.date.textColor}
                    textTransform="uppercase"
                    lineHeight="1"
                    aria-label={moment(item.endDate, "YYYY/MM/DD").format(
                      "MMMM"
                    )}
                  />
                </div>
              </Fragment>
            ) : (
              <Fragment>
                <div className="no-slider">
                  <Text
                    fontSize="28px"
                    text={moment(item.startDate, "YYYY/MM/DD").format("DD")}
                    color={highContrastTextColor || eventStyles.date.textColor}
                    fontWeight="900"
                    lineHeight="1"
                  />
                  <Text
                    fontSize="18px"
                    text={moment(item.startDate, "YYYY/MM/DD").format("MMM")}
                    color={highContrastTextColor || eventStyles.date.textColor}
                    textTransform="uppercase"
                    lineHeight="1"
                    aria-label={moment(item.startDate, "YYYY/MM/DD").format(
                      "MMMM"
                    )}
                  />
                </div>
              </Fragment>
            )}
          </AgendaCard>
        );
      });
  };

  const AgendaNewsColumn = () => {
    return (
      <div className="col-xl-6 col-lg-6 col-md-12 col-12 order-lg-first order-last">
        {onRenderNews(data)}
      </div>
    );
  };

  const AgendaFeaturedColumn = () => {
    const agendaFeaturedTextColor =
      theme.components.agenda.agendaFeaturedTextColor;
    const highContrastBgColor = theme.highContrast.backgroundColor;
    return (
      <div className="col-xl-6 col-lg-6 col-md-12 col-12 order-lg-last order-first">
        {Object.entries(data.listEventsHighlight).length > 0 ? (
          <ContentContainer mdMarginBottom="50px">
            <AgendaFeatured
              href={data.listEventsHighlight[0].url}
              dateContainerBgColor={
                theme.components.agenda.agendaFeaturedDateContainerBgColor
              }
              dateContainerTextColor={highContrastBgColor || backgroundColor}
              buttonBgColor={highContrastBgColor || backgroundColor}
              animationBgColor={highContrastBgColor || backgroundColor}
              plusButtonHoverBgColor={highContrastBgColor || backgroundColor}
              textColorHover={agendaFeaturedTextColor}
              buttonTextColor={agendaFeaturedTextColor}
              textColor={agendaFeaturedTextColor}
              titleColor={agendaFeaturedTextColor}
              borderColor="#fff"
              dateContainerDay={moment(
                data.listEventsHighlight[0].startDate,
                "YYYY/MM/DD"
              ).format("DD")}
              dateContainerMonth={moment(
                data.listEventsHighlight[0].startDate,
                "YYYY/MM/DD"
              ).format("MMM")}
              //Aria-Label
              dateContainerMonthAriaLabel={moment(
                data.listEventsHighlight[0].startDate,
                "YYYY/MM/DD"
              ).format("MMMM")}
              text={data.listEventsHighlight[0].local}
              title={data.listEventsHighlight[0].title}
              date={
                moment(data.listEventsHighlight[0].startDate).isSame(
                  data.listEventsHighlight[0].endDate,
                  "day"
                )
                  ? `${moment(data.listEventsHighlight[0].startDate).format(
                      "DD MMMM YYYY"
                    )}`
                  : `${moment(data.listEventsHighlight[0].startDate).format(
                      "DD MMMM"
                    )} - ${moment(data.listEventsHighlight[0].endDate).format(
                      "DD MMMM YYYY"
                    )}`
              }
            />
          </ContentContainer>
        ) : (
          onRenderNews(data, 4, 8)
        )}
      </div>
    );
  };
  // TODO Corrigir endereço href no json
  return (
    <Section
      className={className}
      backgroundColor={theme.highContrast.backgroundColor || backgroundColor}
    >
      {showHeader ? (
        <SectionHeader
          textColor={theme.components.agenda.titleColor}
          href={href}
        >
          {resource ? (
            <FormattedMessage id={resource} defaultMessage={title} />
          ) : (
            title
          )}
        </SectionHeader>
      ) : null}
      <div className="container">
        {loading ? (
          <Loading
            backgroundColor={
              theme.highContrast.backgroundColor || backgroundColor
            }
          />
        ) : hasError ? (
          <Error />
        ) : (
          <Fragment>
            <div className="row">
              {/*Change Columns order for design & screen reader*/}
              {size.width >= 992 ? (
                <Fragment>
                  <AgendaNewsColumn />
                  <AgendaFeaturedColumn />
                </Fragment>
              ) : (
                <Fragment>
                  <AgendaFeaturedColumn />
                  <AgendaNewsColumn />
                </Fragment>
              )}
            </div>
          </Fragment>
        )}
      </div>
    </Section>
  );
});

const defaults = {
  showHeader: true,
  title: "Title",
  data: []
};

const types = {
  className: PropTypes.string,
  showHeader: PropTypes.bool,
  title: PropTypes.string,
  textColor: PropTypes.string,
  cardBorderColor: PropTypes.string,
  eventStyles: PropTypes.object.isRequired,
  backgroundColor: PropTypes.string,
  resource: PropTypes.string,
  hasError: PropTypes.bool,
  href: PropTypes.string,
  loading: PropTypes.bool,
  data: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),
  size: PropTypes.object,
  theme: PropTypes.object
};

Agenda.propTypes = types;
Agenda.defaultProps = defaults;

export default compose(withSize(), withTheme)(Agenda);

//Only used for the Storybook Props Table
export const WithProps = props => <Agenda {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
