import React from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../atoms";
import { withTheme } from "styled-components";

const Mask = props => {
  return (
    <ContentContainer
      backgroundColor={props.theme.loadings.bgColor}
      position="absolute"
      {...props}
    />
  );
};

const LoadingCard = props => {
  return (
    <div>
      <ContentContainer height="170px">
        <AnimatedBackground height="170px">
          <Mask {...props} height="25px" right="0" width="80%" />
          <Mask {...props} height="10px" top="25px" width="100%" />
          <Mask {...props} height="5px" right="0" top="35px" width="93%" />
          <Mask {...props} height="40px" top="40px" width="100%" />
          <Mask
            {...props}
            backgroundColor={props.theme.loadings.bgColor}
            height="40px"
            top="80px"
            width="100%"
            border={`1px solid ${props.theme.loadings.lightBorderColor}`}
          />
          <Mask {...props} height="50px" top="120px" width="100%" />
        </AnimatedBackground>
      </ContentContainer>

      <ContentContainer>
        <div className="row">
          <div className="col">
            <ContentContainer>
              <AnimatedBackground height="237px">
                {/*Title*/}
                <Mask
                  {...props}
                  height="25px"
                  right="0"
                  width="85%"
                  mobileXsWidth="70%"
                />
                <Mask {...props} height="35px" top="25px" width="100%" />

                <Mask
                  {...props}
                  height="15px"
                  top="60px"
                  right="0"
                  width="75%"
                  mobileXsWidth="45%"
                />
                <Mask {...props} height="12px" top="75px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="87px"
                  right="0"
                  width="80%"
                  mobileXsWidth="50%"
                />
                <Mask {...props} height="12px" top="102px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="114px"
                  right="0"
                  width="70%"
                  mobileXsWidth="40%"
                />
                <Mask {...props} height="12px" top="129px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="141px"
                  right="0"
                  width="75%"
                  mobileXsWidth="45%"
                />
                <Mask {...props} height="12px" top="156px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="168px"
                  right="0"
                  width="80%"
                  mobileXsWidth="50%"
                />
                <Mask {...props} height="12px" top="183px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="195px"
                  right="0"
                  width="70%"
                  mobileXsWidth="40%"
                />
                <Mask {...props} height="12px" top="210px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="222px"
                  right="0"
                  width="75%"
                  mobileXsWidth="45%"
                />
              </AnimatedBackground>
            </ContentContainer>
          </div>
          <div className="col-auto">
            <ContentContainer width="110px" mobileXsWidth="80px">
              <AnimatedBackground height="237px">
                {/*Title Mask*/}
                <Mask {...props} height="25px" width="100%" />
                <Mask {...props} height="35px" top="25px" width="100%" />

                <Mask
                  {...props}
                  height="15px"
                  top="60px"
                  right="0"
                  width="10%"
                />
                <Mask {...props} height="12px" top="75px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="87px"
                  right="0"
                  width="80%"
                />
                <Mask {...props} height="12px" top="102px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="114px"
                  right="0"
                  width="80%"
                />
                <Mask {...props} height="12px" top="129px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="141px"
                  right="0"
                  width="80%"
                />
                <Mask {...props} height="12px" top="156px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="168px"
                  right="0"
                  width="80%"
                />
                <Mask {...props} height="12px" top="183px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="195px"
                  right="0"
                  width="80%"
                />
                <Mask {...props} height="12px" top="210px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="222px"
                  right="0"
                  width="80%"
                />
              </AnimatedBackground>
            </ContentContainer>
          </div>
          <div className="col-auto">
            <ContentContainer width="40px">
              <AnimatedBackground height="237px">
                {/*Title Mask*/}
                <Mask {...props} height="25px" width="100%" />
                <Mask {...props} height="35px" top="25px" width="100%" />

                <Mask
                  {...props}
                  height="15px"
                  top="60px"
                  right="0"
                  width="10%"
                />
                <Mask {...props} height="12px" top="75px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="87px"
                  right="0"
                  width="75%"
                />
                <Mask {...props} height="12px" top="102px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="114px"
                  right="0"
                  width="75%"
                />
                <Mask {...props} height="12px" top="129px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="141px"
                  right="0"
                  width="75%"
                />
                <Mask {...props} height="12px" top="156px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="168px"
                  right="0"
                  width="75%"
                />
                <Mask {...props} height="12px" top="183px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="195px"
                  right="0"
                  width="75%"
                />
                <Mask {...props} height="12px" top="210px" width="100%" />
                <Mask
                  {...props}
                  height="15px"
                  top="222px"
                  right="0"
                  width="75%"
                />
              </AnimatedBackground>
            </ContentContainer>
          </div>
        </div>
      </ContentContainer>
    </div>
  );
};

const Loading = ({ className, ...props }) => {
  return (
    <div className={className}>
      <LoadingCard {...props} />
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  backgroundColor: PropTypes.string
};

export default withTheme(Loading);
