import React from "react";
import PropTypes from "prop-types";
//Styles
import styled from "styled-components";
import Calendar from "react-calendar/dist/entry.nostyle";
import moment from "moment";

const ReactCalendar = ({ className, ...props }) => {
  return (
    <div className={`${className} custom-react-calendar`}>
      <Calendar
        {...props}
        next2Label={null}
        prev2Label={null}
        formatShortWeekday={(locale, date) => moment(date).format("ddd")}
        formatMonthYear={(locale, date) => moment(date).format("MMMM YYYY")}
        formatMonth={(locale, date) => moment(date).format("MMM")}
      />
    </div>
  );
};

ReactCalendar.propTypes = {
  className: PropTypes.string
};

const ReactCalendarWithStyles = styled(ReactCalendar)`
  &.custom-react-calendar {
    position: absolute;
    right: ${props => props.right || "0"};
    left: ${props => props.left};
    z-index: 3;
    top: ${props => props.top};

    .react-calendar__tile--active,
    .react-calendar__tile--hasActive,
    .react-calendar__navigation button[disabled] {
      background-color: ${props =>
        props.theme.highContrast.action ||
        props.primaryColor ||
        props.theme.components.reactCalendar.titleActiveBgColor};
      color: ${props => props.theme.components.reactCalendar.titleActiveColor};
    }

    .react-calendar {
      background: ${props =>
        props.theme.components.reactCalendar.backgroundColor};
      width: calc(40px * 7 + 40px);
      padding: 20px;
      border: none;
      font-family: "Roboto", sans-serif;
    }

    .react-calendar__navigation {
      height: auto;
      margin-bottom: 5px;

      button {
        text-transform: uppercase;
        font-size: 24px;
        font-weight: 100;
        font-family: "Roboto", sans-serif;
        &:hover,
        &:focus {
          background-color: ${props =>
            props.theme.highContrast.action ||
            props.primaryColor ||
            props.theme.components.reactCalendar.buttonFocus};
        }
      }
    }

    .react-calendar__month-view__weekdays {
      color: ${props =>
        props.theme.components.reactCalendar.monthViewWeekDaysColor};
      text-transform: capitalize;
      font-weight: 300;
      font-size: 14px;
    }

    .react-calendar__month-view__days__day--neighboringMonth {
      color: ${props =>
        props.theme.components.reactCalendar.monthViewWeekDaysNeighboringColor};
    }

    .react-calendar__tile {
      padding: 0;
    }

    abbr[title],
    acronym[title] {
      text-decoration: none;
    }

    button {
      font-family: "Roboto", sans-serif;
      flex-basis: unset !important;
      color: ${props => props.theme.components.reactCalendar.buttonColor};
      height: 40px;
      width: 40px;
      font-weight: 300;
      font-size: 12px;

      &:hover,
      &:focus {
        background-color: ${props =>
          props.theme.highContrast.action ||
          props.primaryColor ||
          props.theme.components.reactCalendar.buttonHoverBgColor};
        color: ${props =>
          props.theme.components.reactCalendar.buttonHoverTextColor};
      }
    }

    button.react-calendar__century-view__decades__decade {
      flex-basis: 33.3333% !important;
    }
  }
`;

export default ReactCalendarWithStyles;

//Only used for the Storybook Props Table
export const WithProps = props => <ReactCalendarWithStyles {...props} />;

WithProps.propTypes = {
  right: PropTypes.string,
  left: PropTypes.string,
  top: PropTypes.string,
  primaryColor: PropTypes.string
};
