import React, { Fragment } from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//components
import { Text, Section, Button } from "./../../../atoms";
import {
  ScrollableTab,
  ScrollableTabs,
  SectionHeader,
  MarkdownRenderer
} from "./../../../molecules";
import Loading from "./Loading";
//Context
import { useConfig } from "../../../../context/ConfigContext";

const ButtonContainer = styled.div`
  margin-top: 30px;
  @media (min-width: 768px) {
    display: flex;
    justify-content: center;
  }
`;

const CardContainer = styled.div`
  margin-top: 30px;
`;

const Card = ({
  text,
  subTitle,
  title,
  titleColor,
  hasMoreLink,
  hasTitle,
  theme,
  linkHoverColor,
  isMobile
}) => {
  const { link: Link } = useConfig();
  return (
    <CardContainer>
      {hasTitle && (
        <Text
          size="article"
          color={
            theme.highContrast.text ||
            titleColor ||
            theme.components.subWebWarnings.card.titleColor
          }
          fontWeight="400"
        >
          {title}
        </Text>
      )}
      <Text size="medium" marginBottom="15px" fontWeight="400">
        {subTitle}
      </Text>
      <Text size="article">
        <MarkdownRenderer
          content={text}
          activeColor={titleColor}
          isMobile={isMobile}
        />
        {hasMoreLink && (
          <Link
            fontSize="inherit"
            fontWeight="inherit"
            textDecoration="underline"
            color={theme.components.subWebWarnings.card.linkColor}
            hoverColor={linkHoverColor}
          >
            ler mais
          </Link>
        )}
      </Text>
    </CardContainer>
  );
};

Card.propTypes = {
  title: PropTypes.string,
  subTitle: PropTypes.string,
  text: PropTypes.string,
  hasMoreLink: PropTypes.bool,
  hasTitle: PropTypes.bool,
  theme: PropTypes.object,
  titleColor: PropTypes.string,
  activeColor: PropTypes.string,
  linkHoverColor: PropTypes.string,
  isMobile: PropTypes.bool
};

Card.defaultProps = {
  title: "Título",
  subTitle: "SubTítulo",
  text: "Texto",
  hasMoreLink: false,
  hasTitle: true,
  linkHoverColor: "#0EB4BD"
};

const Warnings = ({
  className,
  isLoading,
  theme,
  data,
  tabs,
  title,
  action,
  activeTab,
  onChangeTab,
  hasLink,
  activeColor,
  isMobile
}) => {
  return (
    <Section
      className={className}
      backgroundColor={theme.components.subWebWarnings.backgroundColor}
    >
      <SectionHeader
        href="/"
        hasContainer={true}
        hasLine={hasLink}
        hasLink={hasLink}
        textColor={theme.components.subWebWarnings.titleColor}
        marginBottom={tabs.length <= 0 && "0"}
      >
        {title}
      </SectionHeader>
      {isLoading ? (
        <Loading />
      ) : (
        <Fragment>
          {/*Tabs*/}
          {tabs.length > 1 && (
            <ScrollableTabs marginBottom="0">
              {tabs.map((tab, i) => {
                return (
                  <ScrollableTab
                    key={i}
                    isActive={tab.id === activeTab}
                    text={tab.text}
                    theme={theme}
                    activeColor={activeColor}
                    onClick={() => onChangeTab(tab.id)}
                  />
                );
              })}
            </ScrollableTabs>
          )}

          <div className="container">
            {data.chunk(2).map((res, index) => {
              return (
                <div key={index} className="row">
                  {res.map((content, i) => {
                    return (
                      <div key={i} className="col-sm-6 col-12">
                        <Card
                          {...content}
                          theme={theme}
                          titleColor={activeColor}
                          isMobile={isMobile}
                          linkHoverColor={
                            theme.highContrast.link || activeColor
                          }
                        />
                      </div>
                    );
                  })}
                </div>
              );
            })}

            {action.show && (
              <ButtonContainer>
                <Button variant="default" onClick={action.onClick}>
                  {action.text}
                </Button>
              </ButtonContainer>
            )}
          </div>
        </Fragment>
      )}
    </Section>
  );
};

const types = {
  className: PropTypes.string,
  isLoading: PropTypes.bool,
  theme: PropTypes.object,
  data: PropTypes.array.isRequired,
  tabs: PropTypes.array.isRequired,
  title: PropTypes.string,
  activeTab: PropTypes.number,
  onChangeTab: PropTypes.func,
  action: PropTypes.object,
  hasLink: PropTypes.bool,
  activeColor: PropTypes.string,
  isMobile: PropTypes.bool
};

const defaults = {
  isLoading: false,
  title: "Avisos",
  hasLink: true,
  action: {
    text: "ver mais",
    show: false,
    onClick: () => null
  },
  activeColor: "#0EB4BD"
};

Warnings.propTypes = types;
Warnings.defaultProps = defaults;

export default withTheme(Warnings);

//Only used for the Storybook Props Table
export const WithProps = props => <Warnings {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
