import React from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, Text, ContentContainer } from "../../atoms";
import { withTheme } from "styled-components";

const Loading = ({ theme }) => {
  return (
    <ContentContainer className="d-flex align-items-center">
      <AnimatedBackground height="18px" width="20px" />
      <Text
        size="article"
        color={theme.loadings.mainBgColor}
        marginLeft="5px"
        marginRight="5px"
      >
        /
      </Text>
      <AnimatedBackground height="18px" width="68px" />
      <Text
        size="article"
        color={theme.loadings.mainBgColor}
        marginLeft="5px"
        marginRight="5px"
      >
        /
      </Text>
      <ContentContainer
        height="32px"
        width="128px"
        padding="0 8px 0 10px"
        className="d-flex align-items-center justify-content-between"
        border={`1px solid ${theme.loadings.mainBgColor}`}
      >
        <AnimatedBackground height="18px" width="75px" />
        <ContentContainer
          height="14px"
          width="14px"
          borderRadius="50%"
          backgroundColor={theme.loadings.mainBgColor}
        />
      </ContentContainer>
    </ContentContainer>
  );
};

Loading.propTypes = {
  borderBottom: PropTypes.string,
  theme: PropTypes.object
};

export default withTheme(Loading);
