import React, { memo, useRef, useEffect, useState } from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//Helpers
import Slider from "react-slick";
//Components
import Loading from "./Loading";
import {
  Section,
  Text,
  Button,
  Image,
  FadeIn,
  ScreenReaderText
} from "../../../atoms";
import {
  CustomArrow,
  SectionHeader,
  NewsCard,
  Error
} from "../../../molecules";
import theme from "../../../../styles/theme.semantic";
import { withSize } from "react-sizeme";
import { lighten } from "polished";
import { compose } from "../../../../utils/utilities";
//Context
import { useConfig } from "../../../../context/ConfigContext";

const JournalSlides = memo(function JournalSlides({
  className,
  showHeader,
  title,
  resource,
  textColor,
  backgroundColor,
  titleProps,
  cardBackgroundColor,
  imageBackgroundColor,
  cardTextColor,
  mobileMdTextColor,
  onAction,
  onEnter,
  data,
  loading,
  hasError,
  size,
  share,
  theme,
  ...props
}) {
  const [bgImage, setBgImage] = useState({
    imageCover: {
      url:
        "https://images.pexels.com/photos/158651/news-newsletter-newspaper-information-158651.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260"
    }
  });
  const sliderRef = useRef(null);
  const { link: Link, formattedMessage: FormattedMessage } = useConfig();

  const onHandleTabNavigation = e => {
    if (e.key === "Tab" && e.shiftKey) {
      sliderRef.current.slickPrev();
    }

    if (e.key === "Tab") {
      sliderRef.current.slickNext();
    }
  };

  const onSetImage = (position = 0) => {
    if (data.news !== undefined) {
      const image = data.news[position];
      setBgImage(image);
    }
  };

  useEffect(() => {
    if (!loading) {
      onSetImage();
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [loading]);

  const onChangeImage = position => {
    onSetImage(position);
  };

  return (
    <Section
      className={className}
      backgroundColor={
        theme.highContrast.backgroundColor ||
        backgroundColor ||
        theme.components.journalSlides
      }
    >
      <ScreenReaderText>
        <Text as="h2" text="Notícias" />
      </ScreenReaderText>
      {showHeader && (
        <div className="container">
          <div className="row">
            <div className="col">
              <SectionHeader
                textColor={theme.highContrast.link || textColor}
                mobileMdColor={theme.highContrast.link || mobileMdTextColor}
                hasContainer={false}
                {...titleProps}
              >
                {resource ? (
                  <FormattedMessage id={resource} defaultMessage={title} />
                ) : (
                  data.dossier.title
                )}
              </SectionHeader>
            </div>
            {size.width >= 992 && (
              <div className="col-auto">
                <Link
                  color={textColor}
                  display="inline-block"
                  href={props.rightHref}
                >
                  <Text
                    as="h2"
                    size="medium"
                    fontWeight="400"
                    lineHeight="1.5"
                    color={theme.highContrast.link || textColor}
                  >
                    {props.linkResource ? (
                      <FormattedMessage
                        id={props.linkResource}
                        defaultMessage={props.linkTitle}
                      />
                    ) : (
                      props.linkTitle
                    )}
                  </Text>
                </Link>
              </div>
            )}
          </div>
        </div>
      )}
      <div className="container slides-container">
        {loading ? (
          <Loading />
        ) : hasError ? (
          <Error>
            <FormattedMessage
              id="app.content.empty"
              defaultMessage="No content to display"
            />
          </Error>
        ) : (
          <Slider
            ref={sliderRef}
            useTransform={false}
            slidesToShow={3}
            slidesToScroll={1}
            speed={500}
            infinite={false}
            centerMode={false}
            centerPadding={0}
            dots={false}
            initialSlide={0}
            customPaging={i => {
              return <button aria-label={`Notícia ${i + 1}`}>&nbsp;</button>;
            }}
            prevArrow={<CustomArrow ariaLabel="slide anterior" left darken />}
            nextArrow={<CustomArrow ariaLabel="próximo slide" darken />}
            responsive={[
              {
                breakpoint: 1199,
                settings: {
                  slidesToShow: 3
                }
              },
              {
                breakpoint: 991,
                settings: {
                  slidesToShow: 2
                }
              },
              {
                breakpoint: 767,
                settings: {
                  slidesToShow: 1
                }
              },
              {
                breakpoint: 575,
                settings: {
                  slidesToShow: 1,
                  arrows: false,
                  dots: true
                }
              }
            ]}
          >
            {data.news.map((item, index) => {
              return (
                <div key={item.id} className="journal-news-container">
                  <NewsCard
                    className={`journal-news-card ${
                      bgImage.id === item.id ? "journal-news-card-active" : ""
                    }`}
                    data={item}
                    onClick={e => onAction(e, item.url)}
                    onKeyPress={e => onEnter(e, item.url)}
                    cardBgColor={cardBackgroundColor}
                    imageBgColor={imageBackgroundColor}
                    linkColor={cardTextColor}
                    onKeyDown={onHandleTabNavigation}
                    hasShareIcon={true}
                    onMouseOver={() => onChangeImage(index)}
                    size={size}
                    share={share}
                    history={props.history}
                  />
                </div>
              );
            })}
          </Slider>
        )}

        {size.width <= 991 && (
          <div className="button-container d-flex justify-content-center">
            <Button
              variant="default"
              onClick={() => window.location.replace(props.rightHref)}
            >
              {props.linkResource ? (
                <FormattedMessage
                  id={props.linkResource}
                  defaultMessage={props.linkTitle}
                />
              ) : (
                props.linkTitle
              )}
            </Button>
          </div>
        )}
      </div>

      {size.width >= 992 && (
        <FadeIn key={bgImage.imageCover.url}>
          <Image
            src={bgImage.imageCover.url}
            height="100%"
            width="100%"
            alt={
              bgImage.imageCover.alt !== undefined ? bgImage.imageCover.alt : ""
            }
          />
        </FadeIn>
      )}
    </Section>
  );
});

const types = {
  title: PropTypes.string,
  rightTitle: PropTypes.string,
  text: PropTypes.string,
  cardStyles: PropTypes.object.isRequired,
  className: PropTypes.string,
  showHeader: PropTypes.bool,
  textColor: PropTypes.string,
  backgroundColor: PropTypes.string,
  titleProps: PropTypes.object,
  cardBackgroundColor: PropTypes.string,
  imageBackgroundColor: PropTypes.string,
  cardTextColor: PropTypes.string,
  mobileMdTextColor: PropTypes.string,
  data: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),
  loading: PropTypes.bool,
  hasError: PropTypes.bool,
  size: PropTypes.object,
  share: PropTypes.object,
  rightHref: PropTypes.string,
  history: PropTypes.object.isRequired,
  resource: PropTypes.string,
  onAction: PropTypes.func,
  onEnter: PropTypes.func,
  linkResource: PropTypes.string,
  linkTitle: PropTypes.string,
  theme: PropTypes.object
};

const defaults = {
  backgroundColor: theme.components.journalSlides.backgroundColor,
  textColor: theme.components.journalSlides.textColor,
  imageBackgroundColor: theme.components.journalSlides.cardBackgroundColor,
  cardTextColor: theme.components.journalSlides.cardTextColor,
  mobileMdTextColor: theme.components.journalSlides.mobileMdTextColor,
  share: {
    facebook: `https://www.facebook.com/sharer/sharer.php?u=url`,
    twitter: `http://twitter.com/share?url=url&text=title`,
    linkedin: `https://www.linkedin.com/shareArticle?mini=true&url=url`,
    url: "/"
  }
};

JournalSlides.propTypes = types;
JournalSlides.defaultProps = defaults;

const JournalSlidesWithStyles = styled(JournalSlides)`
  //Styles for cards in desktop
  .news-card {
    background-color: transparent;
  }
  .content-container {
    background-color: ${props =>
      props.theme.components.journalSlides.cardBackgroundColor};
  }

  // Medium devices (tablets, more than 992px)
  @media (min-width: 992px) {
    span.news-plus-button {
      display: none;
    }
    position: relative;
    img {
      position: absolute;
      top: 0;
      left: 0;
      z-index: 0;
    }
    .container {
      z-index: 2;
      position: relative;
    }
    //Card Design
    .journal-news-container {
      height: 398px;
      display: flex !important;
      flex-direction: column;
      justify-content: flex-end;
      .journal-news-card {
        height: unset;
      }
      .news-card {
        min-height: unset;
        flex-grow: unset;
        height: unset;
        //Not active state
        .content-container {
          background-color: ${props =>
            props.theme.components.journalSlides
              .desktopContentContainerBgColor};
          opacity: 0.9;
          //Access Elements in NewsCard for active a not active card state
          .news-card-category,
          .news-card-title,
          .news-card-text,
          .news-card-date,
          i.fa-share-alt {
            color: ${props =>
              props.theme.components.journalSlides
                .desktopContentContainerContentColor};
          }
        }
      }
      //Active state
      .journal-news-card-active .content-container {
        background-color: ${props =>
          props.theme.components.journalSlides.activeCardBgColor};
        opacity: 1;
        //Access Elements in NewsCard for active a not active card state
        .news-card-category,
        .news-card-title,
        .news-card-text,
        .news-card-date,
        i.fa-share-alt {
          color: ${props =>
            props.theme.components.journalSlides.activeCardContentColor};
        }
        button {
          :hover {
            i.fa-share-alt {
              color: ${lighten(0.5, "#000")};
            }
          }
        }
      }
      .image-container {
        display: none;
      }
    }

    //Gradient
    &:before {
      content: "";
      position: absolute;
      top: 0;
      height: 100%;
      width: 100%;
      z-index: 2;
      background: linear-gradient(
        ${props => props.theme.components.journalSlides.gradientColor},
        transparent
      );
      background: -webkit-linear-gradient(
        ${props => props.theme.components.journalSlides.gradientColor},
        transparent
      );
      background: -o-linear-gradient(
        ${props => props.theme.components.journalSlides.gradientColor},
        transparent
      );
      background: -ms-linear-gradient(
        ${props => props.theme.components.journalSlides.gradientColor},
        transparent
      );
      background: -moz-linear-gradient(
        ${props => props.theme.components.journalSlides.gradientColor},
        transparent
      );
    }
  }

  // Medium devices (tablets, less than 992px)
  @media (max-width: 991px) {
    padding-top: 0;
    padding-bottom: 0;
    background-image: unset;
    .news-card {
      background-color: ${props =>
        props.theme.components.journalSlides.mobileCardBackgroundColor};
    }
    .content-container {
      background-color: inherit;
    }
    span.news-plus-button {
      background-color: ${props =>
        props.theme.components.journalSlides.mobileCardBackgroundColor};
    }
    .Section-Header-Line {
      background-color: ${props =>
        props.theme.components.journalSlides.mobileMdTextColor};
    }
  }

  .slides-container {
    padding-right: 0;
    padding-left: 0;
  }

  .slick-slide {
    //Safari
    height: 398px;
    margin-top: 2px;
    margin-bottom: 2px;
  }

  .slick-prev {
    left: -25px;
  }
  .slick-next {
    right: -25px;
  }
  .slick-next,
  .slick-prev {
    width: auto;
    height: 36px;
    top: 100px;
  }

  .slick-next:before,
  .slick-prev:before {
    content: none;
  }

  .slick-dots {
    width: 100%;
    bottom: 0;
    position: relative;
    margin-top: 10px;
    text-align: right;
    padding: 0 15px;
  }

  .slick-dots li {
    margin: 0 5px;
    :last-child {
      margin-right: 0;
    }
  }

  .slick-dots li,
  .slick-dots li button {
    width: 8px;
    height: 8px;
  }

  .slick-dots li button:before {
    content: "";
    background-color: ${props =>
      props.theme.components.reactSlickSlider.buttonBgColor};
    width: 8px;
    height: 8px;
  }

  .button-container {
    margin-top: 70px;
  }

  // Large devices (desktops, 992px and up to 1199)
  @media (min-width: 992px) and (max-width: 1199px) {
    .slick-prev {
      left: -15px;
    }
    .slick-next {
      right: -15px;
    }
  }
`;

export default compose(withTheme, withSize())(JournalSlidesWithStyles);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => (
  <JournalSlidesWithStyles {...props} />
);

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
