import PropTypes from "prop-types";
//Styles
import React from "react";
import styled from "styled-components";

const HorizontalList = styled.ul`
  width: 100%;
  position: ${props => props.position};
  left: ${props => props.left};

  li {
    display: ${props => props.display};
  }

  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    text-align: ${props => props.xsTextAlign};
  }
`;

const types = {
  display: PropTypes.string,
  xsTextAlign: PropTypes.string,
  position: PropTypes.string,
  left: PropTypes.string,
  children: PropTypes.node
};

const defaults = {
  display: "inline"
};

HorizontalList.propTypes = types;
HorizontalList.defaultProps = defaults;

export default HorizontalList;

//Only used for the Storybook Props Table
export const WithProps = ({ children, ...props }) => (
  <HorizontalList {...props}>{children}</HorizontalList>
);

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
