import React from "react";
import { AnimatedBackground, ContentContainer } from "../../atoms";
import { withTheme } from "styled-components";
import PropTypes from "prop-types";

const Loading = ({ theme }) => {
  return (
    <ContentContainer
      height="38px"
      border={`1px solid ${theme.loadings.lightBorderColor}`}
      padding="0 0 0 10px"
      className="d-flex flex-column justify-content-center"
    >
      <div className="row no-gutters">
        <div className="col d-flex flex-column justify-content-center">
          <ContentContainer marginRight="10px">
            <AnimatedBackground height="15px" />
          </ContentContainer>
        </div>
        <div className="col-auto">
          <ContentContainer
            width="30px"
            height="22px"
            borderLeft={`1px solid ${theme.loadings.lightBorderColor}`}
            className="d-flex align-items-center justify-content-center"
          >
            <ContentContainer
              height="14px"
              width="14px"
              borderRadius="50%"
              backgroundColor={theme.loadings.mainBgColor}
            />
          </ContentContainer>
        </div>
      </div>
    </ContentContainer>
  );
};

Loading.propTypes = {
  theme: PropTypes.object
};

export default withTheme(Loading);
