import React from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../../atoms";
import { withTheme } from "styled-components";

const Mask = ({ MaskBgColor, ...props }) => {
  return (
    <ContentContainer
      backgroundColor={MaskBgColor || props.theme.loadings.maskBgColor}
      position="absolute"
      {...props}
    />
  );
};

const Loading = ({ CardBgColor, ...props }) => {
  return (
    <ContentContainer
      height="100px"
      padding="12px 0"
      borderTop={`1px solid ${props.theme.loadings.lightBorderColor}`}
    >
      <AnimatedBackground height="26px">
        <Mask
          height="26px"
          right="0"
          width="15%"
          MaskBgColor={CardBgColor}
          {...props}
        />
      </AnimatedBackground>
    </ContentContainer>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  CardBgColor: PropTypes.string
};

export default withTheme(Loading);
