import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
import { darken, lighten } from "polished";
import { withExtendedClass } from "../../../utils/utilities.style";
import React from "react";

const lightenHover = 0.5;
const DropdownList = styled.div.attrs(({ className }) =>
  withExtendedClass({
    className,
    extend: "dropdown-content"
  })
)`
  a,
  button {
    color: ${props => props.color || props.theme.components.dropdownList.color};
    font-weight: ${props => props.fontWeight || props.theme.weight.base};
    padding: ${props =>
      props.padding || `${props.theme.sizes.xs2} ${props.theme.sizes.xl}`};
    &:hover {
      background-color: ${props =>
        props.theme.highContrast.link ||
        lighten(
          lightenHover,
          props.hoverBackgroundColor ||
            props.theme.components.dropdownList.hoverBackgroundColor
        )};
    }
    &:first-child {
      margin-top: 0;
    }
    &:focus {
      outline-offset: -2px;
    }
  }
  a {
    margin-right: 0 !important;
    text-decoration: none;
    display: block;
  }
  button {
    border: none;
    background-color: transparent;
    width: 100%;
    text-align: left;
    font-size: 14px;
    cursor: pointer;
    &:hover {
      color: ${props =>
        darken(0.5, props.color || props.theme.components.dropdownList.color)};
    }
  }

  ${props =>
    props.hasMaxHeight &&
    `
    max-height: ${props.maxHeight}px;
    overflow-y: scroll;
  `};
`;

const defaults = {
  hasMaxHeight: false,
  maxHeight: 300
};

const types = {
  className: PropTypes.string,
  color: PropTypes.string,
  fontWeight: PropTypes.string,
  padding: PropTypes.string,
  hoverBackgroundColor: PropTypes.string,
  maxHeight: PropTypes.number,
  hasMaxHeight: PropTypes.bool
};

DropdownList.propTypes = types;
DropdownList.defaultProps = defaults;

export default withTheme(DropdownList);

//Only used for the Storybook Props Table
export const WithProps = props => <DropdownList {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
