import React, { Fragment, memo } from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//components
import Slider from "react-slick";
import { Text, Button, Section, ContentContainer } from "../../atoms";
import {
  SectionHeader,
  InjectHtml,
  CustomArrow,
  CandidatureCard,
  Error
} from "../../molecules";
import Loading from "./Loading";
//Context
import { useConfig } from "../../../context/ConfigContext";

const Candidature = memo(function Candidature({
  textColor,
  backgroundColor,
  className,
  hasLine,
  content,
  loading,
  hasError,
  theme
}) {
  const { link: Link } = useConfig();
  return (
    <Section
      className={className}
      backgroundColor={
        theme.highContrast.backgroundColor ||
        backgroundColor ||
        theme.components.candidature.backgroundColor
      }
    >
      {loading ? (
        <Loading />
      ) : hasError ? (
        <Error text={"No content to display"} />
      ) : (
        <Fragment>
          <SectionHeader
            marginBottom="30px"
            title={content.data.title}
            textColor={textColor}
            hasLine={hasLine}
            hasLink={hasLine}
          />
          <div className="container candidature-container">
            <Slider
              useTransform={false}
              slidesToShow={Object.keys(content.data.list).length >= 3 ? 3 : 2}
              slidesToScroll={1}
              speed={500}
              infinite={false}
              centerMode={false}
              centerPadding={0}
              dots={false}
              arrows={Object.keys(content.data.list).length >= 3}
              initialSlide={0}
              customPaging={i => {
                return <button aria-label={`${i + 1}ª Fase`}>&nbsp;</button>;
              }}
              prevArrow={
                <CustomArrow
                  color={theme.components.candidature.arrowColor}
                  left
                  darken
                />
              }
              nextArrow={
                <CustomArrow
                  color={theme.components.candidature.arrowColor}
                  darken
                />
              }
              responsive={[
                {
                  breakpoint: 1199,
                  settings: {
                    slidesToShow: 3
                  }
                },
                {
                  breakpoint: 991,
                  settings: {
                    slidesToShow: 2
                  }
                },
                {
                  breakpoint: 767,
                  settings: {
                    slidesToShow: 1,
                    dots: true
                  }
                },
                {
                  breakpoint: 575,
                  settings: {
                    slidesToShow: 1,
                    arrows: false,
                    dots: true
                  }
                }
              ]}
            >
              {content.data.list.map((res, index) => {
                return (
                  <CandidatureCard
                    key={index}
                    title={res.title}
                    buttonBgColor={backgroundColor}
                  >
                    <div className="d-flex flex-column flex-grow-1">
                      {res.contents.map((item, index) => {
                        return (
                          <ContentContainer key={index} marginTop="20px">
                            <Text
                              text={item.info.label}
                              size="article"
                              fontWeight="400"
                            />
                            <Fragment>
                              {item.type === "link" ? (
                                <Link
                                  href={item.info.value}
                                  aria-label={item.info.value}
                                  fontWeight="300"
                                  fontSize="15px"
                                  textDecoration="underline"
                                  color={
                                    theme.components.candidature.card.linkColor
                                  }
                                  hoverColor={
                                    theme.highContrast.link ||
                                    backgroundColor ||
                                    theme.components.candidature.card
                                      .linkHoverColor
                                  }
                                  default
                                  native
                                >
                                  {item.info.value}
                                </Link>
                              ) : (
                                <InjectHtml content={item.info.value} />
                              )}
                            </Fragment>
                          </ContentContainer>
                        );
                      })}

                      {res.extras !== null &&
                        res.extras.map((res, index) => {
                          return (
                            <ContentContainer key={index} marginTop="20px">
                              <div className="row no-gutters">
                                <div className="col-12">
                                  <Text
                                    text={res.title}
                                    size="article"
                                    fontWeight="300"
                                  />
                                </div>

                                {res.contents.map((item, index) => {
                                  return (
                                    <Fragment key={index}>
                                      <div className="col-12">
                                        <Text
                                          text={item.info.label}
                                          size="article"
                                          fontWeight="300"
                                        />
                                      </div>
                                      <div className="col-12">
                                        {item.type === "link" ? (
                                          <Link
                                            href={item.info.value}
                                            aria-label={item.info.value}
                                            fontWeight="300"
                                            fontSize="15px"
                                            textDecoration="underline"
                                            color={
                                              theme.components.candidature.card
                                                .linkColor
                                            }
                                            hoverColor={
                                              theme.highContrast.link ||
                                              backgroundColor ||
                                              theme.components.candidature.card
                                                .linkHoverColor
                                            }
                                            default
                                            native
                                          >
                                            {item.info.value}
                                          </Link>
                                        ) : (
                                          <InjectHtml
                                            content={item.info.value}
                                          />
                                        )}
                                      </div>
                                    </Fragment>
                                  );
                                })}
                              </div>
                            </ContentContainer>
                          );
                        })}
                    </div>

                    {res.button !== null && (
                      <ContentContainer marginTop="30px">
                        <Button
                          variant="default"
                          as="a"
                          display="flex"
                          width="fit-content"
                          textDecoration="none"
                          href={res.button.url}
                          backgroundColor={
                            theme.highContrast.lightBackgroundColor
                          }
                        >
                          {res.button.label}
                        </Button>
                      </ContentContainer>
                    )}
                  </CandidatureCard>
                );
              })}
            </Slider>
            {(content.data.minimalNumberStudents ||
              content.data.mobility !== null) && (
              <ContentContainer marginTop="40px" marginLeft="15px">
                <InjectHtml
                  content={content.data.minimalNumberStudents}
                  variant="light"
                />
                <div>
                  {content.data.mobility && (
                    <Link
                      href={content.data.mobility.value}
                      color={theme.highContrast.link || "#fff"}
                      fontSize="15px"
                      fontWeight="400"
                      textDecoration="underline"
                      default
                    >
                      {content.data.mobility.label}
                    </Link>
                  )}
                </div>
              </ContentContainer>
            )}
          </div>
        </Fragment>
      )}
    </Section>
  );
});

const defaults = {
  hasLine: false,
  hasError: false
};

const types = {
  textColor: PropTypes.string,
  backgroundColor: PropTypes.string,
  className: PropTypes.string,
  hasLine: PropTypes.bool,
  content: PropTypes.object.isRequired,
  loading: PropTypes.bool,
  hasError: PropTypes.bool,
  theme: PropTypes.object
};

Candidature.propTypes = types;
Candidature.defaultProps = defaults;

const CandidatureWithStyles = styled(Candidature)`
  .candidature-container {
    padding: 0;
  }

  .slick-next,
  .slick-prev {
    height: auto;
  }

  .slick-dots {
    width: 100%;
    bottom: 0;
    position: relative;
    margin-top: 10px;
    text-align: right;
    padding: 0 15px;
  }

  .slick-dots li {
    margin: 0 5px;
    &:last-child {
      margin-right: 0;
    }
  }

  .slick-dots li,
  .slick-dots li button {
    width: 8px;
    height: 8px;
  }

  .slick-dots li button:before {
    content: "";
    background-color: ${props => props.theme.components.candidature.dotsColor};
    width: 8px;
    height: 8px;
  }

  .slick-next:before,
  .slick-prev:before {
    content: none;
  }

  //Cards all same height
  .slick-track {
    display: flex;
  }

  .slick-slide {
    height: auto;
    margin: 15px;
  }

  .slick-slide > div {
    height: 100%;
  }
`;

export default withTheme(CandidatureWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <CandidatureWithStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
