import React from "react";
import PropTypes from "prop-types";
import { withTheme } from "styled-components";
import { Link, Icon, ContentContainer } from "../../../atoms";

const IconCard = ({
  iconName,
  linkText,
  theme,
  margin,
  mobileXsMargin,
  linkProps,
  favorite
}) => {
  return (
    <ContentContainer
      margin={margin}
      mobileXsMargin={mobileXsMargin}
      width="64px"
    >
      <ContentContainer
        className="d-flex align-items-center justify-content-center"
        height="64px"
        width="64px"
        backgroundColor={
          theme.components.iconCard.iconContainer.backgroundColor
        }
        marginBottom="4px"
      >
        <Icon
          name={iconName}
          size="medium"
          color={theme.components.iconCard.iconContainer.iconColor}
        />
      </ContentContainer>
      <Link
        size="xSmall"
        color={theme.components.iconCard.link.color}
        {...linkProps}
      >
        {linkText}
      </Link>
      {/** TODO: this is only for testing */}

      {favorite.show && (
        <Icon
          name={`${favorite.check ? "fas" : "fal"} fa-star`}
          onClick={favorite.check ? favorite.remove : favorite.set}
          size="xSmall"
          color="#000"
          marginLeft="5px"
          cursor="pointer"
        />
      )}
    </ContentContainer>
  );
};

const types = {
  iconName: PropTypes.string,
  linkText: PropTypes.string,
  margin: PropTypes.string,
  mobileXsMargin: PropTypes.string,
  theme: PropTypes.object,
  linkProps: PropTypes.object,
  favorite: PropTypes.object
};

const defaults = {
  iconName: "fal fa-paperclip",
  linkText: "texto",
  favorite: {
    show: false,
    check: false,
    set: () => null,
    remove: () => null
  }
};

IconCard.propTypes = types;
IconCard.defaultProps = defaults;

export default withTheme(IconCard);

//Only used for the Storybook Props Table
export const WithProps = props => <IconCard {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
