import React, { Fragment } from "react";
import PropTypes from "prop-types";
//components
import { Text } from "../../atoms";
import { InjectHtml } from "../../molecules";
import { withTheme } from "styled-components";
//Context
import { useConfig } from "../../../context/ConfigContext";

const TwoCols = ({ data, theme }) => {
  const { link: Link, formattedMessage: FormattedMessage } = useConfig();
  if (data === null || Object.keys(data.description).length <= 0)
    return (
      <div>
        <FormattedMessage
          id="app.content.empty"
          defaultMessage="No content to display"
        />
      </div>
    );
  return (
    <div className="row">
      {data.description.map((res, index) => {
        return (
          <div key={index} className="col-xl col-lg col-md-12">
            <InjectHtml content={res.value} variant="presentation" />
            {index === 1 && data.url !== null && (
              <Fragment>
                <Text
                  lineHeight="1.6"
                  fontSize="14px"
                  fontWeight="400"
                  text={data.url.title}
                  color={theme.components.contents.twoCols.textColor}
                />
                {data.url.urls !== null &&
                  data.url.urls.map((item, index) => {
                    return (
                      <Link
                        key={index}
                        lineHeight="1.6"
                        fontSize="14px"
                        fontWeight="300"
                        color={theme.components.contents.twoCols.linkColor}
                        text={item.label}
                        aria-label={item.label}
                        href={item.value}
                        textDecoration="underline"
                        hoverColor={
                          theme.components.contents.twoCols.linkHoverColor
                        }
                        display="block"
                        default
                        native
                      />
                    );
                  })}
              </Fragment>
            )}
          </div>
        );
      })}
    </div>
  );
};

TwoCols.propTypes = {
  data: PropTypes.object.isRequired,
  theme: PropTypes.object
};

export default withTheme(TwoCols);
