import React from "react";
import styled from "styled-components";
import PropTypes from "prop-types";
import { NewsCardLoading } from "../../molecules";

const Loading = ({ className }) => {
  return (
    <div className={className}>
      <div className="container">
        <div className="row">
          <div className="col-xl col-lg col-md-6 col-sm-12">
            <NewsCardLoading />
          </div>
          <div className="col-xl col-lg col-md-6 hide-sm">
            <NewsCardLoading />
          </div>
          <div className="col-xl col-lg hide-md">
            <NewsCardLoading />
          </div>
        </div>
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string
};

const LoadingWithStyles = styled(Loading)`
  // Medium devices (tablets, 768px and up)
  @media (max-width: 768px) {
    .hide-sm {
      display: none;
    }
  }

  // Large devices (desktops, 992px and up)
  @media (max-width: 992px) {
    .hide-md {
      display: none;
    }
  }
`;

export default LoadingWithStyles;
