import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//Components
import { ContentLoading } from "../../molecules";
import EmptyStyles from "../../molecules/Empty";

const Loading = ({ className, hasDelay, backgroundColor }) => {
  const [show, setShow] = useState(false);

  useEffect(() => {
    if (hasDelay) {
      const timeout = setTimeout(() => {
        setShow(true);
      }, 500);

      return () => {
        clearTimeout(timeout);
      };
    }
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  if (!show && hasDelay) return null;

  return (
    <div className={className}>
      <div className="container">
        <div className="row no-gutters">
          <div className="col-xl-6 offset-xl-3 col-lg-8 col-12 offset-0">
            <ContentLoading maskBgColor={backgroundColor} />
          </div>
        </div>
      </div>
    </div>
  );
};

const types = {
  className: PropTypes.string,
  hasDelay: PropTypes.bool,
  backgroundColor: PropTypes.string
};

EmptyStyles.propTypes = types;

const LoadingWithStyles = styled(Loading)`
  background-color: ${props => props.theme.components.loading.backgroundColor};
  //145 is navs height
  min-height: calc(100vh - 145px);
  padding: 70px 0;
  // Medium devices (tablets, less than 992px)
  @media (max-width: 991px) {
    min-height: calc(100vh - 125px);
  }
`;

export default withTheme(LoadingWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <LoadingWithStyles {...props} />;

WithProps.propTypes = types;
