import React, { useMemo } from "react";
import PropTypes from "prop-types";
import { withTheme } from "styled-components";
//Components
import {
  Text,
  Picture,
  AspectRatio,
  Link,
  ContentContainer,
  Icon
} from "../../../atoms";

const PeopleCard = ({
  src,
  alt,
  name,
  email,
  extension,
  theme,
  href,
  hasPhoto,
  marginBottom,
  linkHoverColor,
  lang
}) => {
  const phoneNumber = useMemo(() => {
    if (extension) {
      const extensionArray = extension.split("|");
      return extensionArray.reduce((a, b) => {
        return a.length > b.length ? a : b;
      });
    }
  }, [extension]);

  return (
    <ContentContainer marginBottom={marginBottom}>
      <AspectRatio ratio="oneByOne">
        {hasPhoto ? (
          <Link textDecoration="none" cursor="pointer" href={href}>
            <Picture src={src} alt={alt} height="100%" hasResize={false} />
          </Link>
        ) : (
          <ContentContainer
            height="100%"
            width="100%"
            position="absolute"
            top="0"
            left="0"
            display="flex"
            alignItems="center"
            justifyContent="center"
          >
            <Icon
              name={theme.icons.user}
              fontSize="80px"
              xsFontSize="40px"
              color={
                theme.components.subWebPeople.personCard.imagePlaceholderColor
              }
            />
          </ContentContainer>
        )}
      </AspectRatio>

      <Link
        fontSize="16px"
        marginTop="8px"
        marginBottom="8px"
        fontWeight="300"
        display="block"
        lineHeight="1.5"
        href={href}
        hoverColor={theme.highContrast.link || linkHoverColor}
      >
        {name}
      </Link>

      <Link
        fontSize="12px"
        fontWeight="300"
        textDecoration="underline"
        color={theme.components.subWebPeople.personCard.linkColor}
        href={`mailto:${email}`}
        hoverColor={theme.highContrast.link || linkHoverColor}
      >
        {email}
      </Link>

      {extension && (
        <Text
          size="xSmall"
          marginTop="4px"
          color={theme.components.subWebPeople.personCard.textColor}
        >
          {lang === "pt" ? "extensão:" : "extension:"}{" "}
          <Link
            fontSize="12px"
            fontWeight="300"
            textDecoration="underline"
            href={`tel:${phoneNumber.trim()}`}
            color={theme.components.subWebPeople.personCard.linkColor}
            hoverColor={theme.highContrast.link || linkHoverColor}
          >
            {extension}
          </Link>
        </Text>
      )}
    </ContentContainer>
  );
};

PeopleCard.propTypes = {
  className: PropTypes.string,
  src: PropTypes.string,
  alt: PropTypes.string,
  name: PropTypes.string,
  email: PropTypes.string,
  extension: PropTypes.string,
  marginBottom: PropTypes.string,
  theme: PropTypes.object,
  href: PropTypes.string.isRequired,
  hasPhoto: PropTypes.bool,
  linkHoverColor: PropTypes.string,
  lang: PropTypes.string
};

PeopleCard.defaultProps = {
  src: "",
  name: "Nome",
  email: "Email",
  extension: "000000",
  hasPhoto: true,
  marginBottom: "70px",
  lang: "pt"
};

export default withTheme(PeopleCard);
