import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";

const Indent = ({ children, className }) => {
  return (
    <div className={`row no-gutters ${className}`}>
      <div className="col-auto">
        <span className="indent">·</span>
      </div>
      <div className="col">{children}</div>
    </div>
  );
};

Indent.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string
};

const IndentWithStyles = styled(Indent)`
  /*indent for a div with ·*/
  .indent {
    margin: 0 5px 0 ${props => (props.double ? "20px" : "10px")};
    color: ${props => props.theme.components.table.indentColor};
  }
`;

const Table = ({ children, className, width }) => {
  return (
    <div className={className}>
      <table width={width}>{children}</table>
    </div>
  );
};

Table.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  width: PropTypes.string
};

const TableWithStyles = styled(Table)`
  table {
    width: ${props => props.width};
    margin-top: ${props => props.marginTop};
    border-collapse: collapse;
    border-spacing: 0;
    color: ${props => props.theme.components.table.textColor};
  }

  tbody {
    vertical-align: top;
  }

  caption {
    font-size: 28px;
    font-weight: 500;
    line-height: 1.4;
  }

  tbody td {
    padding-top: ${props => props.tableRowSpace};
    padding-bottom: ${props => props.tableRowSpace};
  }

  thead th {
    padding-top: ${props => props.tableHeaderMarginTop};
    padding-bottom: ${props => props.tableRowSpace};
  }

  th,
  td,
  caption {
    text-align: left;
  }

  tr th:first-child,
  tr td:first-child {
    //word-break: break-word;
    word-break: initial;
  }

  tr th:nth-child(2),
  tr td:nth-child(2) {
    width: ${props => props.col2Size};
    padding-right: 20px;
    padding-left: 20px;
  }

  tr th:nth-child(3),
  tr td:nth-child(3) {
    width: ${props => props.col3Size};
  }

  tr th:nth-child(4),
  tr td:nth-child(4) {
    width: ${props => props.col4Size};
  }

  // Small devices (landscape phones, 576px and up)
  @media (max-width: 576px) {
    tr th:nth-child(2),
    tr td:nth-child(2) {
      width: ${props => props.col2SizeMobile};
    }

    tr th:nth-child(3),
    tr td:nth-child(3) {
      width: ${props => props.col3SizeMobile};
    }

    tr th:nth-child(4),
    tr td:nth-child(4) {
      width: ${props => props.col4SizeMobile};
    }
  }

  tbody td {
    border-top: ${props =>
      props.borders
        ? `1px solid ${props.theme.components.table.borderColor}`
        : props.lightBorders
        ? `1px solid ${props.theme.components.table.borderLightColor}`
        : null};
    border-bottom: ${props =>
      props.borders
        ? `1px solid ${props.theme.components.table.borderColor}`
        : props.lightBorders
        ? `1px solid ${props.theme.components.table.borderLightColor}`
        : null};
  }

  /*An option has color*/
  .option {
    color: ${props =>
      props.theme.highContrast.text ||
      props.activeColor ||
      props.theme.components.table.optionColor};
  }

  /*An option inside minor has indent and color*/
  .option-inside-minor {
    margin-left: 10px;
    display: block;
  }

  /*A minor has color and weight*/
  .minor {
    font-weight: 400;
    color: ${props =>
      props.theme.highContrast.text ||
      props.activeColor ||
      props.theme.components.table.optionColor};
  }

  .last-child {
    padding-top: 20px;
    font-weight: 400;
  }

  .if-has-links {
    a {
      text-decoration: underline;
    }
  }
`;

TableWithStyles.defaultProps = {
  width: "100%",
  tableRowSpace: "6px",
  marginTop: "40px",
  tableHeaderMarginTop: "20px"
};

export { TableWithStyles, IndentWithStyles };

//Only used for the Storybook Props Table
export const WithProps = props => <TableWithStyles {...props} />;

WithProps.propTypes = {
  children: PropTypes.node,
  className: PropTypes.string,
  width: PropTypes.string,
  tableRowSpace: PropTypes.string,
  marginTop: PropTypes.string,
  tableHeaderMarginTop: PropTypes.string,
  col2Size: PropTypes.string,
  col3Size: PropTypes.string,
  col4Size: PropTypes.string,
  col2SizeMobile: PropTypes.string,
  col3SizeMobile: PropTypes.string,
  col4SizeMobile: PropTypes.string,
  borders: PropTypes.bool,
  lightBorders: PropTypes.bool,
  activeColor: PropTypes.string
};

WithProps.defaultProps = {
  width: "100%",
  tableRowSpace: "6px",
  marginTop: "40px",
  tableHeaderMarginTop: "20px"
};
