import React, { useState } from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
import { lighten } from "polished";
import { Icon, Link, Text, HorizontalList, ListItem } from "../../../atoms";
import { copyTextToClipboard } from "../../../../utils/utilities";
import { useConfig } from "../../../../context/ConfigContext";

const ShareSocialMedia = ({ className, share, theme }) => {
  const [isOpen, setOpen] = useState(false);

  const { useIntl, formattedMessage: FormattedMessage } = useConfig();

  const intl = useIntl();

  const onHandleAnimation = e => {
    e.preventDefault();
    setOpen(true);
  };

  const onHandleCopyToClipboard = async e => {
    e.preventDefault();

    if (await copyTextToClipboard(share.url)) {
      onHandleAnimation(e);
    }
  };

  const openLink = (e, url) => {
    e.preventDefault();
    window.open(url, "_blank");
  };

  return (
    <div className={className}>
      <div className="share-social-media">
        {isOpen && (
          <div className="copy-clipboard-feedback">
            <Text
              color={theme.components.shareSocialMedia.copyToClipboardTextColor}
              size="xSmall"
            >
              <FormattedMessage
                id="app.journal.news.share.feedback"
                defaultMessage="Url copiado para clipboard"
              />
            </Text>
          </div>
        )}
        <HorizontalList display="inline-block">
          <ListItem>
            <Link
              as="button"
              tabIndex="0"
              onClick={onHandleCopyToClipboard}
              title={intl({
                id: "app.journal.news.share.button.title",
                defaultMessage: "Copy to Clipboard"
              })}
              aria-label={intl({
                id: "app.journal.news.share.button.title",
                defaultMessage: "Copy to Clipboard"
              })}
            >
              <Icon
                name={theme.icons.link}
                size="small"
                color={theme.components.shareSocialMedia.iconColor}
              />
            </Link>
          </ListItem>

          <ListItem>
            <Link
              as="button"
              onClick={e => openLink(e, share.facebook)}
              title="Facebook"
              aria-label="Facebook"
            >
              <Icon
                name={theme.icons.facebook}
                size="small"
                color={theme.components.shareSocialMedia.iconColor}
              />
            </Link>
          </ListItem>

          <ListItem>
            <Link as="button" onClick={e => openLink(e, share.twitter)}>
              <Icon
                name={theme.icons.twitter}
                size="small"
                title="Twitter"
                aria-label="Twitter"
                color={theme.components.shareSocialMedia.iconColor}
              />
            </Link>
          </ListItem>

          <ListItem>
            <Link
              as="button"
              onClick={e => openLink(e, share.linkedin)}
              title="LinkedIn"
              aria-label="LinkedIn"
            >
              <Icon
                name={theme.icons.linkedIn}
                size="small"
                color={theme.components.shareSocialMedia.iconColor}
              />
            </Link>
          </ListItem>
        </HorizontalList>
      </div>
    </div>
  );
};

ShareSocialMedia.defaultProps = {
  share: {
    facebook: `https://www.facebook.com/sharer/sharer.php?u=url`,
    twitter: `http://twitter.com/share?url=url&text=title`,
    linkedin: `https://www.linkedin.com/shareArticle?mini=true&url=url`,
    url: "/"
  }
};

ShareSocialMedia.propTypes = {
  className: PropTypes.string,
  share: PropTypes.object,
  intl: PropTypes.string,
  theme: PropTypes.object
};

const ShareSocialMediaWithStyles = styled(ShareSocialMedia)`
  /*
  -- Copy to Clipboard Feedback -- Globals
  */
  .copy-clipboard-feedback {
    position: absolute;
    z-index: 1;
    padding: 5px 15px;
    left: 0;
    top: 0;
    background-color: ${props =>
      props.theme.components.shareSocialMedia.copyToClipboardBgColor};
    box-shadow: ${props => props.theme.components.shareSocialMedia.boxShadow};
    -webkit-animation: animateClipboard 2s ease-in-out forwards;
    animation: animateClipboard 2s ease-in-out forwards;
  }
  //Copy to Clipboard Feedback Animation
  @-webkit-keyframes animateClipboard {
    0% {
      top: 0;
      opacity: 0;
    }
    15% {
      top: -30px;
      opacity: 1;
    }
    80% {
      top: -30px;
      opacity: 1;
    }
    100% {
      top: 0;
      opacity: 0;
    }
  }

  @keyframes animateClipboard {
    0% {
      top: 0;
      opacity: 0;
    }
    15% {
      top: -30px;
      opacity: 1;
    }
    80% {
      top: -30px;
      opacity: 1;
    }
    100% {
      top: 0;
      opacity: 0;
    }
  }

  // Extra small devices (portrait phones, more than 575px)
  @media (min-width: 576px) {
    position: absolute;
    width: auto;
    top: ${props => props.top || "-85px"};
    right: ${props => props.right || "-25px"};
    height: ${props => props.height || "90px"};

    .share-social-media {
      width: 181px;
      height: 64px;
      position: relative;

      /*
      -- Copy to Clipboard Feedback
      */
      .copy-clipboard-feedback {
        width: 181px;
      }

      ul {
        z-index: 3;
        position: relative;
        height: 100%;
        li {
          height: 100%;
          width: 25%;
          &:last-child {
            margin-right: 0;
          }
        }
      }

      a,
      button {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: transparent;
        border: none;
        cursor: pointer;
        height: 100%;
        width: 100%;
        text-decoration: none;
        &:hover {
          i {
            color: ${props =>
              lighten(0.5, props.theme.components.shareSocialMedia.iconColor)};
          }
        }
        &:focus {
          ${props => props.theme.mixins.focus};
        }
      }

      //Paper
      &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: inherit;
        left: 0;
        top: 0;
        z-index: 2;
      }

      &:before,
      &:after {
        background-color: ${props =>
          props.backgroundColor ||
          props.theme.components.shareSocialMedia.backgroundColor};
      }

      ,
      &:after {
        box-shadow: ${props =>
          props.theme.components.shareSocialMedia.boxShadow};
      }

      //Triangle
      &:after {
        content: "";
        position: absolute;
        width: 10px;
        height: 10px;
        bottom: -5px;
        right: 31px;
        z-index: 0;
        transform: rotate(45deg);
      }
    }
  }

  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    height: 100px;
    width: 100%;
    padding: 0 15px;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 2147483641;
    box-shadow: ${props => props.theme.components.shareSocialMedia.boxShadow};
    background-color: ${props =>
      props.backgroundColor ||
      props.theme.components.shareSocialMedia.backgroundColor};

    .share-social-media {
      width: 100%;
      height: inherit;
      display: flex;
      align-items: center;

      // -- Copy to Clipboard Feedback
      .copy-clipboard-feedback {
        width: 100%;
      }

      //Paper
      &:before {
        content: "";
        position: absolute;
        width: 100%;
        height: inherit;
        left: 0;
        top: 0;
        z-index: 2;
        background-color: ${props =>
          props.backgroundColor ||
          props.theme.components.shareSocialMedia.backgroundColor};
      }

      ul {
        display: flex;
        justify-content: space-between;
        z-index: 3;
      }

      a,
      button {
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: transparent;
        border: none;
        cursor: pointer;
        height: 50px;
        width: 50px;
        text-decoration: none;
        i {
          font-size: 24px;
        }
        &:hover {
          i {
            color: ${props =>
              lighten(0.5, props.theme.components.shareSocialMedia.iconColor)};
          }
        }
        &:focus {
          ${props => props.theme.mixins.focus};
        }
      }
    }
  }
`;

//export default withTheme(injectIntl(ShareSocialMediaWithStyles));
export default withTheme(ShareSocialMediaWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <ShareSocialMediaWithStyles {...props} />;

WithProps.propTypes = {
  className: PropTypes.string,
  share: PropTypes.object,
  intl: PropTypes.string,
  theme: PropTypes.object,
  top: PropTypes.string,
  right: PropTypes.string,
  height: PropTypes.string,
  backgroundColor: PropTypes.string
};

WithProps.defaultProps = {
  share: {
    facebook: `https://www.facebook.com/sharer/sharer.php?u=url`,
    twitter: `http://twitter.com/share?url=url&text=title`,
    linkedin: `https://www.linkedin.com/shareArticle?mini=true&url=url`,
    url: "/"
  },
  top: "-85px",
  right: "-25px",
  height: "90px"
};
