import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import { Icon, Text } from "../../atoms";

const Error = ({ className, children, text }) => {
  return (
    <div className={className}>
      <Icon name="fas fa-server" fontSize="24px" />
      <Text size="mediumSmall" fontWeight="400">
        {children || text}
      </Text>
    </div>
  );
};

const defaults = {
  text: "Aconteceu um erro ao fazer o pedido"
};

const types = {
  text: PropTypes.string,
  className: PropTypes.string,
  children: PropTypes.node,
  minHeight: PropTypes.string
};

Error.propTypes = types;
Error.defaultProps = defaults;

const ErrorWithStyles = styled(Error)`
  width: 100%;
  min-height: ${props => props.minHeight || "200px"};
  height: inherit;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  p {
    margin-top: 10px;
  }
`;

export default ErrorWithStyles;

//Only used for the Storybook Props Table
export const WithProps = props => <ErrorWithStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
