import React from "react";
import PropTypes from "prop-types";
import { AnimatedBackground, ContentContainer } from "../../atoms";
import { withTheme } from "styled-components";

const Mask = props => {
  return (
    <ContentContainer
      backgroundColor={props.theme.loadings.bgColor}
      position="absolute"
      {...props}
    />
  );
};

const LoadingCard = props => {
  return (
    <ContentContainer height="314px">
      <AnimatedBackground height="314px">
        <Mask {...props} height="25px" right="0" width="70%" />
        <Mask {...props} height="10px" top="25px" width="100%" />
        <Mask {...props} height="5px" right="0" top="35px" width="90%" />
        <Mask {...props} height="40px" top="40px" width="100%" />

        <Mask
          {...props}
          height="12px"
          top="80px"
          width="100%"
          borderTop={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />
        <Mask {...props} height="15px" top="92px" right="0" width="60%" />
        <Mask
          {...props}
          height="12px"
          top="107px"
          width="100%"
          borderBottom={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />

        <Mask {...props} height="12px" top="119px" width="100%" />
        <Mask {...props} height="15px" top="131px" right="0" width="65%" />
        <Mask
          {...props}
          height="12px"
          top="146px"
          width="100%"
          borderBottom={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />

        <Mask {...props} height="12px" top="158px" width="100%" />
        <Mask {...props} height="15px" top="170px" right="0" width="65%" />
        <Mask
          {...props}
          height="12px"
          top="185px"
          width="100%"
          borderBottom={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />

        <Mask {...props} height="12px" top="197px" width="100%" />
        <Mask {...props} height="15px" top="209px" right="0" width="70%" />
        <Mask
          {...props}
          height="12px"
          top="224px"
          width="100%"
          borderBottom={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />

        <Mask {...props} height="12px" top="236px" width="100%" />
        <Mask {...props} height="15px" top="248px" right="0" width="60%" />
        <Mask
          {...props}
          height="12px"
          top="263px"
          width="100%"
          borderBottom={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />

        <Mask {...props} height="12px" top="275px" width="100%" />
        <Mask {...props} height="15px" top="287px" right="0" width="65%" />
        <Mask
          {...props}
          height="12px"
          top="302px"
          width="100%"
          borderBottom={`1px solid ${props.theme.loadings.lightBorderColor}`}
        />
      </AnimatedBackground>
    </ContentContainer>
  );
};

const Loading = ({ className, ...props }) => {
  return (
    <div className={className}>
      <div className="container">
        <div className="row no-gutters">
          <div className="col-xl-7 offset-xl-3 col-lg-8 offset-lg-4 col-12 offset-0">
            <LoadingCard {...props} />
          </div>
        </div>
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  backgroundColor: PropTypes.string
};

export default withTheme(Loading);
