import React, { Fragment } from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
//Components
import {
  Link,
  ListItem,
  Picture,
  Text,
  VerticalList,
  ContentContainer
} from "../../../atoms";
import MarkdownRenderer from "../../MarkdownRenderer";
import PlusButton from "../../PlusButton";
import SectionHeader from "../../SectionHeader";

//import { MarkdownRenderer, PlusButton, SectionHeader } from "../../index";

//Context
import { useConfig } from "../../../../context/ConfigContext";

const CardContainer = styled.div`
  background-color: ${props => props.backgroundColor};
  position: relative;
  overflow: hidden;

  @media (max-width: 575px) {
    .campus-items {
      display: none;
    }
  }
`;

const PositionContainer = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
`;

const TextContainer = styled.div`
  position: relative;
  padding-right: ${props => props.paddingRight};
  padding-left: ${props => props.paddingLeft};
  width: 50%;
  float: ${props => props.float};

  @media (max-width: 575px) {
    padding-right: ${props => props.xsPaddingRight};
    padding-left: ${props => props.xsPaddingLeft};
  }
`;

const OverflowContainer = styled.div`
  max-height: 200px;
  overflow: hidden;
`;

const LayeredCard = ({
  theme,
  textColor,
  backgroundColor,
  alt,
  src,
  showPlusButton,
  inverted,
  title,
  href,
  links,
  externalLink,
  description,
  hasText,
  isMarkdown,
  activeColor
}) => {
  const { formattedMessage: FormattedMessage } = useConfig();
  const {
    textColor: themeTextColor,
    linkColor: themeLinkColor,
    backgroundColor: themeBgColor
  } = theme.components.layeredCard;
  return (
    <Fragment>
      <CardContainer
        backgroundColor={
          theme.highContrast.backgroundColor || backgroundColor || themeBgColor
        }
      >
        <PositionContainer>
          <div className="container">
            <TextContainer
              paddingLeft={inverted ? "70px" : "0"}
              paddingRight={inverted ? "0" : "70px"}
              xsPaddingLeft={inverted ? "30px" : "0"}
              xsPaddingRight={inverted ? "0" : "30px"}
              float={inverted ? "right" : "left"}
            >
              <ContentContainer position="relative" zIndex="2">
                <SectionHeader
                  href={href}
                  tag="h3"
                  fontSize="28px"
                  mobileSmFontSize="26px"
                  mobileXsFontSize="20px"
                  hasContainer={false}
                  hasLine={true}
                  hasLink={true}
                  textColor={
                    theme.highContrast.link || textColor || themeTextColor
                  }
                  marginBottom="15px"
                  externalLink={externalLink}
                >
                  {title}
                </SectionHeader>
                <OverflowContainer>
                  {hasText ? (
                    isMarkdown ? (
                      <MarkdownRenderer
                        content={description}
                        activeColor={activeColor}
                      />
                    ) : (
                      <Text
                        lineHeight="1.6"
                        size="article"
                        fontWeight="300"
                        text={description}
                      />
                    )
                  ) : (
                    <VerticalList className="campus-items">
                      {links.map((item, index) => (
                        <ListItem key={index} marginBottom="5px">
                          <Link
                            size="medium"
                            fontWeight="400"
                            color={
                              theme.highContrast.link ||
                              textColor ||
                              themeLinkColor
                            }
                            href={item.href}
                          >
                            {item.resource ? (
                              <FormattedMessage
                                id={item.resource}
                                defaultMessage={item.text}
                              />
                            ) : (
                              item.text
                            )}
                          </Link>
                        </ListItem>
                      ))}
                    </VerticalList>
                  )}
                </OverflowContainer>
              </ContentContainer>
              {showPlusButton && (
                <PlusButton
                  backgroundColor={
                    theme.highContrast.backgroundColor ||
                    backgroundColor ||
                    themeBgColor
                  }
                  size="60px"
                  position="absolute"
                  height="100%"
                  right={inverted ? "initial" : "-30px"}
                  left={inverted ? "-30px" : "initial"}
                  top="0"
                  zIndex="1"
                  as="a"
                  href={href}
                >
                  <Text
                    fontSize="50px"
                    fontWeight="100"
                    color={
                      theme.highContrast.link || textColor || themeLinkColor
                    }
                    text={theme.icons.plus}
                    lineHeight="1"
                  />
                </PlusButton>
              )}
            </TextContainer>
          </div>
        </PositionContainer>
        <div className="row no-gutters">
          <div className={`col-6 ${inverted ? "" : "offset-6"}`}>
            <Picture src={src} alt={alt} height="350px" />
          </div>
        </div>
      </CardContainer>
    </Fragment>
  );
};

const types = {
  showPlusButton: PropTypes.bool,
  inverted: PropTypes.bool,
  alt: PropTypes.string,
  src: PropTypes.string,
  title: PropTypes.string,
  links: PropTypes.array,
  href: PropTypes.string,
  externalLink: PropTypes.bool,
  description: PropTypes.string,
  hasText: PropTypes.bool,
  isMarkdown: PropTypes.bool,
  activeColor: PropTypes.string
};

const defaults = {
  href: "#",
  showPlusButton: true,
  title: "Título",
  description: "Texto",
  alt: "imagem sem alt",
  src: "https://static.ua.pt/images/ua/placeholder-bw.png",
  inverted: false,
  links: [
    {
      text: "texto",
      href: "#"
    }
  ],
  hasText: false,
  isMarkdown: false,
  activeColor: "#0EB4BD",
  externalLink: false
};

LayeredCard.propTypes = types;
LayeredCard.defaultProps = defaults;

export default withTheme(LayeredCard);

//Only used for the Storybook Props Table
export const WithProps = props => <LayeredCard {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
