import React from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//Components
import { ScreenReaderText, Text, Section } from "../../../atoms";
import { SectionHeader, BorderedCard, Error } from "../../../molecules";
import Loading from "./Loading";
//Context
import { useConfig } from "../../../../context/ConfigContext";

const Card = ({ title, pretitle, url, onKeyPress, history }) => {
  return (
    <BorderedCard
      href={url}
      onKeyPress={e => onKeyPress(e, url)}
      history={history}
    >
      <Text size="small">{pretitle}</Text>
      <Text size="small" fontWeight="400">
        {title}
      </Text>
    </BorderedCard>
  );
};

Card.propTypes = {
  title: PropTypes.string,
  pretitle: PropTypes.string,
  url: PropTypes.string,
  onKeyPress: PropTypes.func,
  history: PropTypes.object
};

const Columns = ({
  className,
  backgroundColor,
  titleProps,
  leftSectionTitle,
  centerSectionTitle,
  rightSectionTitle,
  leftSectionData,
  centerSectionData,
  rightSectionData,
  theme,
  ...props
}) => {
  const { formattedMessage: FormattedMessage } = useConfig();
  return (
    <Section
      className={className}
      backgroundColor={
        theme.components.journalColumns.backgroundColor || backgroundColor
      }
      paddingTop="0"
      paddingBottom="0"
      mdPaddingTop="0"
    >
      <ScreenReaderText>
        <Text as="h2" text="Notícias" />
      </ScreenReaderText>
      <div className="container">
        <div className="row">
          <div className="col-xl col-lg col-md-12">
            <div className="container-left">
              <SectionHeader
                href={props.categoryUrl.left}
                hasContainer={false}
                textColor={theme.components.journalColumns.titleColor}
                {...titleProps}
              >
                {props.leftResource ? (
                  <FormattedMessage
                    id={props.leftResource}
                    defaultMessage={leftSectionTitle}
                  />
                ) : (
                  leftSectionTitle
                )}
              </SectionHeader>
              {leftSectionData.loading ? (
                <Loading />
              ) : leftSectionData.error ? (
                <Error />
              ) : leftSectionData.data.listNews.length <= 0 ? (
                <div>
                  <FormattedMessage
                    id="app.content.empty"
                    defaultMessage="No content to display"
                  />
                </div>
              ) : (
                leftSectionData.data.listNews.map(item => (
                  <Card
                    key={item.id}
                    {...item}
                    onKeyPress={props.onEnter}
                    history={props.history}
                  />
                ))
              )}
            </div>
          </div>
          <div className="col-xl col-lg col-md-12">
            <div className="container-center">
              <SectionHeader
                href={props.categoryUrl.center}
                hasContainer={false}
                textColor={theme.components.journalColumns.titleColor}
                {...titleProps}
              >
                {props.centerResource ? (
                  <FormattedMessage
                    id={props.centerResource}
                    defaultMessage={centerSectionTitle}
                  />
                ) : (
                  centerSectionTitle
                )}
              </SectionHeader>
              {centerSectionData.loading ? (
                <Loading />
              ) : centerSectionData.error ? (
                <Error />
              ) : centerSectionData.data.listNews.length <= 0 ? (
                <div>
                  <FormattedMessage
                    id="app.content.empty"
                    defaultMessage="No content to display"
                  />
                </div>
              ) : (
                centerSectionData.data.listNews.map(item => (
                  <Card
                    key={item.id}
                    {...item}
                    onKeyPress={props.onEnter}
                    history={props.history}
                  />
                ))
              )}
            </div>
          </div>
          <div className="col-xl col-lg col-md-12">
            <div className="container-right">
              <SectionHeader
                href={props.categoryUrl.right}
                hasContainer={false}
                textColor={theme.components.journalColumns.titleColor}
                {...titleProps}
              >
                {props.rightResource ? (
                  <FormattedMessage
                    id={props.rightResource}
                    defaultMessage={rightSectionTitle}
                  />
                ) : (
                  rightSectionTitle
                )}
              </SectionHeader>
              {rightSectionData.loading ? (
                <Loading />
              ) : rightSectionData.error ? (
                <Error />
              ) : rightSectionData.data.listNews.length <= 0 ? (
                <div>
                  <FormattedMessage
                    id="app.content.empty"
                    defaultMessage="No content to display"
                  />
                </div>
              ) : (
                rightSectionData.data.listNews.map(item => (
                  <Card
                    key={item.id}
                    {...item}
                    onKeyPress={props.onEnter}
                    history={props.history}
                  />
                ))
              )}
            </div>
          </div>
        </div>
      </div>
    </Section>
  );
};

const types = {
  title: PropTypes.string,
  className: PropTypes.string,
  backgroundColor: PropTypes.string,
  titleProps: PropTypes.object,
  leftSectionTitle: PropTypes.string,
  centerSectionTitle: PropTypes.string,
  rightSectionTitle: PropTypes.string,
  leftSectionData: PropTypes.object.isRequired,
  centerSectionData: PropTypes.object.isRequired,
  rightSectionData: PropTypes.object.isRequired,
  categoryUrl: PropTypes.object,
  onEnter: PropTypes.func,
  history: PropTypes.object,
  leftResource: PropTypes.string,
  centerResource: PropTypes.string,
  rightResource: PropTypes.string,
  theme: PropTypes.object
};

Columns.propTypes = types;

const ColumnsWithStyles = styled(Columns)`
  .container-left,
  .container-center,
  .container-right {
    padding: 70px 0;
  }
  // Medium devices (tablets, less than 992px)
  @media (max-width: 991px) {
    .container-center,
    .container-right {
      padding-top: 0;
    }
  }
`;

export default withTheme(ColumnsWithStyles);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => <ColumnsWithStyles {...props} />;

WithProps.propTypes = types;
