import React from "react";
import PropTypes from "prop-types";
import { AgendaCardLoading, AgendaFeaturedLoading } from "../../molecules";
import { withTheme } from "styled-components";

const Loading = ({ className, ...props }) => {
  return (
    <div className={className}>
      <div className="row">
        <div className="col-xl-6 col-lg-6 col-md-12 col-12 order-lg-first order-last">
          <AgendaCardLoading {...props} />
          <AgendaCardLoading {...props} />
          <AgendaCardLoading {...props} />
          <AgendaCardLoading
            {...props}
            borderBottom={`1px solid ${props.theme.loadings.cardBorderColor}`}
          />
        </div>
        <div className="col-xl-6 col-lg-6 col-md-12 col-12 order-lg-last order-first">
          <AgendaFeaturedLoading {...props} mdMarginBottom="50px" />
        </div>
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  theme: PropTypes.object
};

export default withTheme(Loading);
