import React from "react";
import styled, { withTheme } from "styled-components";
import PropTypes from "prop-types";
import Icon from "../Icon";

const ScrollMore = React.forwardRef(({ className, onClick, theme }, ref) => (
  <div ref={ref} className={className}>
    <button onClick={onClick} aria-label="Sroll para baixo">
      <Icon
        name={theme.icons.chevron_down}
        size="medium"
        color={theme.components.scrollMore.iconColor}
      />
    </button>
  </div>
));

ScrollMore.displayName = "ScrollMore";

const ScrollMoreWithStyles = styled(ScrollMore)`
  height: ${props => props.height};
  width: 100%;
  position: ${props => props.position};
  bottom: ${props => props.bottom};
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  button {
    background-color: transparent;
    height: 100%;
    width: 100%;
    border: none;
    cursor: pointer;
    i {
      -webkit-animation: bouncey 1.6s linear infinite;
      animation: bouncey 1.6s linear infinite;
    }
  }

  @keyframes bouncey {
    0% {
      transform: translateY(0);
      -webkit-transform: translateY(0);
      -moz-transform: translateY(0);
    }
    50% {
      transform: translateY(4px);
      -webkit-transform: translateY(4px);
      -moz-transform: translateY(4px);
    }
    100% {
      transform: translateY(0);
      -webkit-transform: translateY(0);
      -moz-transform: translateY(0);
    }
  }
`;

const types = {
  className: PropTypes.string,
  theme: PropTypes.object,
  onClick: PropTypes.func,
  height: PropTypes.string,
  position: PropTypes.string,
  bottom: PropTypes.string
};

const defaults = {
  height: "50px",
  position: "relative",
  bottom: "initial"
};

ScrollMoreWithStyles.propTypes = types;
ScrollMoreWithStyles.defaultProps = defaults;

export default withTheme(ScrollMoreWithStyles);

//Only used for the Storybook Props Table
export const WithProps = ({ ...props }) => <ScrollMoreWithStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
