import React from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
import { lighten } from "polished";
//Components
import { Text, Picture, LineTruncate, Triangle, Link } from "../../../atoms";
import SocialMediaActions from "../../SocialMediaActions";
import { compose } from "../../../../utils/utilities";

const HorizontalNewsCard = ({
  className,
  category,
  title,
  date,
  src,
  alt,
  removeImage,
  triangleBgColor,
  changeMdOrder,
  href,
  theme,
  size,
  categoryUrl,
  share,
  history
}) => {
  return (
    <Link className={className} tabIndex={0} href={href} hasHover={false}>
      <div className="horizontal-news-card">
        <div className="row no-gutters">
          <div className={`col order-lg-0 ${changeMdOrder ? "order-1" : ""}`}>
            <div className="content-container d-flex flex-column justify-content-between">
              <Triangle
                className="triangle"
                transform={removeImage ? "rotate(-90deg)" : "rotate(90deg)"}
                withContent={true}
                contentColor={
                  theme.components.horizontalNewsCard.triangleContentColor
                }
                color={
                  triangleBgColor ||
                  theme.components.horizontalNewsCard.backgroundColor
                }
                size="22px"
                top="calc(50% - 11px)"
                right="-33px"
                contentSize="24px"
                zIndex="1"
              />
              <div>
                <Link
                  as="button"
                  text={category}
                  fontWeight="300"
                  onClick={e => {
                    e.preventDefault();
                    history.push(categoryUrl);
                  }}
                  color={theme.components.horizontalNewsCard.categoryColor}
                  lineHeight="1.4"
                  //Accessibility problems with id="tag" remove later on if all ok
                  //id="tag"
                  mobileXsFontSize="12px"
                />
                <div className="title-container">
                  <Text
                    isSerif
                    as="h3"
                    display="block"
                    size="large"
                    sizeXsSize="small"
                    fontDecoration="none"
                    fontWeight="400"
                    lineHeight="1.5"
                    text={title}
                    color={theme.components.horizontalNewsCard.textColor}
                  />
                </div>
              </div>
              <div className="row">
                <div className="col">
                  <LineTruncate>
                    <Text
                      text={date}
                      size="xSmall"
                      color={theme.components.horizontalNewsCard.textColor}
                      textTransform="lowercase"
                    />
                  </LineTruncate>
                </div>
                <div className="col-auto">
                  <SocialMediaActions
                    size={size}
                    iconColor={theme.components.horizontalNewsCard.textColor}
                    share={share}
                  />
                </div>
              </div>
            </div>
          </div>
          {removeImage ? (
            ""
          ) : (
            <div className={`col order-lg-1 ${changeMdOrder ? "order-0" : ""}`}>
              <div className="image-container">
                <Picture
                  src={src}
                  alt={alt}
                  height={theme.components.horizontalNewsCard.height}
                  xsHeight={theme.components.horizontalNewsCard.mobileXsHeight}
                />
              </div>
            </div>
          )}
        </div>
      </div>
    </Link>
  );
};

HorizontalNewsCard.propTypes = {
  className: PropTypes.string,
  category: PropTypes.string,
  title: PropTypes.string,
  date: PropTypes.string,
  src: PropTypes.string,
  triangleBgColor: PropTypes.string,
  removeImage: PropTypes.bool,
  changeMdOrder: PropTypes.bool,
  href: PropTypes.string,
  theme: PropTypes.object,
  size: PropTypes.object,
  categoryUrl: PropTypes.string,
  share: PropTypes.object,
  history: PropTypes.object,
  alt: PropTypes.string
};

HorizontalNewsCard.defaultProps = {
  category: "Categoria",
  title: "Título",
  date: "data",
  src: "",
  changeMdOrder: false,
  href: "/",
  share: {
    facebook: `https://www.facebook.com/sharer/sharer.php?u=url`,
    twitter: `http://twitter.com/share?url=url&text=title`,
    linkedin: `https://www.linkedin.com/shareArticle?mini=true&url=url`,
    url: "/"
  }
};

const HorizontalNewsCardWithStyles = styled(HorizontalNewsCard)`
  display: block;
  text-decoration: none;
  &:focus {
    ${props => props.theme.mixins.focus};
  }
  button {
    background-color: transparent;
    border: none;
    cursor: pointer;
    &:hover {
      i {
        color: ${lighten(0.5, "#000")};
      }
    }
  }

  .horizontal-news-card {
    //Image Animation
    img {
      -webkit-transition: all 0.4s;
      -moz-transition: all 0.4s;
      -o-transition: all 0.4s;
      transition: all 0.4s;
    }

    cursor: pointer;
    &:hover {
      img {
        -ms-transform: scale(1.1);
        -moz-transform: scale(1.1);
        -webkit-transform: scale(1.1);
        -o-transform: scale(1.1);
        transform: scale(1.1);
      }
    }

    background-color: ${props =>
      props.backgroundColor ||
      props.theme.components.horizontalNewsCard.backgroundColor};
    .image-container {
      height: ${props => props.theme.components.horizontalNewsCard.height};
      overflow: hidden;
    }
    .content-container {
      position: relative;
      height: 100%;
      padding: 20px;
      background-color: ${props =>
        props.contentContainerBgColor ||
        props.theme.components.horizontalNewsCard.contentContainerBgColor};
      .title-container {
        max-height: 210px;
        overflow: hidden;
        margin-top: 10px;
      }
    }
  }

  // Medium devices (tablets, less than 992px)
  @media (max-width: 992px) {
    .triangle {
      ${props =>
        props.changeMdOrder &&
        `
        right: -33px;
        left: unset;
        transform: rotate(90deg);
      `}
    }
  }

  // Extra small devices (portrait phones, less than 576px)
  @media (max-width: 575px) {
    .horizontal-news-card {
      height: ${props =>
        props.theme.components.horizontalNewsCard.mobileXsHeight};
      .image-container {
        height: ${props =>
          props.theme.components.horizontalNewsCard.mobileXsHeight};
      }
      .content-container {
        .title-container {
          max-height: 135px;
        }
      }
    }
  }
`;

export default compose(withTheme)(HorizontalNewsCardWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <HorizontalNewsCardWithStyles {...props} />;

WithProps.propTypes = {
  className: PropTypes.string,
  category: PropTypes.string,
  title: PropTypes.string,
  date: PropTypes.string,
  src: PropTypes.string,
  triangleBgColor: PropTypes.string,
  removeImage: PropTypes.bool,
  changeMdOrder: PropTypes.bool,
  href: PropTypes.string,
  theme: PropTypes.object,
  size: PropTypes.object,
  categoryUrl: PropTypes.string,
  share: PropTypes.object,
  history: PropTypes.object,
  alt: PropTypes.string,
  backgroundColor: PropTypes.string,
  contentContainerBgColor: PropTypes.string
};

WithProps.defaultProps = {
  category: "Categoria",
  title: "Título",
  date: "data",
  src: "",
  changeMdOrder: false,
  href: "/",
  share: {
    facebook: `https://www.facebook.com/sharer/sharer.php?u=url`,
    twitter: `http://twitter.com/share?url=url&text=title`,
    linkedin: `https://www.linkedin.com/shareArticle?mini=true&url=url`,
    url: "/"
  }
};
