import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
import { withExtendedClass } from "../../../utils/utilities.style";
import React from "react";

const Dropdown = styled.div.attrs(({ className }) =>
  withExtendedClass({
    className,
    extend: "dropdown"
  })
)`
  width: ${props => props.width};
  display: ${props => props.display};
  position: relative;
  padding-bottom: ${props => props.paddingBottom};
  .dropdown-content,
  .dropdown-menu {
    //HighContrast border only
    border: ${props => props.theme.highContrast.border};

    padding: ${props => props.padding || `${props.theme.spacing.m} 0`};
    display: ${props => (props.openMenuOnHover ? "none" : "block")};
    position: absolute;
    background-color: ${props =>
      props.backgroundColor || props.theme.components.dropDown.backgroundColor};
    box-shadow: ${props => props.boxShadow || props.theme.shadows.base};
    -moz-box-shadow: ${props => props.boxShadow};
    -webkit-box-shadow: ${props => props.boxShadow};
    -o-box-shadow: ${props => props.boxShadow};
    min-width: ${props =>
      props.minContentWidth || props.theme.components.dropDown.minWidth};
    max-width: ${props => props.maxContentWidth};
    z-index: ${props => props.zIndex || "99"};
    right: ${props => props.right};
    left: ${props => props.left};
    top: ${props => props.top};
    margin-top: ${props => props.marginTop};
  }

  ${props =>
    props.openMenuOnHover &&
    `
  &:hover {
    .dropdown-content,
    .dropdown-menu {
      display: block;
    }
  }
  `}

  &:focus-within {
    .dropdown-content {
      display: block;
    }
  }

  //Underline animation on hover menu links
  ${props =>
    props.withUnderlineAnimation &&
    `
    &:hover {
      &:after {
        background-color: ${props.underlineAnimationColor ||
          props.theme.components.dropDown.underlineAnimationColor};
        width: 30px;
      }
    }
    &:focus-within {
      &:after {
        background-color: ${props.underlineAnimationColor ||
          props.theme.components.dropDown.underlineAnimationColor};
        width: 30px;
      }
    }
    &:after {
      content: "";
      display: block;
      position: absolute;
      width: 0;
      height: 3px;
      background-color: transparent;
      margin-top: 5px;
      transition: width 0.4s ease;
      -webkit-transition: width 0.4s ease;
    }
  `};
`;

const defaults = {
  width: "100%",
  display: "inline",
  openMenuOnHover: true
};

const types = {
  className: PropTypes.string,
  width: PropTypes.string,
  display: PropTypes.string,
  paddingBottom: PropTypes.string,
  padding: PropTypes.string,
  openMenuOnHover: PropTypes.bool,
  backgroundColor: PropTypes.string,
  boxShadow: PropTypes.string,
  minContentWidth: PropTypes.string,
  top: PropTypes.string,
  right: PropTypes.string,
  left: PropTypes.string,
  marginTop: PropTypes.string,
  withUnderlineAnimation: PropTypes.bool,
  underlineAnimationColor: PropTypes.string,
  zIndex: PropTypes.string,
  maxContentWidth: PropTypes.string
};

Dropdown.propTypes = types;
Dropdown.defaultProps = defaults;

export default withTheme(Dropdown);

//Only used for the Storybook Props Table
export const WithProps = props => <Dropdown {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
