import React from "react";
import PropTypes from "prop-types";
import styled, { withTheme } from "styled-components";
import {
  AspectRatio,
  LineTruncate,
  Link,
  Picture,
  Text,
  Triangle,
  ContentContainer
} from "../../../atoms";
import SocialMediaActions from "../../SocialMediaActions";
import { usePalette } from "react-palette";

const ColorExtractCard = ({
  className,
  alt,
  src,
  share,
  size,
  category,
  title,
  date,
  isOpen,
  theme
}) => {
  //data: {darkMuted, darkVibrant, lightMuted, lightVibrant, muted, vibrant}
  const { data, loading, error } = usePalette(src);

  //Convert hex Color to rgba
  const hex2rgba = (hex, alpha = 1) => {
    const [r, g, b] = hex.match(/\w\w/g).map(x => parseInt(x, 16));
    return `rgba(${r},${g},${b},${alpha})`;
  };

  const dominantColor = hex2rgba(`${data.lightMuted}`, 0.6);
  const defaultColor = hex2rgba(
    `${theme.components.colorExtractCard.backgroundColor}`,
    0.5
  );

  return (
    <div className={className}>
      <ContentContainer
        className="content-container"
        backgroundColor={
          !loading && !error
            ? theme.highContrast.lightOpacityBgColor || dominantColor
            : defaultColor
        }
      >
        <Triangle
          className="triangle"
          transform="rotate(90deg)"
          withContent={true}
          color={
            !loading && !error
              ? theme.highContrast.lightOpacityBgColor || dominantColor
              : defaultColor
          }
          size="22px"
          top="calc(50% - 11px)"
          right="-33px"
          contentSize="24px"
          zIndex="1"
        />
        <div className="d-flex flex-column justify-content-between h-100">
          <div>
            <Link
              text={category}
              fontWeight="300"
              lineHeight="1.4"
              mobileXsFontSize="12px"
              color={theme.components.colorExtractCard.linkColor}
            />
            <Text
              isSerif
              as="h3"
              size="large"
              sizeXsSize="small"
              fontWeight="400"
              lineHeight="1.5"
              text={title}
              marginTop="10px"
            />
          </div>
          <div className="row">
            <div className="col">
              <LineTruncate>
                <Text text={date} size="xSmall" textTransform="lowercase" />
              </LineTruncate>
            </div>
            <div className="col-auto">
              <SocialMediaActions
                size={size}
                iconColor={theme.components.colorExtractCard.iconColor}
                share={share}
                isOpen={isOpen}
              />
            </div>
          </div>
        </div>
      </ContentContainer>
      <AspectRatio
        ratio="sixteenByNine"
        backgroundColor={theme.components.colorExtractCard.backgroundColor}
      >
        <Picture src={src} alt={alt} height="100%" />
      </AspectRatio>
    </div>
  );
};

ColorExtractCard.propTypes = {
  size: PropTypes.object,
  share: PropTypes.object,
  category: PropTypes.string,
  title: PropTypes.string,
  date: PropTypes.string,
  src: PropTypes.string,
  className: PropTypes.string,
  alt: PropTypes.string,
  theme: PropTypes.object,
  isOpen: PropTypes.bool
};

ColorExtractCard.defaultProps = {
  href: "/",
  category: "Categoria",
  title: "Título",
  date: "Data",
  share: {
    facebook: `https://www.facebook.com/sharer/sharer.php?u=url`,
    twitter: `http://twitter.com/share?url=url&text=title`,
    linkedin: `https://www.linkedin.com/shareArticle?mini=true&url=url`,
    url: "/"
  },
  alt: "Imagem sem alt",
  src: "https://static.ua.pt/images/ua/placeholder-bw.png"
};

const ColorExtractCardWithStyles = styled(ColorExtractCard)`
  position: relative;
  background-color: ${props =>
    props.theme.components.colorExtractCard.backgroundColor};
  .content-container {
    padding: 20px;
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 50%;
    z-index: 1;
    .h-100 {
      height: 100%;
    }
  }

  // Medium devices (tablets, less than 992px)
  @media (max-width: 992px) {
    .content-container {
      ${props =>
        props.changeMdOrder &&
        `
        right: 0;
        left: unset;
      `}
      .triangle {
        ${props =>
          props.changeMdOrder &&
          `
        left: -33px;
        right: unset;
        transform: rotate(-90deg);
      `}
      }
    }
  }
`;

ColorExtractCardWithStyles.propTypes = {
  changeMdOrder: PropTypes.bool
};

ColorExtractCardWithStyles.defaultProps = {
  changeMdOrder: false
};

export default withTheme(ColorExtractCardWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <ColorExtractCardWithStyles {...props} />;

WithProps.propTypes = {
  size: PropTypes.object,
  share: PropTypes.object,
  category: PropTypes.string,
  title: PropTypes.string,
  date: PropTypes.string,
  src: PropTypes.string,
  className: PropTypes.string,
  alt: PropTypes.string,
  theme: PropTypes.object,
  isOpen: PropTypes.bool,
  changeMdOrder: PropTypes.bool
};

WithProps.defaultProps = {
  href: "/",
  category: "Categoria",
  title: "Título",
  date: "Data",
  share: {
    facebook: `https://www.facebook.com/sharer/sharer.php?u=url`,
    twitter: `http://twitter.com/share?url=url&text=title`,
    linkedin: `https://www.linkedin.com/shareArticle?mini=true&url=url`,
    url: "/"
  },
  alt: "Imagem sem alt",
  src:
    "https://images.pexels.com/photos/3097297/pexels-photo-3097297.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260",
  changeMdOrder: false
};
