import React, { Fragment } from "react";
import PropTypes from "prop-types";
//Styles
import styled, { withTheme } from "styled-components";
//components
import {
  Text,
  Icon,
  Section,
  HorizontalList,
  ListItem,
  ContentContainer
} from "./../../../atoms";
import { MarkdownRenderer } from "../../../molecules";
import { lighten } from "polished";
import Loading from "./Loading";

const SocialMediaButton = ({ name, theme, href }) => {
  return (
    <a
      className="social-media-link"
      rel="noopener noreferrer"
      href={href}
      target="_blank"
    >
      <Icon
        name={name}
        color={theme.components.subWebContacts.card.iconColor}
        size="xSmall"
      />
    </a>
  );
};

SocialMediaButton.propTypes = {
  name: PropTypes.string,
  onClick: PropTypes.func,
  theme: PropTypes.object,
  href: PropTypes.string
};

const Card = ({ theme, title, data, main, social, activeColor }) => {
  return (
    <div className="card d-flex flex-column">
      <Text as="h3" size="xLarge" marginBottom="25px" fontWeight="500">
        {title}
      </Text>
      <div className="d-flex flex-column flex-grow-1">
        <MarkdownRenderer content={main} />

        <ContentContainer marginTop="25px">
          <MarkdownRenderer activeColor={activeColor} content={data} />
        </ContentContainer>
      </div>
      <ContentContainer marginTop="25px">
        <HorizontalList display="inline-block">
          {social.map((item, i) => {
            return (
              <ListItem key={i}>
                <SocialMediaButton
                  name={theme.icons[item.type]}
                  href={item.url}
                  theme={theme}
                />
              </ListItem>
            );
          })}
        </HorizontalList>
      </ContentContainer>
    </div>
  );
};

Card.propTypes = {
  theme: PropTypes.object,
  title: PropTypes.string,
  data: PropTypes.array,
  main: PropTypes.array,
  social: PropTypes.array
};

const Contacts = ({
  className,
  isLoading,
  theme,
  title,
  data,
  main,
  mapUrl,
  social,
  activeColor
}) => {
  return (
    <Section className={className} paddingTop="0" hasBottomSpacing={false}>
      {/*
      <div className="iframe-container">
        <iframe
          title="google-maps"
          src="https://www.google.com/maps/d/embed?mid=1T5C-L79Kh_IKDSWUDELzyPqrqyIIKep5&z=16&ll=40.632543, -8.658140"
        />
      </div>
      */}

      {isLoading ? (
        <Loading theme={theme} />
      ) : (
        <Fragment>
          <div className="iframe-container">
            <iframe
              title="google_maps"
              id="gmap_canvas"
              src={mapUrl}
              frameBorder="0"
              scrolling="no"
              marginHeight="0"
              marginWidth="0"
            >
              &nbsp;
            </iframe>
          </div>
          <div className="contacts">
            <div className="container">
              <div className="row no-gutters justify-content-end">
                <div className="col-lg-6 col-md-8 col-12">
                  <div className="content-container">
                    <Card
                      title={title}
                      data={data}
                      theme={theme}
                      main={main}
                      social={social}
                      activeColor={activeColor}
                    />
                  </div>
                </div>
              </div>
            </div>
          </div>
        </Fragment>
      )}
    </Section>
  );
};

const types = {
  className: PropTypes.string,
  isLoading: PropTypes.bool,
  theme: PropTypes.object,
  title: PropTypes.string,
  data: PropTypes.array,
  main: PropTypes.array,
  mapUrl: PropTypes.string,
  activeColor: PropTypes.string,
  social: PropTypes.array
};

const defaults = {
  activeColor: "#0EB4BD",
  isLoading: false,
  title: "Contactos do departamento",
  mapUrl:
    "https://maps.google.com/maps?q=university%20of%20aveiro&t=&z=13&ie=UTF8&iwloc=&output=embed"
};

Contacts.propTypes = types;
Contacts.defaultProps = defaults;

const ContactsWithStyles = styled(Contacts)`
  position: relative;

  .iframe-container {
    height: ${props => props.theme.components.subWebContacts.height};
    iframe {
      height: 100%;
      width: 100%;
      border: none;
    }
  }

  /*
  &:after {
    content: "";
    position: absolute;
    right: 40px;
    top: 40px;
    bottom: 40px;
    left: 50%;
    background-color: #fff;
    z-index: 0;
  }
  */

  .contacts {
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    //z-index: 1;

    .content-container {
      height: ${props => props.theme.components.subWebContacts.height};
      padding: 50px 0;
      .card {
        pointer-events: all;
        height: 100%;
        padding: 50px 40px;
        background-color: ${props =>
          props.theme.components.subWebContacts.card.bgColor};

        .social-media-link {
          width: 30px;
          height: 30px;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;
          text-decoration: none;
          &:hover {
            i {
              color: ${props =>
                lighten(
                  0.5,
                  props.theme.components.subWebContacts.card.iconColor
                )};
            }
          }
        }
      }
    }
  }

  // Small devices (landscape phones, less than 768px)
  @media (max-width: 767px) {
    .contacts {
      position: relative;
      .container {
        max-width: initial;
      }
      .content-container {
        height: auto;
        .card {
          padding: 0;
        }
      }
    }
  }
`;

export default withTheme(ContactsWithStyles);

//Only used for the Storybook Props Table
export const WithProps = props => <ContactsWithStyles {...props} />;

WithProps.propTypes = types;
WithProps.defaultProps = defaults;
