import React from "react";
import PropTypes from "prop-types";
import { ContentContainer } from "../../../atoms";
import { ContentLoading } from "../../../molecules";
import styled, { withTheme } from "styled-components";

const Loading = ({ className, theme }) => {
  return (
    <div className={`${className} d-flex`}>
      <ContentContainer
        className="d-flex align-items-center justify-content-center flex-grow-1"
        backgroundColor={theme.loadings.iconContainerBgColor}
      />
      <div className="contacts">
        <div className="container">
          <div className="row no-gutters justify-content-end">
            <div className="col-lg-6 col-md-8 col-12">
              <div className="content-container">
                <div className="card">
                  <ContentLoading />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

Loading.propTypes = {
  className: PropTypes.string,
  theme: PropTypes.object
};

const LoadingWithStyles = styled(Loading)`
  height: ${props => props.theme.components.subWebContacts.height};
  position: relative;

  .contacts {
    pointer-events: none;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;

    .content-container {
      height: ${props => props.theme.components.subWebContacts.height};
      padding: 50px 0;
      .card {
        height: 100%;
        padding: 50px 40px;
        background-color: ${props =>
          props.theme.components.subWebContacts.card.bgColor};
      }
    }
  }

  // Small devices (landscape phones, less than 768px)
  @media (max-width: 767px) {
    .contacts {
      display: none;
    }
  }
`;

export default withTheme(LoadingWithStyles);
