import React from "react";
import PropTypes from "prop-types";
import { withTheme } from "styled-components";
//Components
import { Text, Section } from "../../atoms";
//Context
import { useConfig } from "../../../context/ConfigContext";

const LastUpdate = ({ date, theme }) => {
  const { formattedMessage: FormattedMessage } = useConfig();
  return (
    <Section
      backgroundColor={theme.components.lastUpdate.bgColor}
      paddingTop="0"
      paddingBottom="0"
      height="45px"
      className="d-flex align-items-center"
    >
      <div className="container">
        <div className="offset-xl-3">
          <Text color={theme.components.lastUpdate.textColor} size="xSmall">
            <FormattedMessage
              id="app.lastUpdate.text"
              defaultMessage="última atualização a"
            />{" "}
            {date}
          </Text>
        </div>
      </div>
    </Section>
  );
};

LastUpdate.propTypes = {
  date: PropTypes.string,
  theme: PropTypes.object
};

LastUpdate.defaultProps = {
  date: "dd/mm/yyyy"
};

export default withTheme(LastUpdate);
